import React, { useEffect, useRef, useMemo, useState, useCallback } from 'react';
import { ToolbarWrapper, LeftWrapper, CenterWrapper, RightWrapper } from './styled';
import { MoreOverlay, ToolbarLeftWrapper } from '../toolbar/styled';

import { SpaceBetweenLayoutProps, BlockType } from '../interface';
import ResizeObserver from 'resize-observer-polyfill';
import { debounce, uuid } from '../../../utils';
import { GroupWrapperContext, ToolbarContext, OverLayContext } from '../contexts';
import { SpaceBetweenLayoutOffScreenWrapper } from './OffScreenWrapper';
import { Dropdown, MoreBetaNormal } from '@ali/we-design';
import { IconButton } from '../../../common';
import { useGetContextLocale, useTranslate } from '@ali/we-util';
import { ToolbarGroupWrapper, ToolbarRowWrapper } from '../toolbar';


export const SpaceBetweenLayout: React.FC<SpaceBetweenLayoutProps> = React.memo((props) => {
  const elRef = useRef<HTMLElement>();
  const leftElRef = useRef<Element>();
  const centerElRef = useRef<Element>();
  const [needGetPositions, setNeedGetPositions] = useState<number>(0);
  const id = useMemo(() => uuid(), []);
  const [blocks, setBlocks] = useState<BlockType[]>([]);
  const [hideBlocks, setHideBlocks] = useState<string[]>([]);
  const [overlayHideBlocks, setOverlayShowBlocks] = useState<string[]>([]);
  const [moreOverlayCss, setMoreOverlayCss] = useState<string>();
  const [moreVisible, setMoreVisible] = useState<boolean>(false);
  const {
    leftModule,
    centerModule,
    locale,
    leftOutModule,
    rightOutModule,
    leftOutModuleWidth,
    rightOutModuleWidth,
  } = props;

  const contextLocale = useGetContextLocale();
  const t = useTranslate(locale || contextLocale);
  const offset = 100;
  useEffect(() => {
    setNeedGetPositions(new Date().getTime());
  }, []);

  const layoutResize = useCallback(
    (width: number) => {
      if (elRef.current) {
        const hideEl: BlockType[] = [];
        const showEl: BlockType[] = [];
        blocks.forEach((o) => {
          o.offset + offset < width ? showEl.push(o) : hideEl.push(o);
        });
        setHideBlocks(
          hideEl
            .map((o) => o.id)
            .concat(
              Array.from(new Set(hideEl.map((o) => o.groupId || ''))).filter(
                (key) => key && showEl.findIndex((o) => o.groupId === key) === -1,
              ),
            ),
        );

        setOverlayShowBlocks(
          showEl
            .map((o) => o.id)
            .concat(
              Array.from(new Set(showEl.map((o) => o.groupId || ''))).filter(
                (key) => key && hideEl.findIndex((o) => o.groupId === key) === -1,
              ),
            ),
        );
      }
    },
    [elRef, blocks],
  );

  useEffect(() => {
    const handleResize = debounce((width) => {
      layoutResize(width - leftOutModuleWidth - rightOutModuleWidth);
    }, 50);

    const resizeObserver = new ResizeObserver((entries: ResizeObserverEntry[]) => {
      const entrie = entries.find(
        (o) => o.target.getAttribute('data-id') === `ToolbarWrapper_${id}`,
      );
      if (entrie) {
        handleResize(entrie.contentRect.width);
      }
    });

    if (resizeObserver && elRef.current && leftElRef.current && centerElRef.current) {
      resizeObserver.observe(elRef.current);
      resizeObserver.observe(leftElRef.current);
      resizeObserver.observe(centerElRef.current);
    }
    return () => {
      if (resizeObserver) {
        resizeObserver.disconnect();
        if (typeof handleResize.cancel === 'function') {
          handleResize.cancel();
        }
      }
    };
  }, [blocks]);

  const renderMoreBtn = useMemo(() => {
    if (hideBlocks.length === 0) return null;
    /**
     * 展开收起动画
     */
    const moreTransition = () => {
      setMoreOverlayCss(moreVisible ? 'scale-out' : 'scale-up');
      !moreVisible && setTimeout(() => setMoreOverlayCss(''), 100);
      setTimeout(() => setMoreVisible(!moreVisible), !moreVisible ? 0 : 500);
    };

    return (
      <Dropdown
        visible={moreVisible}
        overlayStyle={{
          backgroundColor: 'transparent',
          boxShadow: 'none',
          border: 'none',
        }}
        overlay={
          <MoreOverlay className={moreOverlayCss}>
            <ToolbarLeftWrapper className="show-left-wrapper">
              <OverLayContext.Provider
                value={{
                  inOverlay: true,
                  overlayHideBlocks,
                  ignoreRegister: true,
                }}
              >
                <ToolbarGroupWrapper groupKey="left" fineAdaptation>
                  <ToolbarRowWrapper>{leftModule}</ToolbarRowWrapper>
                </ToolbarGroupWrapper>
                <ToolbarGroupWrapper groupKey="center" fineAdaptation>
                  <ToolbarRowWrapper>{centerModule}</ToolbarRowWrapper>
                </ToolbarGroupWrapper>
              </OverLayContext.Provider>
            </ToolbarLeftWrapper>
          </MoreOverlay>
        }
        onVisibleChange={moreTransition}
        placement="bottomRight"
      >
        <div>
          <IconButton
            icon={<MoreBetaNormal />}
            title={t('we_toolbar_more')}
            testid="toolbar_more"
            className="more-btn"
          />
        </div>
      </Dropdown>
    );
  }, [hideBlocks, overlayHideBlocks, moreVisible, moreOverlayCss]);

  return (
    <>
      <SpaceBetweenLayoutOffScreenWrapper
        onBlockChange={setBlocks}
        needGetPositions={needGetPositions}
        centerModule={centerModule}
        leftModule={leftModule}
      />

      <ToolbarContext.Provider value={{ hideBlocks }}>
        <OverLayContext.Provider
          value={{
            inOverlay: false,
            overlayHideBlocks: [],
            ignoreRegister: true,
          }}
        >
          <ToolbarWrapper ref={elRef} data-id={`ToolbarWrapper_${id}`}>
            <GroupWrapperContext.Provider value={{ fineAdaptation: true, groupKey: 'left' }}>
              <LeftWrapper ref={leftElRef} data-id={`LeftWrapper_${id}`}>
                {leftOutModule}
                {leftModule}
              </LeftWrapper>
            </GroupWrapperContext.Provider>

            <GroupWrapperContext.Provider value={{ fineAdaptation: true, groupKey: 'center' }}>
              <CenterWrapper ref={centerElRef} data-id={`CenterWrapper_${id}`}>
                {centerModule}
                {renderMoreBtn}
              </CenterWrapper>
            </GroupWrapperContext.Provider>
            <RightWrapper>{rightOutModule}</RightWrapper>
          </ToolbarWrapper>
        </OverLayContext.Provider>
      </ToolbarContext.Provider>
    </>
  );
});
