import styled, { css } from 'styled-components';
import { withTheme } from '@ali/we-design';

export const ToolbarWrapper = withTheme(styled.div`
  display: flex;
  justify-content: center;
  background: #f7f8fa;
  height: 44px;
  align-items: center;
  position: relative;
`);

export const LeftWrapper = withTheme(styled.div`
  display: flex;
  position: absolute;
  left: 0;
  ${() => {
    const cs = ['.icon-button-layout'];
    return css`
      ${`${cs.map((o) => cs.map((oo) => `${o} ~ ${oo}`)).join(',')
  }{ margin-left: 4px;}`}
    `;
  }}
`);

export const RightWrapper = withTheme(styled.div`
  display: flex;
  position: absolute;
  right: 0;
`);

export const CenterWrapper = withTheme(styled.div`
  display: flex;
  padding: 0 24px;
  ${() => {
    const cs = ['.icon-button-layout', '.icon-menu-layout'];
    return css`
      ${`${cs.map((o) => cs.map((oo) => `${o} ~ ${oo}`)).join(',')
  }{ margin-left: 4px;}`}
    `;
  }};
`);

export const OffScreenWrapper = withTheme(styled.div`
  position: absolute;
  left: -10000px;
  display: flex;
`);
