import * as React from 'react';
import { ToolbarFrozenNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';
import { useTranslate } from '@ali/we-util';

export type FrozenButtonProps = IconMenuPropsOmit & {
  icon?: React.ReactNode;
};

export const FrozenButton: React.FC<FrozenButtonProps> = React.memo((props) => {
  const [visible, setVisible] = React.useState(false);

  const handleVisibleChange = (value: boolean) => {
    setVisible(value);
  };
  const t = useTranslate(props.locale);
  return (
    <IconMenu
      visible={visible}
      icon={props.icon || <ToolbarFrozenNormal />}
      title={t('we_toolbar_frozen')}
      tooltip={t('we_toolbar_frozen')}
      onVisibleChange={handleVisibleChange}
      testid="FrozenButton"
      {...props}
    />
  );
});

FrozenButton.defaultProps = {};
