import React, { useMemo } from 'react';
import { IconButtonProps } from '../interface';
import { useGetContextLocale, useTranslate, hooks } from '@ali/we-util';
import { Button, Tooltip } from '@ali/we-design';
import { ButtonWrap, TitleWrap } from './styled';
import useVisibilitySensor from '../../hooks/useVisibilitySensor';
import { classnames } from '../../utils';
import { DisableWrap } from '../styled';
import { ToolbarIconContext } from '@ali/we-icon';

export const IconButton: React.FC<IconButtonProps> = React.memo(
  React.forwardRef((props, ref: React.ForwardedRef<{}>) => {
    const {
      icon,
      title,
      tooltip,
      shortcutText,
      showTitle,
      disabled,
      size,
      active,
      onClick,
      testid,
      className,
      onDoubleClick,
      borderRadius,
      disabledTip,
      style,
      extraNode,
      getContainer,
      role = '',
    } = props;
    const contextLocale = useGetContextLocale();
    const t = useTranslate(contextLocale);

    const [click, doubleClick] = hooks.useClickPreventionOnDoubleClick(
      onClick,
      onDoubleClick,
    );

    const {
      el,
      id,
      isHide,
      fineAdaptation,
      groupKey,
      isInOffScreenWrap,
      mode,
    } = useVisibilitySensor(ref, testid);

    const styleProps = {
      size,
      active,
      showTitle,
      borderRadius,
    };

    let tooltipForButton: string | React.ReactNode = '';
    if (typeof tooltip === 'string') {
      tooltipForButton = t(tooltip);
      if (shortcutText != null) {
        tooltipForButton += ` ${shortcutText}`;
      }
    } else {
      tooltipForButton = tooltip;
    }

    const ToolbarIconMemo = useMemo(
      () => ({
        size: size || 'medium',
      }),
      [size],
    );

    if (isHide) return null;

    return (
      <ToolbarIconContext.Provider value={ToolbarIconMemo}>
        <Tooltip
          disabled={!disabled}
          title={disabledTip}
          getContainer={getContainer}
        >
          <DisableWrap
            className='icon-button-layout button-wrap'
            disabled={disabled}
          >
            <ButtonWrap
              ref={el}
              data-id={id}
              data-mode={mode}
              {...styleProps}
              key={testid}
              locale={contextLocale}
              data-biz-key={fineAdaptation ? id : null}
              data-group-id={groupKey}
              className={classnames(
                className,
                'button-box',
                active ? 'actived' : '',
              )}
              onClick={disabled ? undefined : click}
              onDoubleClick={disabled ? undefined : doubleClick}
              style={style}
              data-testid={`${isInOffScreenWrap ? '' : testid}_wrap`}
              data-role={role}
            >
              <Button
                bordered={false}
                icon={icon}
                testid={isInOffScreenWrap ? '' : testid}
                className={classnames('icon-button')}
                title={tooltipForButton}
              />
              {showTitle ? (
                <TitleWrap {...styleProps} locale={contextLocale}>
                  <div>
                    {title && typeof title !== 'string' ? title : t(title)}
                  </div>
                </TitleWrap>
              ) : null}
              {extraNode}
            </ButtonWrap>
          </DisableWrap>
        </Tooltip>
      </ToolbarIconContext.Provider>
    );
  }),
);

IconButton.defaultProps = {
  size: 'medium',
};

export default IconButton;
