import getAsync from 'get-async';
import { getSharedFontDetector } from '../../../utils/FontDetector';
import { getFontFamily } from './getFontFamily';

// 并发请求时，保证只有一次 font 计算
export const detectFonts = async (): Promise<boolean[]> => {
  const asyncTaskKey = '@/ali/we-toolbar/detect-font';
  const rsts = getAsync(asyncTaskKey, async () => {
    const detector = getSharedFontDetector();
    const fonts = getFontFamily((s) => s).map((s) => s.value);

    const result = await new Promise((resolve) => {
      detector.detect(fonts, (rst: boolean[]) => {
        resolve(rst);
        detector.destroy();
      });
    });
    return result;
  });
  return rsts;
};
