import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
  useRef,
} from 'react';
import { CommonComponentProps } from '../../../common/interface';
import {
  ContainerMenuDown,
  DesWrap,
  GalleryWrap,
  IconWrapper,
  TableButton,
  TableStyleSet,
  TextWrap,
  TitleWrap,
} from './style';
import { useTranslate } from '@ali/we-util';
import {
  ArrowDownLineNormal,
  classnames,
  Dropdown,
  TabPane,
  Tabs,
  Tooltip,
} from '@ali/we-design';
import { ToolbarContext } from '../../layout/contexts';

import { DisableWrap } from '../../../common/styled';
import OnIdle from '../../../common/onIdle';
import FontSizeButton, { ITextTypeSelectOption } from './FontSizeButton';

export interface GalleryContainerProps extends CommonComponentProps {
  onChange?: (nextKey: string) => void;
  toolbarLength?: number;
  value?: string;
  defaultValue?: string;
  testid?: string;
}

const cellWidth = 76;
const margin = 34;

export const FontSizeMaxSelect: React.FC<GalleryContainerProps> = React.memo(
  (props) => {
    const {
      locale,
      onChange,
      value,
      defaultValue,
      disabled,
      disabledTip,
      testid,
    } = props;
    const [showArrowDown, setShowArrowDown] = useState(false);
    const [overlayVisible, setOverlayVisible] = useState(false);
    const { fontSizeMaxSelectSafeWidth = 0 } = useContext(ToolbarContext);
    const [moreOverlayCss, setMoreOverlayCss] = useState<string>();
    const getLength = () => {
      const moreLength = Math.floor(
        (fontSizeMaxSelectSafeWidth - margin) / cellWidth,
      );
      return Math.min(Math.max(2, moreLength), 6);
    };

    const [length, setLength] = useState(getLength);

    const translate = useTranslate(locale);
    const selfRef = useRef<HTMLElement>();
    const sizeComputedRef = useRef<boolean>(false);

    useEffect(() => {
      const timer = setTimeout(() => {
        setLength(getLength());
        // 未计算好 size 之前去掉缓动（from xingtain）
        sizeComputedRef.current =
          sizeComputedRef.current || fontSizeMaxSelectSafeWidth !== 0;
      }, 200);

      return () => clearTimeout(timer);
    }, [fontSizeMaxSelectSafeWidth]);

    const minFontSize = 9;
    const maxtFontSize = 13.5;
    // 文字类型列表：正文、标题1-6
    const options = useMemo<ITextTypeSelectOption[]>(
      () => [
        {
          title: translate('we_toolbar_font_family_Title_1'),
          type: 'heading-1',
          des: 'Heading 1',
          default: 20,
          fontWeight: 500,
          lineHeight: '1.45',
        },
        {
          title: translate('we_toolbar_font_family_Title_2'),
          type: 'heading-2',
          des: 'Heading 2',
          default: 16,
          fontWeight: 500,
          lineHeight: '1.45',
        },
        {
          title: translate('we_toolbar_font_family_Title_3'),
          type: 'heading-3',
          des: 'Heading 3',
          default: 14,
          fontWeight: 500,
          lineHeight: '1.45',
        },
        {
          title: translate('we_toolbar_font_family_Title_4'),
          type: 'heading-4',
          des: 'Heading 4',
          default: 12,
          fontWeight: 500,
          lineHeight: '1.45',
        },
        {
          title: translate('we_toolbar_font_family_Body'),
          type: 'paragraph',
          des: 'Paragraph',
          default: 11,
          fontWeight: 400,
          lineHeight: '1.45',
        },

        // todo huilan：locale
        {
          title: translate('we_toolbar_reference'),
          type: 'blockquote',
          des: 'Blockquote',
          default: 11,
          fontWeight: 400,
          lineHeight: '1.45',
        },
      ],
      [translate],
    );

    const handleChange = useCallback(
      (e: string) => {
        if (onChange) {
          onChange(e);
        }
      },
      [onChange],
    );

    const handleMenuChange = useCallback(
      (e: string) => {
        if (onChange) {
          onChange(e);
        }
      },
      [onChange],
    );

    const getOverlay = () => {
      return (
        <TableStyleSet
          className={moreOverlayCss}
          data-testid={`fontSizeMaxSelect_overlay_${testid}`}
        >
          {options.map((option) => {
            return (
              <TableButton
                key={option.type}
                value={option.type}
                active={option.type === (value || defaultValue)}
                className={classnames({
                  active: option.type === (value || defaultValue),
                })}
                testid={option.type}
                onClick={() => {
                  if (option.type !== (value || defaultValue)) {
                    handleMenuChange(option.type);
                  }
                }}
              >
                <TitleWrap
                  type={option.type}
                  style={{
                    fontSize: `${Math.min(
                      Math.max(option.default * 0.75, minFontSize),
                      maxtFontSize,
                    )}pt`,
                    fontWeight: option.fontWeight,
                    textOverflow: 'clip',
                    maxWidth: '65px',
                    lineHeight: '29px',
                    height: '29px',
                    overflow: 'hidden',
                  }}
                >
                  {option.title}
                </TitleWrap>
                <DesWrap>
                  <TextWrap>
                    {option.title === 'AaBbCcDdEe'
                      ? option.des
                      : `${translate('we_toolbar_font_family_detault')} ${
                          option.type === 'blockquote' ? '-' : option.default
                        }`}
                  </TextWrap>
                </DesWrap>
              </TableButton>
            );
          })}
        </TableStyleSet>
      );
    };

    /**
     * 展开收起动画
     */
    const moreTransition = () => {
      setMoreOverlayCss(overlayVisible ? 'scale-out' : 'scale-up');
      !overlayVisible && setTimeout(() => setMoreOverlayCss(''), 150);
    };

    const tabsStyle: React.CSSProperties = {
      width: length * 76 + 34,
      height: '100%',
    };

    const WrapperPlaceholder = React.useMemo(() => {
      return (
        <div
          style={{
            ...tabsStyle,
            padding: '3px 22px',
            display: 'flex',
            cursor: 'not-allowed',
            filter: 'opacity(0.3)',
          }}
        >
          {options.slice(0, 2).map((option) => (
            <div key={option.type} style={{ padding: '0 3px' }}>
              <FontSizeButton
                option={option}
                maxFontSize={maxtFontSize}
                minFontSize={minFontSize}
                value={value || defaultValue}
                defaultFontFamily={translate('we_toolbar_font_family_detault')}
              />
            </div>
          ))}
        </div>
      );
    }, [options, value, defaultValue, minFontSize, maxtFontSize]);

    if (!sizeComputedRef.current) {
      tabsStyle.transition = 'none';
    }

    return (
      <Tooltip disabled={!disabled} title={disabledTip}>
        <DisableWrap
          className="font-size-max-select-layout"
          disabled={disabled}
        >
          <div
            style={{
              position: 'relative',
              background: 'white',
              borderRadius: '4px',
            }}
            onMouseEnter={() => setShowArrowDown(true)}
            onMouseLeave={() => setShowArrowDown(false)}
            data-testid={testid}
          >
            <Dropdown
              offsetY={-3}
              overlayStyle={{ border: '0px' }}
              overlay={
                <OnIdle>
                  <ContainerMenuDown
                    data-testid={`fontSizeMaxSelect_showDown_${testid}`}
                  >
                    <Dropdown
                      overlay={getOverlay()}
                      overlayStyle={{
                        backgroundColor: 'transparent',
                        boxShadow: 'none',
                        border: 'none',
                      }}
                      onVisibleChange={(vi) => {
                        setOverlayVisible(vi);
                        moreTransition();
                      }}
                      placement="bottom"
                      visible={overlayVisible}
                    >
                      <IconWrapper
                        isDown
                        onClick={() => setOverlayVisible(!overlayVisible)}
                        active={showArrowDown || overlayVisible}
                        left={(length * 76 + 34) / 2}
                      >
                        <ArrowDownLineNormal
                          className={classnames({ opened: overlayVisible })}
                          style={
                            showArrowDown || overlayVisible
                              ? { width: 8, height: 8 }
                              : { visibility: 'hidden', width: 8, height: 8 }
                          }
                        />
                      </IconWrapper>
                    </Dropdown>
                  </ContainerMenuDown>
                </OnIdle>
              }
              placement="bottom"
              trigger={'hover'}
            >
              <GalleryWrap ref={selfRef}>
                <OnIdle placeholder={WrapperPlaceholder}>
                  <Tabs
                    excessMode="triangle"
                    type="custom"
                    style={tabsStyle}
                    onChange={handleChange}
                    activeKey={value || defaultValue}
                  >
                    {options.map((option) => {
                      return (
                        <TabPane
                          tab={
                            <FontSizeButton
                              option={option}
                              maxFontSize={maxtFontSize}
                              minFontSize={minFontSize}
                              value={value || defaultValue}
                              defaultFontFamily={translate(
                                'we_toolbar_font_family_detault',
                              )}
                            />
                          }
                          key={option.type}
                        />
                      );
                    })}
                  </Tabs>
                </OnIdle>
              </GalleryWrap>
            </Dropdown>
          </div>
        </DisableWrap>
      </Tooltip>
    );
  },
);
