import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconButtonPropsOmit } from '../../../common/interface';
import { IconButtonDefaultProps } from '../../../utils/defaultProps';
import IconButton from '../../../common/iconButton';
import { ToolbarBoldNormal } from '@ali/we-design';

export type BoldButtonProps = IconButtonPropsOmit;

export const BoldButton: React.FC<BoldButtonProps> = React.memo((props) => {
  const t = useTranslate(props.locale);
  return (
    <IconButton
      icon={<ToolbarBoldNormal />}
      title={t('we_toolbar_bold')}
      tooltip={t('we_toolbar_bold')}
      testid="bold_button"
      {...props}
    />
  );
});

BoldButton.defaultProps = { ...IconButtonDefaultProps };
