

import React, { useMemo, useCallback } from 'react';
import {
  QuoteNormal,
} from '@ali/we-design';
import {
  DocToolbarTextNormal,
  DocToolbarH1Normal,
  DocToolbarH2Normal,
  DocToolbarH3Normal,
  DocToolbarH4Normal,
} from '@ali/we-icon';
import { useTranslate } from '@ali/we-util';
import { IconSelectPropsOmit, Omit } from '../../../common/interface';
import useControlledValue from '../../../hooks/useControlledValue';
import { IconSelectDefaultProps } from '../../../utils/defaultProps';
import IconSelect from '../../../common/iconSelect';
import { TriggerWrap, OptionWrap, IconSelectWrap } from './styled';

export type HeadingSimple = 'paragraph' | 'heading-1' | 'heading-2' | 'heading-3' | 'heading-4' | 'blockquote';

export interface IHeadingSimpleSelectProps
  extends IconSelectPropsOmit<HeadingSimple> {
  /**
   * 默认的文字标题样式，当设置了value，该项忽略
   */
  defaultValue?: HeadingSimple;
  /**
   * 文字标题样式
   */
  value?: HeadingSimple;
  /**
   * 文字标题样式修改时的回调函数
   */
  onChange?: (align: HeadingSimple) => void;
  /**
   * 禁用的选项列表
   */
  disabledKeys?: HeadingSimple[];
}

interface ISelectOption {
  title: string;
  value: HeadingSimple;
  Icon: React.ComponentType<any>;
}

export const HeadingSimpleSelect: React.FC<
  Omit<IHeadingSimpleSelectProps, 'bordered'>
> = (props) => {
  const {
    locale,
    onChange,
    disabledKeys,
    title,
    active,
    showTitle,
    size,
    getContainer,
    role,
    ...restProps
  } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  // 标题的选项
  const options: ISelectOption[] = useMemo(
    () => [
      {
        title: t('we_toolbar_font_family_ts_Title_1'),
        value: 'heading-1',
        Icon: DocToolbarH1Normal,
      },
      {
        title: t('we_toolbar_font_family_ts_Title_2'),
        value: 'heading-2',
        Icon: DocToolbarH2Normal,
      },
      {
        title: t('we_toolbar_font_family_ts_Title_3'),
        value: 'heading-3',
        Icon: DocToolbarH3Normal,
      },
      {
        title: t('we_toolbar_font_family_ts_Title_4'),
        value: 'heading-4',
        Icon: DocToolbarH4Normal,
      },
      {
        title: t('we_toolbar_reference_simple'),
        value: 'blockquote',
        Icon: QuoteNormal,
      },
      {
        title: t('we_toolbar_font_family_ts_Body'),
        value: 'paragraph',
        Icon: DocToolbarTextNormal,
      },
    ],
    [t],
  );

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback(
    (align?: string) => {
      const index = options.findIndex((option) => option.value === align);
      const currentOption = options[index];
      return (
        <TriggerWrap style={{ minWidth: 16 }}>
          {currentOption && <currentOption.Icon />}
        </TriggerWrap>
      );
    },
    [options],
  );

  const handleChange = useCallback(
    (e) => {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
    },
    [onChange, setValue],
  );


  return (
    <IconSelectWrap>
      {/*
      // @ts-ignore */}
      <IconSelect
        {...restProps}
        value={value}
        onChange={handleChange}
        renderTrigger={renderTrigger}
        testid="HeadingSimpleSelect"
        title={title}
        tooltip={t('we_toolbar_heading_selection')}
        active={active}
        size={size}
        showTitle={showTitle}
        disabledKeys={disabledKeys}
        role={role}
      >
        {options.map((option) => {
          // 设计变更 - 不要前面的勾，被选中的对象在列表中高亮显示
          return (
            <OptionWrap
              value={option.value}
              key={option.value}
              testid={option.value}
              disabled={disabledKeys?.includes(option.value)}
              title={{ title: option.title, placement: 'left' }}
              data-role={option.value}
            >
              <option.Icon className="icon" style={{ fontSize: 20 }}/>
            </OptionWrap>
          );
      })}
      </IconSelect>
    </IconSelectWrap>
  );
};

HeadingSimpleSelect.defaultProps = {
  defaultValue: 'paragraph',
  ...IconSelectDefaultProps,
};
