import { Dropdown, Menu, MenuItem } from '@ali/we-design';
import { CardPreviewNormal, ToolbarPasteNormal } from '@ali/we-icon';
import { DropdownAnimation, useTranslate } from '@ali/we-util';
import React, { ReactElement, useRef, useState } from 'react';
import {
  AbstractSmallIcon,
  TextSmallIcon,
} from '../cardLayout/toolbar/styleSelectIcon';
import IconMenu from '../iconMenu';
import {
  SelectGalleryBox,
  StyleSelectFontBox,
  StyleSelectIconContainer,
  Wrapper,
} from './styled';
import useDelayedState from './useDelayedState';

export interface IOption {
  key: string;
  title: string;
  icon?: ReactElement;
  des?: string;
  desImg?: string;
}

type cardType = 'text' | 'card' | 'wideCard' | 'preview';

type ccpType = 'textFormat' | 'sourceFormat' | 'matchFormat' | 'markdownToDoc';

export interface IPastePanel {
  options: Array<IOption | string>;
  onChange?: (key: string, e?: React.MouseEvent<HTMLElement>) => void;
  hideSplitBtton?: boolean;
  autoUnfold?: boolean;
  title?: string;
  locale?: string;
  getContainer?: () => HTMLElement;
  width?: number;
}

const isCardType = (type: string) =>
  type === 'text' || type === 'card' || type === 'wideCard' || type === 'preview';

const isCCPType = (type: string) =>
  type === 'textFormat' ||
  type === 'sourceFormat' ||
  type === 'matchFormat' ||
  type === 'markdownToDoc';

export const PastePanel: React.FC<IPastePanel> = (props) => {
  const {
    options,
    onChange,
    hideSplitBtton = false,
    autoUnfold = false,
    locale,
    getContainer,
    width,
    title = '',
  } = props;
  const [visibleIndex, setVisibleIndex] = useDelayedState(-1);
  const [selectKey, setSelectKey] = useState(0);
  const [buttonVisible, setButtonVisible] = useState(autoUnfold);
  const isCreate = useRef<number>(0);
  const t = useTranslate(locale);

  // 卡片默认配置
  const defaultCardOptionMap = {
    text: {
      icon: <TextSmallIcon />,
      title: t('we_toolbar_locale_paste_custom_panel_link'),
      des: t('we_toolbar_locale_paste_custom_panel_text'),
      desImg:
        'https://img.alicdn.com/imgextra/i4/O1CN01SVRR9O1cHOarEhMi5_!!6000000003575-2-tps-180-140.png',
    },
    card: {
      icon: <AbstractSmallIcon />,
      title: t('we_toolbar_locale_paste_custom_panel_abstract_card'),
      des: t('we_toolbar_locale_paste_custom_panel_abstract'),
      desImg:
        'https://img.alicdn.com/imgextra/i2/O1CN01fPfwUo1RSskY9PviT_!!6000000002111-2-tps-180-140.png',
    },
    wideCard: {
      icon: <AbstractSmallIcon />,
      title: t('we_toolbar_locale_paste_custom_panel_abstract_card'),
      des: t('we_toolbar_locale_paste_custom_panel_abstract'),
      desImg:
        'https://img.alicdn.com/imgextra/i2/O1CN01fPfwUo1RSskY9PviT_!!6000000002111-2-tps-180-140.png',
    },
    preview: {
      icon: <CardPreviewNormal />,
      title: t('we_toolbar_locale_paste_custom_panel_preview_card'),
      des: t('we_toolbar_locale_paste_custom_panel_preview'),
      desImg:
        'https://img.alicdn.com/imgextra/i1/O1CN01I7NSjK1RPfypgAwaG_!!6000000002104-2-tps-180-140.png',
    },
  };

  const defaultCCPOptionMap = {
    textFormat: {
      title: t('we_toolbar_locale_paste_mode_text'),
    },
    sourceFormat: {
      title: t('we_toolbar_locale_paste_mode_source'),
    },
    matchFormat: {
      title: t('we_toolbar_locale_paste_mode_match'),
    },
    markdownToDoc: {
      title: t('we_toolbar_locale_paste_mode_markdown'),
    },
  };

  const handleVisibleChange = (v: boolean, index: number) => {
    if (v) {
      isCreate.current += 1;
      setVisibleIndex(index, 300);
    }
  };

  const handleFocusItemChange = (key?: string) => {
    if (key !== undefined) {
      isCreate.current += 1;
      setVisibleIndex(Number(key), 300);
    }
  };

  const handleButtonVisibleChange = (v: boolean) => {
    setButtonVisible(v);
  };

  const renderOptionItem = (
    index: number,
    key: string,
    title: string,
    des?: string,
    desImg?: string,
    icon?: ReactElement,
  ) => {
    return (
      <Dropdown
        placement="rightTop"
        trigger="hover"
        key={`paste-dropdown-${key}`}
        offsetY={-4}
        offsetX={2}
        onVisibleChange={(v) => handleVisibleChange(v, index)}
        visible={visibleIndex === index}
        overlay={
          <DropdownAnimation visible={visibleIndex === index}>
            {des && isCreate.current > 1 && visibleIndex === index ? (
              <SelectGalleryBox key={title}>
                <StyleSelectIconContainer>
                  <img alt={des} src={desImg} />
                </StyleSelectIconContainer>
                <StyleSelectFontBox>{title}</StyleSelectFontBox>
              </SelectGalleryBox>
            ) : null}
          </DropdownAnimation>
        }
        getContainer={getContainer}
      >
        <div>
          <MenuItem
            icon={icon}
            menuKey={key}
            testid={key}
            role={key}
            onClick={(_, e) => {
              onChange && onChange(key, e);
              if (!hideSplitBtton) {
                setButtonVisible(false);
                setSelectKey(index);
              }
            }}
          >
            {title}
          </MenuItem>
        </div>
      </Dropdown>
    );
  };

  const renderMenu = () => {
    return (
      <Menu
        selectedKeys={[`${selectKey}`]}
        onFocusItemChange={handleFocusItemChange}
        showSelect
        shortcutKey
        showSelectOnRight
        shouldSelectFirstItem
      >
        {options?.map((item, index) => {
          // 传入的 option 为卡片自定义
          if (typeof item === 'string') {
            if (isCardType(item)) {
              const { title, icon, des, desImg } = defaultCardOptionMap[
                item as cardType
              ];
              return (
                renderOptionItem(
                  index,
                  `${index}`,
                  title,
                  des,
                  desImg,
                  icon,
                ) || <div />
              );
            } else if (isCCPType(item)) {
              const { title } = defaultCCPOptionMap[item as ccpType];
              return renderOptionItem(index, item, title);
            }
          } else {
            const { key, title, icon, des, desImg } = item as IOption;
            return renderOptionItem(index, key, title, des, desImg, icon);
          }
        })}
      </Menu>
    );
  };

  const renderOptions = () => {
    return (
      <Wrapper width={width} className="ModeMenu">
        <DropdownAnimation visible>{renderMenu()}</DropdownAnimation>
      </Wrapper>
    );
  };

  const renderOverlay = () => {
    return (
      <Wrapper width={width} className="ModeMenu">
        {renderMenu()}
      </Wrapper>
    );
  };

  const renderButton = () => {
    return (
      <IconMenu
        icon={<ToolbarPasteNormal />}
        title={title}
        showTitle={Boolean(title)}
        overlay={renderOverlay()}
        visible={buttonVisible}
        onVisibleChange={handleButtonVisibleChange}
        getContainer={getContainer}
        testid="paste-panel-button"
      />
    );
  };

  return hideSplitBtton ? renderOptions() : renderButton();
};
