import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconButtonPropsOmit } from '../../../common/interface';
import { IconButtonDefaultProps } from '../../../utils/defaultProps';
import IconButton from '../../../common/iconButton';
import { ToolbarTranslateNormal } from '@ali/we-design';

export type TranslateButtonProps = IconButtonPropsOmit;

export const TranslateButton: React.FC<TranslateButtonProps> = React.memo((props) => {
  const t = useTranslate(props.locale);

  return (
    <IconButton
      icon={<ToolbarTranslateNormal />}
      title={t('we_toolbar_translate')}
      tooltip={t('we_toolbar_translate')}
      testid="translate_button"
      {...props}
    />
  );
});

TranslateButton.defaultProps = { ...IconButtonDefaultProps };
