import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconButtonPropsOmit } from '../../../common/interface';
import { IconButtonDefaultProps } from '../../../utils/defaultProps';
import IconButton from '../../../common/iconButton';
import { ToolbarDiagramNormal } from '@ali/we-design';

export type ChartButtonProps = IconButtonPropsOmit & {
  icon?: React.ReactNode;
};

// TODO: 需要替换 icon
export const ChartButton: React.FC<ChartButtonProps> = React.memo((props) => {
  const t = useTranslate(props.locale);

  return (
    <IconButton
      testid="chart_button"
      icon={props.icon || <ToolbarDiagramNormal />}
      title={t('we_toolbar_diagram')}
      tooltip={t('we_toolbar_diagram')}
      {...props}
    />
  );
});

ChartButton.defaultProps = { ...IconButtonDefaultProps };
