export const GalleryButton: any = withTheme(styled.div<any>`
  flex-direction: column;
  display: flex;
  height: 44px;
  width: 70px;
  cursor: default;
  align-items: center;
  justify-content: space-between;
  &.active {
    border-radius: 4px;
    background-color: ${({ theme }) => theme.ui.toolbarButtonSelect};
  }
  ${({ theme, active, disabled }) =>
    !disabled &&
    css`
      :hover {
        border-radius: 4px;
        background-color: ${active
    ? theme.ui.toolbarButtonSelectHover
    : theme.ui.toolbarButtonHover};
      }
      :active {
        border-radius: 4px;
        background-color: ${disabled
    ? active
      ? theme.ui.toolbarButtonClick
      : theme.ui.toolbarButtonTransparent
    : theme.ui.toolbarButtonClick};
      }
    `}
`);

