import React, { FC, useState, useMemo, useEffect } from 'react';
import { PreviewCardLayoutContainer } from './styled';
import { PreviewCardLayoutProps } from './interface';
import { CardContainer } from '../container';
import { DragLevelIcon } from './dragLevel';
import { Toolbar, InlineToolbarContext } from '../toolbar';
import { ArrowRightOutlineSmallBetaNormal, uuid } from '@ali/we-design';

export const PreviewCardLayout: FC<PreviewCardLayoutProps> = (props) => {
  const id = useMemo(() => uuid(), []);
  const {
    defaultHeight = 170,
    minHeight = 38,
    maxHeight = Infinity,
    isSelected,
    children,
    icon,
    title,
    linkTitleClick,
    toolbarConfigs,
    style,
    dragLevel = false,
    className,
    onClick,
    onMoveStart,
    onMove,
    onMoveEnd,
    throttle = 0,
    type = 'normal',
    thresholdWidth,
    getContainer,
    height: currentHeight,
  } = props;

  const {
    toolbarOffsetX = !title ? 4 : 6,
    toolbarOffsetY = toolbarOffsetX,
  } = props;

  const [height, setHeight] = useState(defaultHeight);

  useEffect(() => {
    currentHeight && setHeight(currentHeight);
  }, [currentHeight]);
  // 是否拖拽
  const [drag, setDrag] = useState(false);
  let preDate = useMemo(() => Date.now(), []);

  // 鼠标抬起
  const mouseUp = () => {
    setDrag(false);
    onMoveEnd && onMoveEnd();
    document.removeEventListener('mousemove', mouseMove);
  };

  // 拖拽时移动鼠标
  const mouseMove = (e: MouseEvent) => {
    // 节流
    const now = Date.now();
    if (now - preDate < throttle) {
      return;
    }
    preDate = now;

    // 计算 height
    const container = document.getElementById(`preview-card-layout-${id}`);
    if (!container) {
      return;
    }
    const { clientY } = e;
    const topPosition = container.getBoundingClientRect().top;
    let height = clientY - topPosition;
    if (height > maxHeight) {
      height = maxHeight;
    }
    if (height < minHeight) {
      height = minHeight;
    }
    setHeight(height);
    onMove && onMove(height);
  };

  return type === 'custom' ? (
    <PreviewCardLayoutContainer
      toolbarOffset={[toolbarOffsetX, toolbarOffsetY]}
      id={`preview-card-layout-${id}`}
      className={className}
      drag={drag}
      style={
        isSelected
          ? {
            border: '1px solid #0089FF',
            boxShadow: '0px 0px 0px 3px rgba(0, 106, 254, 0.12)',
            height,
            ...style,
          }
          : {
            border: '1px solid #EBECF0',
            height,
            ...style,
          }
      }
    >
      <CardContainer
        className="card-layout-container"
        thresholdWidth={thresholdWidth}
      >
        <div className="card-layout-wrap-custom">
          <div className="card-layout-content-wrap-custom">{children}</div>
          <div
            className="card-layout-toolbar-custom"
            onClick={(e) => e.stopPropagation()}
          >
            <Toolbar
              toolbarConfigs={toolbarConfigs}
              getContainer={getContainer}
            />
          </div>
        </div>
        <InlineToolbarContext.Consumer>
          {({ isToolbarVisible }) =>
            dragLevel && (
              <div
                className="drag-level"
                style={{ opacity: isToolbarVisible || drag ? 1 : 0 }}
                onMouseDown={() => {
                  setDrag(true);
                  onMoveStart && onMoveStart(height);
                  document.addEventListener('mouseup', mouseUp);
                  document.addEventListener('mousemove', mouseMove);
                }}
              >
                <DragLevelIcon />
              </div>
            )
          }
        </InlineToolbarContext.Consumer>
      </CardContainer>
    </PreviewCardLayoutContainer>
  ) : type === 'normal' ? (
    <PreviewCardLayoutContainer
      toolbarOffset={[toolbarOffsetX, toolbarOffsetY]}
      id={`preview-card-layout-${id}`}
      className={className}
      drag={drag}
      style={
        isSelected
          ? {
            border: '1px solid #0089FF',
            boxShadow: '0px 0px 0px 3px rgba(0, 106, 254, 0.12)',
            height,
            ...style,
          }
          : {
            height,
            ...style,
          }
      }
    >
      <CardContainer
        className="card-layout-container"
        thresholdWidth={thresholdWidth}
      >
        <div className="card-layout-wrap">
          <div
            className="card-layout-head"
            style={{ height: !title ? 32 : 39 }}
          >
            <div className="card-layout-icon" onClick={linkTitleClick}>
              {icon}
            </div>
            <div className="card-layout-head-right">
              <div
                className={`card-layout-title ${linkTitleClick && 'link'}`}
                onClick={linkTitleClick}
              >
                <div className="card-layout-title-arrow">
                  <span className="text">{title}</span>
                  {linkTitleClick && (
                    <ArrowRightOutlineSmallBetaNormal
                      className="right-arrow link"
                      onClick={linkTitleClick}
                    />
                  )}
                </div>
              </div>

              <div
                className="card-layout-toolbar"
                onClick={(e) => e.stopPropagation()}
              >
                <Toolbar
                  toolbarConfigs={toolbarConfigs}
                  getContainer={getContainer}
                />
              </div>
            </div>
          </div>
          <div className="card-layout-content-wrap">{children}</div>
          {!title && (
            <div
              className="no-title-box"
              onClick={onClick}
              style={
                style?.backgroundColor && {
                  backgroundColor: style?.backgroundColor,
                }
              }
            />
          )}
        </div>
        <InlineToolbarContext.Consumer>
          {({ isToolbarVisible }) =>
            dragLevel && (
              <div
                className="drag-level"
                style={{ opacity: isToolbarVisible || drag ? 1 : 0 }}
                onMouseDown={() => {
                  setDrag(true);
                  onMoveStart && onMoveStart(height);
                  document.addEventListener('mouseup', mouseUp);
                  document.addEventListener('mousemove', mouseMove);
                }}
              >
                <DragLevelIcon />
              </div>
            )
          }
        </InlineToolbarContext.Consumer>
      </CardContainer>
    </PreviewCardLayoutContainer>
  ) : (
    <PreviewCardLayoutContainer
      toolbarOffset={[toolbarOffsetX, toolbarOffsetY]}
      id={`preview-card-layout-${id}`}
      className={className}
      drag={drag}
    >
      <div className="card-layout-wrap">
        <div className="card-layout-head">
          <div className="card-layout-icon">{icon}</div>
          <div className="card-layout-head-right">
            <div className="card-layout-title">{title}</div>
            {linkTitleClick && (
              <ArrowRightOutlineSmallBetaNormal
                className="right-arrow link"
                onClick={onClick}
              />
            )}
            <div
              className="card-layout-toolbar"
              onClick={(e) => e.stopPropagation()}
            >
              <Toolbar toolbarConfigs={toolbarConfigs} />
            </div>
          </div>
        </div>
        <div className="card-layout-content-wrap">{children}</div>
      </div>
    </PreviewCardLayoutContainer>
  );
};
