import { useTranslate } from '@ali/we-util';
import * as React from 'react';
import { ToolbarInsertNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';

export type InsertButtonProps = IconMenuPropsOmit;

export const InsertButton: React.FC<InsertButtonProps> = React.memo((props) => {
  const [visible, setVisible] = React.useState(false);

  const handleVisibleChange = (value: boolean) => {
    setVisible(value);
  };
  const t = useTranslate(props.locale);
  return (
    <IconMenu
      visible={visible}
      icon={<ToolbarInsertNormal />}
      title={t('we_toolbar_insert')}
      tooltip={t('we_toolbar_insert')}
      onVisibleChange={handleVisibleChange}
      testid="InsertButton"
      {...props}
    />
  );
});

InsertButton.defaultProps = {};
