import { useCallback, useMemo, useRef, useContext } from 'react';

import {
  ToolbarContext,
  GroupWrapperContext,
  OverLayContext,
} from '../biz/layout/contexts';
import { uuid } from '../utils';

// @ts-ignore
export default (ref: React.ForwardedRef<{}>, testid?: string) => {
  const el = useRef<HTMLElement>();
  const { fineAdaptation, groupKey } = useContext(GroupWrapperContext);
  const { inOverlay, overlayHideBlocks, ignoreRegister } = useContext(
    OverLayContext,
  );
  const { hideBlocks = [], mode, isInOffScreenWrap } = useContext(
    ToolbarContext,
  );

  const id = useMemo(
    () => [mode, groupKey, testid || uuid()].filter((o) => o).join('-'),
    [groupKey, mode],
  );

  const isHide = useCallback((): boolean => {
    if (!fineAdaptation) return false;
    if (inOverlay) {
      return overlayHideBlocks.indexOf(id) !== -1;
    } else {
      return hideBlocks.indexOf(id) !== -1;
    }
  }, [
    inOverlay,
    overlayHideBlocks,
    hideBlocks,
    groupKey,
    fineAdaptation,
    testid,
  ]);

  return {
    el: ref || el,
    id,
    isHide: isHide(),
    fineAdaptation,
    groupKey: [mode, groupKey].filter((o) => o).join('-'),
    mode,
    ignoreRegister,
    inOverlay,
    isInOffScreenWrap,
  };
};
