import React, { useCallback, useMemo, useState } from 'react';
import { IconSelectProps } from '../interface';
import { Select as SelectUI, Tooltip, theme } from '@ali/we-design';
import { useTranslate, useGetContextLocale } from '@ali/we-util';
import { ToolbarIconContext } from '@ali/we-icon';
import { ButtonWrap, TextWarp, TitleWrap } from './styled';
import { TOOL_SIZE } from '../../utils/consts';

import { classnames } from '../../utils';
import useVisibilitySensor from '../../hooks/useVisibilitySensor';
import { DisableWrap } from '../styled';
import OnIdle, { OnIdlePlaceholder } from '../onIdle';

export const IconSelect: React.FC<IconSelectProps<any>> = React.memo(
  React.forwardRef((props, ref: React.ForwardedRef<{}>) => {
    const {
      title,
      showTitle,
      disabled,
      size,
      active,
      disabledTip,
      renderTrigger,
      children,
      value,
      visible: propsVisiable,
      onChange,
      locale,
      tooltip,
      shortcutText,
      testid,
      width,
      style,
      borderRadius,
      extraNode,
      animationPlacement,
      getContainer,
      role,
      ...restProps
    } = props;

    const contextLocale = useGetContextLocale();
    const t = useTranslate(contextLocale);
    const [visible, setVisible] = useState(propsVisiable);

    const {
      el,
      id,
      isHide,
      fineAdaptation,
      groupKey,
      isInOffScreenWrap,
      mode,
    } = useVisibilitySensor(ref, testid);

    const styleProps = {
      size,
      active,
      showTitle,
      locale: locale || contextLocale,
    };

    const titleNode = useMemo(() => {
      return <div>{title && typeof title !== 'string' ? title : t(title)}</div>;
    }, [t, title]);

    const makeTrigger = useCallback(
      (val) => (
        <>
          {renderTrigger && renderTrigger(val)}{' '}
          {showTitle && size !== TOOL_SIZE.LARGE ? (
            <TextWarp {...styleProps}>{titleNode}</TextWarp>
          ) : null}
        </>
      ),
      [renderTrigger, title, showTitle]
    );

    let tooltipForButton: string | React.ReactNode = '';
    if (typeof tooltip === 'string') {
      tooltipForButton = t(tooltip);
      if (shortcutText != null) {
        tooltipForButton += ` ${shortcutText}`;
      }
    } else {
      tooltipForButton = tooltip;
    }

    const ToolbarIconMemo = useMemo(
      () => ({
        size: size || 'medium',
      }),
      [size]
    );

    const styleWrap = useMemo(
      () => ({
        ...style,
        ...{ width, borderRadius },
        ...(visible ? { backgroundColor: theme.toolbarButtonClick } : {}),
      }),
      [style, visible, width, borderRadius]
    );

    if (isHide) return null;

    return (
      <ToolbarIconContext.Provider value={ToolbarIconMemo}>
        <Tooltip
          disabled={!disabled}
          title={disabledTip}
          getContainer={getContainer}
        >
          <DisableWrap
            disabled={disabled}
            className={classnames('icon-select-layout')}
          >
            <ButtonWrap
              ref={el}
              data-id={id}
              data-mode={mode}
              data-biz-key={fineAdaptation ? id : null}
              data-group-id={groupKey}
              data-role={role}
              {...styleProps}
            >
              {showTitle && size === TOOL_SIZE.LARGE && (
                <TitleWrap {...styleProps} onClick={() => setVisible(true)}>
                  <div>{titleNode}</div>
                </TitleWrap>
              )}
              <SelectUI
                {...restProps}
                className='select-toolbar'
                onChange={onChange}
                value={value}
                style={styleWrap}
                renderTrigger={makeTrigger}
                title={disabled ? disabledTip : tooltipForButton}
                visible={visible}
                onVisibleChange={(visible) => setVisible(visible)}
                testid={isInOffScreenWrap ? '' : testid}
                overlayClassName='select-toolbar-overlay'
                animationPlacement={animationPlacement}
                getContainer={getContainer}
              >
                <></>
                <OnIdle placeholder={<OnIdlePlaceholder />}>{children}</OnIdle>
              </SelectUI>
              {extraNode}
            </ButtonWrap>
          </DisableWrap>
        </Tooltip>
      </ToolbarIconContext.Provider>
    );
  })
);

IconSelect.defaultProps = {
  size: 'medium',
};

export default IconSelect;
