import React, { FC, useState, useEffect, useRef, useMemo } from 'react';
import { CardLayoutContainer, ToolbarBox } from './styled';
import { CardLayoutProps } from '../interface';
import ResizeObserve from 'resize-observer-polyfill';
import { uuid } from '@ali/we-design';
import { InlineToolbarContext } from './inlineToolbarContext';

export const CardLayout: FC<CardLayoutProps> = (props: CardLayoutProps) => {
  const {
    children,
    toolbar,
    isSelected,
    onToolbarWidthChange,
    onHoverChange,
    toolbarVisible,
    style,
    toolbarOffsetX,
    toolbarOffsetY,
  } = props;
  const id = useMemo(() => uuid(), []);
  const [opacity, setOpacity] = useState(0);
  const [cardLayoutWidth, setCardLayoutWidth] = useState(0);
  const [menuFoldedOpacity, setMenuFoldedOpacity] = useState(0);
  const cardLayoutRef = useRef(null);
  const toolbarBoxRef = useRef(null);

  useEffect(() => {
    if (!cardLayoutRef.current || !toolbarBoxRef.current) return;
    const resizerObserve = new ResizeObserve((entries: ResizeObserverEntry[]) => {
      entries.forEach((item) => {
        switch (item.target.getAttribute('data-id')) {
          case `card-layout-container-${id}`:
            setCardLayoutWidth(item.contentRect.width);
            break;
          case `toolbar-box-${id}`:
            onToolbarWidthChange && onToolbarWidthChange(item.contentRect.width);
          default:
            break;
        }
      });
    });

    if (resizerObserve && cardLayoutRef.current && toolbarBoxRef.current) {
      resizerObserve.observe(cardLayoutRef.current!);
      resizerObserve.observe(toolbarBoxRef.current!);
    }

    return () => {
      if (ResizeObserve) {
        resizerObserve.disconnect();
      }
    };
  }, [cardLayoutRef, toolbarBoxRef]);

  const onVisibleChange = (e: boolean) => {
    setMenuFoldedOpacity(e ? 1 : 0);
  };

  const controlledOpacity =
    typeof toolbarVisible === 'boolean' ? (toolbarVisible ? 1 : 0) : opacity || menuFoldedOpacity;

  return (
    <InlineToolbarContext.Provider value={{ width: cardLayoutWidth, onVisibleChange }}>
      <CardLayoutContainer
        style={style}
        isSelected={isSelected}
        className="card-layout-container"
        data-id={`card-layout-container-${id}`}
        ref={cardLayoutRef}
        onMouseEnter={() => {
          setOpacity(1);
          onHoverChange && onHoverChange(true);
        }}
        onMouseLeave={() => {
          setOpacity(0);
          onHoverChange && onHoverChange(false);
        }}
      >
        {children}
        <ToolbarBox
          onClick={(e) => e.stopPropagation()}
          onMouseDown={(e) => e.stopPropagation()}
          style={{ opacity: controlledOpacity }}
          ref={toolbarBoxRef}
          data-id={`toolbar-box-${id}`}
          toolbarOffsetX={toolbarOffsetX}
          toolbarOffsetY={toolbarOffsetY}
        >
          {toolbar}
        </ToolbarBox>
      </CardLayoutContainer>
    </InlineToolbarContext.Provider>
  );
};
