import React, { FC, useEffect, useRef, useState, useMemo } from 'react';
import styled from 'styled-components';
import ResizeObserve from 'resize-observer-polyfill';
import { uuid } from '@ali/we-design';
import { InlineToolbarContext } from '../toolbar';
import { CardContainerProps } from './interface';

const id = uuid();

const THROTTLE_TIME = 100;

// 延迟显示 & 消失
const DEALY_APPEAR = 100;
const DELAY_DISAPPEAR = 200;

const CardContainerBox = styled.div`
  box-sizing: border-box;
`;

export const CardContainer: FC<CardContainerProps> = (props) => {
  const cardContainerBoxRef = useRef(null);
  const [mouseEnter, setMouseEnter] = useState(false);
  const [isFolded, setIsFolded] = useState(false);
  const [menuVisible, setMenuVisible] = useState(false);
  const [delay, setDelay] = useState(setTimeout(() => {}, 0));
  const {
    children,
    toolbarVisible,
    className,
    thresholdWidth = -Infinity,
  } = props;
  let preDate = useMemo(() => Date.now(), []);

  const onVisibleChange = (e: boolean) => {
    setMenuVisible(e);
  };

  useEffect(() => {
    if (!cardContainerBoxRef.current) return;
    const resizeObserve = new ResizeObserve(
      (entries: ResizeObserverEntry[]) => {
        const entry = entries.find(
          (item) =>
            item.target.getAttribute('data-id') === `card-container-${id}`,
        );
        entry && setIsFolded(entry.contentRect.width < thresholdWidth);
      },
    );

    if (resizeObserve && cardContainerBoxRef.current) {
      resizeObserve.observe(cardContainerBoxRef.current!);
    }

    return () => {
      if (resizeObserve) {
        resizeObserve.disconnect();
      }
    };
  }, [cardContainerBoxRef]);

  useEffect(() => {
    return () => {
      clearTimeout(delay);
    };
  }, []);

  const handleMouseEnterOrLeave = (e: boolean) => {
    const delayTime = e ? DEALY_APPEAR : DELAY_DISAPPEAR;
    clearTimeout(delay);
    setDelay(
      setTimeout(() => {
        setMouseEnter(e);
      }, delayTime),
    );
  };

  const handleMouseOver = () => {
    if (Date.now() - preDate > THROTTLE_TIME) {
      handleMouseEnterOrLeave(true);
      preDate = Date.now();
    }
  };

  return (
    <InlineToolbarContext.Provider
      value={{
        isFolded,
        onVisibleChange,
        isToolbarVisible: mouseEnter || menuVisible,
        toolbarDefault: toolbarVisible,
      }}
    >
      <CardContainerBox
        ref={cardContainerBoxRef}
        data-id={`card-container-${id}`}
        onMouseEnter={() => handleMouseEnterOrLeave(true)}
        onMouseLeave={() => handleMouseEnterOrLeave(false)}
        // TODO: onMouseOver 检测鼠标 over 事件
        onMouseOver={() => handleMouseOver()}
        className={className}
      >
        {children}
      </CardContainerBox>
    </InlineToolbarContext.Provider>
  );
};
