import { useTranslate } from '@ali/we-util';
import * as React from 'react';
import { SettingNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';

export type SettingMenuButtonProps = IconMenuPropsOmit;

export const SettingMenuButton: React.FC<SettingMenuButtonProps> = React.memo(
  (props) => {
    const [visible, setVisible] = React.useState(false);

    const handleVisibleChange = (value: boolean) => {
      setVisible(value);
    };
    const t = useTranslate(props.locale);

    return (
      <IconMenu
        visible={visible}
        icon={<SettingNormal />}
        title={t('we_toolbar_setting')}
        tooltip={t('we_toolbar_setting')}
        onVisibleChange={handleVisibleChange}
        testid="SettingMenuButton"
        {...props}
      />
    );
  },
);

SettingMenuButton.defaultProps = {};
