import React, { useEffect, useMemo, useRef, useState } from 'react';
import { CardPlaceholderProps } from './interface';
import { CardPlaceholderContainer } from './styled';
import { Toolbar } from '../toolbar';
import { CardContainer } from '../container';
import { Dropdown, uuid } from '@ali/we-design';
import ResizeObserver from 'resize-observer-polyfill';

import { DropdownAnimation } from '@ali/we-util';

const delay = 150;

export const Placeholder: React.FC<CardPlaceholderProps> = (props) => {
  const {
    height = 65,
    title,
    icon,
    desc,
    type = 'normal',
    isSelected = false,
    toolbarConfigs,
    onClick,
    onVisibleChange,
    clickable = true,
    overlay = <></>,
    visible,
    offsetX,
    offsetY = 4,
    overlayStyle,
    thresholdWidth,
    getContainer,
  } = props;

  const id = useMemo(() => uuid(), []);
  const cardPlaceholderContainerRef = useRef(null);
  const [active, setActive] = useState(false);
  const [placeholderWidth, setPlaceholderWidth] = useState(0);
  const [dropdownVisible, setDropdownVisible] = useState(false);

  // 实时计算宽度
  useEffect(() => {
    if (!cardPlaceholderContainerRef.current) {
      return;
    }
    const resizeObserver = new ResizeObserver(
      (entries: ResizeObserverEntry[]) => {
        const entry = entries.find(
          (item) =>
            item.target.getAttribute('data-id') ===
            `card-placeholder-container-${id}`,
        );
        if (entry) {
          setPlaceholderWidth(entry.contentRect.width);
        }
      },
    );
    if (resizeObserver && cardPlaceholderContainerRef.current) {
      resizeObserver.observe(cardPlaceholderContainerRef.current!);
    }
    return () => {
      if (resizeObserver) {
        resizeObserver.disconnect();
      }
    };
  }, [cardPlaceholderContainerRef]);

  return type === 'normal' ? (
    <Dropdown
      getContainer={getContainer}
      overlay={
        <DropdownAnimation
          visible={visible || dropdownVisible}
          overlayStyle={{
            width: placeholderWidth,
            borderRadius: '8px',
            border: 'none',
            boxShadow:
              '0px 0px 1px rgba(0, 0, 0, 0.24), 0px 8px 24px rgba(0, 0, 0, 0.16)',
            ...overlayStyle,
          }}
        >
          {overlay}
        </DropdownAnimation>
      }
      overlayStyle={{
        border: 'none',
        boxShadow: 'none',
        background: 'none',
      }}
      offsetX={offsetX}
      offsetY={offsetY}
      placement="bottom"
      visible={visible}
      onVisibleChange={(e: boolean) => {
        setDropdownVisible(e);
        onVisibleChange && onVisibleChange(e);
      }}
      disableDomAlign
    >
      <CardPlaceholderContainer
        delay={delay}
        height={height}
        isSelected={isSelected}
        clickable={clickable}
        onClick={onClick}
        className={`${active ? 'active' : ''} ${
          visible || dropdownVisible ? 'select' : ''
        }`}
        onMouseDown={() => setActive(true)}
        onMouseUp={() => setActive(false)}
        ref={cardPlaceholderContainerRef}
        data-id={`card-placeholder-container-${id}`}
        type={type}
      >
        <CardContainer
          className="card-container"
          thresholdWidth={thresholdWidth}
        >
          {icon && <div className="placeholder-icon-box">{icon}</div>}
          <div className="placeholder-right">
            <div className="placeholder-top">
              <div
                className="placeholder-title"
                style={!desc ? { lineHeight: '38px' } : {}}
              >
                {title}
              </div>
              <div className="placeholder-toolbar">
                <Toolbar
                  toolbarConfigs={toolbarConfigs}
                  getContainer={getContainer}
                />
              </div>
            </div>
            {desc && (
              <div className="placeholder-desc">
                <div className="desc-text">{desc}</div>
              </div>
            )}
          </div>
        </CardContainer>
      </CardPlaceholderContainer>
    </Dropdown>
  ) : (
    <CardPlaceholderContainer
      delay={delay}
      height={height}
      clickable={clickable}
      onClick={onClick}
      data-id={`card-placeholder-container-${id}`}
      type={type}
    >
      <CardContainer className="card-container">
        {icon && <div className="placeholder-icon-box">{icon}</div>}
        <div className="placeholder-right">
          <div className="placeholder-top">
            <div className="placeholder-title">{title}</div>
          </div>
        </div>
      </CardContainer>
    </CardPlaceholderContainer>
  );
};
