import styled, { css } from 'styled-components';
import { withTheme } from '@ali/we-design';
import { IconButtonProps, IconSelectProps } from '../interface';
import { TOOL_SIZE } from '../../utils/consts';

export const TitleWrap: any = withTheme(styled.div<IconButtonProps>`
  font-size: ${({ theme, size }) =>
    size === TOOL_SIZE.LARGE ? theme.ui.fontSizeSmall : theme.ui.fontSizeMenu};
  color: ${({ theme }) => theme.ui.commonActionIconColor};
  font-weight: normal;
  user-select: none;
  cursor: default;
  margin-left: ${({ theme, size }) =>
    size === TOOL_SIZE.LARGE ? '' : theme.ui.marginMini};
  align-items: center;
  display: flex;
  flex: 1;
  text-align: center;
  max-width: 52px;
  min-width: 45px;
  justify-content: center;
  div {
    overflow: hidden;
    text-overflow: ellipsis;
    padding-top: 2px;
    -webkit-line-clamp: 2;
    word-break: break-word;
    line-height: 12px;
  }
  ${({ disabled }) => disabled && 'opacity:.35;cursor:not-allowed;'};

  ${({ theme, active, disabled }) =>
    !disabled &&
    css`
      :hover ~ .select-toolbar {
        background-color: ${active
          ? theme.ui.toolbarButtonClick
          : theme.ui.toolbarButtonHover};
      }
      :active ~ .select-toolbar {
        background-color: ${disabled
          ? active
            ? theme.ui.toolbarButtonSelect
            : theme.ui.toolbarButtonTransparent
          : theme.ui.toolbarButtonClick};
      }
    `}
`);

export const TextWarp: any = withTheme(styled.div<IconSelectProps<string>>`
  margin-left: ${({ theme }) => theme.ui.marginMini};
  font-size: ${({ theme, size }) =>
    size === TOOL_SIZE.LARGE ? theme.ui.fontSizeSmall : theme.ui.fontSizeMenu};
  line-height: 20px;
`);

const largeAndShowTitleWrap = css<any>`
  max-width: 52px;
  min-width: 45px;
  ${TextWarp} {
    margin-left: 0;
    font-size: ${({ theme }) => theme.ui.fontSizeSmall};
    line-height: 11px;
    display: block;
    overflow: hidden;
    text-align: center;
    div {
      overflow: hidden;
      text-overflow: ellipsis;
      padding-top: 2px;
      -webkit-line-clamp: 2;
      word-break: break-word;
      line-height: 12px;
    }
    ${({ disabled }) =>
      disabled &&
      css`
        opacity: 0.35;
      `}
  }
`;

export const ButtonWrap = withTheme(styled.div<IconSelectProps<string>>`
  display: flex;
  flex-direction: ${({ size }) =>
    size === TOOL_SIZE.LARGE ? 'column-reverse' : 'row'};
  ${({ size }) =>
    size === TOOL_SIZE.LARGE &&
    css`
      height: 52px;
    `}

  .select-toolbar {
    border-width: 0;
    height: auto;
    padding: 0;
    display: flex;
    margin: auto;
    cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'default')};
    ${({ active, theme, disabled }) =>
      active &&
      `background-color: ${
        disabled
          ? theme.ui.toolbarButtonSelectDisable
          : theme.ui.toolbarButtonSelect
      };`}
  }
  ${({ disabled, theme, active }) =>
    disabled &&
    css`
      .select-toolbar.disabled {
        background-color: ${active
          ? theme.ui.toolbarButtonSelectDisable
          : 'transparent'};
      }
    `}
  .select_trigger_wrap {
    width: 100%;
    .title {
      margin-left: 2px;
      font-size: 13px;
    }
  }
  .select_left_wrap {
    padding: ${({ theme, size }) =>
      `${
        size === TOOL_SIZE.MEDIUM ? theme.ui.paddingTiny : theme.ui.paddingMini
      } ${theme.ui.paddingMini}`};
    font-size: ${({ theme, size }) =>
      size === TOOL_SIZE.LARGE
        ? theme.ui.fontSizeSmall
        : theme.ui.fontSizeMenu};
    border-right: 1px solid transparent;
    display: flex;
  }
  .select_left_wrap .weicon {
    font-size: ${({ theme, size }) =>
      size === TOOL_SIZE.LARGE
        ? theme.ui.toolbarFontSizeIconLarge
        : theme.ui.toolbarFontSizeIcon};
  }
  .select_right_wrap {
    width: 12px;
    display: flex;
    justify-content: center;
  }
  .select_right_wrap .weicon {
    font-size: 8px;
    margin: 0;
    transform: rotate(0deg) scale(1);
  }
  .select_right_wrap .weicon.opened {
    font-size: 8px;
    margin: 0;
    transform: rotate(180deg) scale(1);
  }

  .select-toolbar:hover {
    border-width: 0;
    ${({ active, theme, disabled }) =>
      !disabled &&
      `background-color:  ${
        active ? theme.ui.toolbarButtonSelectHover : theme.ui.toolbarButtonHover
      }; `}
  }

  .select-toolbar:active {
    ${({ theme, disabled }) =>
      !disabled && `background-color :${theme.ui.toolbarButtonClick}`}
  }

  ${({ size, showTitle }) =>
    size === TOOL_SIZE.LARGE && showTitle && largeAndShowTitleWrap};
`);
