import * as React from 'react';
import { ToolbarShapeInsertNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';
import { useTranslate } from '@ali/we-util';

export type ShapeInsertButtonProps = IconMenuPropsOmit;

export const ShapeInsertButton: React.FC<ShapeInsertButtonProps> = React.memo(
  (props) => {
    const [visible, setVisible] = React.useState(false);

    const handleVisibleChange = (value: boolean) => {
      setVisible(value);
    };
    const t = useTranslate(props.locale);
    return (
      <IconMenu
        visible={visible}
        icon={<ToolbarShapeInsertNormal />}
        title={t('we_toolbar_shape_insert')}
        tooltip={t('we_toolbar_shape_insert')}
        onVisibleChange={handleVisibleChange}
        testid="ShapeInsertButton"
        {...props}
      />
    );
  },
);

ShapeInsertButton.defaultProps = {};
