import React, { PureComponent } from 'react';
import {
  ArrowRightNormal,
  SomecolorNormal,
  Select,
  SelectOption,
} from '@ali/we-design';
import { TranslateComp } from '@ali/we-util';
import {
  colorDataNormal,
  colorDataArt,
  colorDataArtWithGradient,
} from './colorData';
import {
  LineGroupNormal,
  LineGroupArt,
  PaletteWrapper,
  CustomGroup,
  CustomPicker,
  CustomText,
  DefaultGroup,
  LatestColorWrap,
  LatestColorText,
  CustomGroupInnerWrap,
  MainColorGroup,
  Header,
} from './styled';
import { classnames } from '../../../utils';
import { ColorBlockItem, IColorResult } from './ColorBlockItem';
import SketchPicker from './sketch';
import { colorUtil } from './colorUtil';
import useClickOuterRef from '../../../hooks/useClickOuterRef';

export enum EColorPanelMode {
  normal = 'normal',
  art = 'art',
}

const decimalToHex = (alpha: number): string =>
  alpha === 0
    ? '00'
    : Math.round(255 * alpha)
        .toString(16)
        .padStart(2, '0');

export interface IColorPaletteProps {
  /**
   * 要选中的目标颜色
   */
  color: string;
  /**
   * 快捷设置（自动）的颜色
   */
  automaticColor: string;
  /**
   * 要显示清楚线的颜色块的颜色值
   */
  clearColor?: string;
  /**
   * 是否显示 colorPicker 组件, 即：选择“更多”颜色的组件
   */
  picker?: boolean;
  /**
   * 是否显示 最近使用 的颜色
   */
  showRecentColors?: boolean;
  /**
   * 选中颜色的回调
   */
  onPick: (color: string) => void;
  /**
   * 选中自定义颜色的回调，picker为true时生效
   */
  onCustomColorPick?: (color: string) => void;
  /**
   * 拖动放开后自定义颜色的回调，picker为true时生效
   */
  onCustomColorAfterPick?: (color: string) => void;
  /**
   * 本地语言类型，如zh-CN
   */
  locale?: string;
  /**
   * 快捷设置颜色的文案（如 "自动" "无颜色"）
   */
  quickSetText?: string | React.ReactNode;
  /**
   * 是否显示自动
   */
  showAutomatic?: boolean;
  /**
   * 色块hover出现的字
   */
  colorBlockText?: string;
  /**
   * 自定义色块hover出现的字的颜色
   */
  customColorBlockTextColor?: (color: string) => string;
  /**
   * 自定义颜色面板插槽
   */
  customColorPalette?: React.ReactElement;
  /**
   * 是否显示面板样式切换下拉框, 默认不显示
   */
  showToggleSelect?: boolean;
  /**
   * 面板样式下拉框响应函数函数
   */
  onSelectValueChange?: (mode: EColorPanelMode) => void;
  /**
   * 艺术色板是否显示渐变色, 默认不显示
   */
  showGradient?: boolean;
  /**
   * 艺术色板第几行拥有双倍下间距，有渐变色时默认 1， 其它默认 0
   */
  doubleGapRowIndex?: number;
  /**
   * 获取色板样式, 默认返回 normal
   */
  getColorPanelMode?: () => EColorPanelMode;
}

interface IColorPaletteState {
  // 选择的自定义面板的颜色
  customColor: string;
  // 是否显示自定义色彩面板
  showPicker: boolean;
  // 是否右侧显示自定义色彩面板
  isPickerAlignRight: boolean;
  // 最近使用自定义颜色列表
  recentColors: string[];
  // 颜色面板类型
  mode: EColorPanelMode;
}

interface IClickOuterRefProps {
  handler: any;
  on: boolean;
  box: HTMLElement;
  children: (ref: any) => React.ReactElement;
}

// 双倍间距
const doubleLineStyle = { paddingBottom: '10px' };

const CUSTOMER_CONFIG_KEY = 'color-picker-custom-config';

const MAX_LATEST_COLOR = 10;

const ClickOuterRef: React.FC<IClickOuterRefProps> = (props) => {
  const { handler, on, box, children } = props;
  const ref = useClickOuterRef(handler, on, 'mousedown', box);
  return children(ref);
};

export class ColorPalette extends PureComponent<
  IColorPaletteProps,
  IColorPaletteState
> {
  // eslint-disable-next-line react/static-property-placement
  static defaultProps = {
    color: 'black',
    onPick: (): void => {},
    canClear: false,
    automaticColor: '',
    latestColors: [],
    picker: true,
    showRecentColors: true,
    showAutomatic: true,
  };

  private readonly refPalette = React.createRef<HTMLDivElement>();
  private readonly moreButtonRef = React.createRef<HTMLDivElement>();

  private timer = 0;

  constructor(props: IColorPaletteProps) {
    super(props);
    const { getColorPanelMode } = this.props;

    this.state = {
      customColor: '',
      showPicker: false,
      isPickerAlignRight: true,
      recentColors: colorUtil.getRecentColorsFromLocalStorage(),
      mode:
        typeof getColorPanelMode === 'function'
          ? getColorPanelMode() || EColorPanelMode.normal
          : EColorPanelMode.normal,
    };
  }

  // 选中颜色之后，数值回调
  handlePick = (color: string): void => {
    const { onPick } = this.props;
    onPick(color);
  };

  handleCustomClick = (e: any) => {
    // 阻止 更多颜色面板的 点击事件冒泡
    e.preventDefault();
    e.stopPropagation();
  };

  // 自定义色彩面板的展示逻辑
  showHandleCustomPicker = (e: any): void => {
    const { picker } = this.props;
    if (!picker) {
      return;
    }
    if (this.timer) window.clearTimeout(this.timer);
    const { target } = e;
    // 确认按钮不阻止事件流
    if (!target.id || target.id !== CUSTOMER_CONFIG_KEY) {
      e.preventDefault();
      e.stopPropagation();
    }

    const { showPicker } = this.state;
    if (showPicker) return;
    this.changePickerAlignment();
    this.setState({
      showPicker: true,
    });
  };

  hideHandleCustomPicker = () => {
    this.timer = window.setTimeout(() => {
      const { customColor } = this.state;
      this.addRecentColor(customColor);
      this.setState({
        showPicker: false,
      });
    }, 200);
  };

  componentWillUnmount() {
    if (this.timer) window.clearTimeout(this.timer);
  }

  changePickerAlignment = (): void => {
    if (!this.refPalette.current) {
      return;
    }

    const { left, width } = this.refPalette.current.getBoundingClientRect();
    const right = window.innerWidth - left - width;

    // SketchPicker 的宽度为 200px
    if (right <= 210) {
      this.setState({
        isPickerAlignRight: false,
      });
    } else {
      this.setState({
        isPickerAlignRight: true,
      });
    }
  };

  getColorStringColorObj = (colorObj: IColorResult): string => {
    let result = colorObj.hex;

    // 这里可能是 "transparent"
    if (result.startsWith('#')) {
      if (colorObj.rgb && colorObj.rgb.a !== 1) {
        result = `${colorObj.hex}${decimalToHex(
          colorObj.rgb.a || 0,
        )}`.toLowerCase();
      }
    }

    return result;
  };

  // 自定义颜色板 选中颜色变化时的事件
  handleCustomPickerChange = (colorObj: IColorResult): void => {
    const { onCustomColorPick, onPick, onCustomColorAfterPick } = this.props;
    const color = this.getColorStringColorObj(colorObj);

    this.setState({
      customColor: color,
    });

    // 选中颜色变化时立即应用
    if (onCustomColorPick) {
      onCustomColorPick(color);
    } else if (!onCustomColorAfterPick) {
      onPick(color);
    }
  };
  // 自定义颜色板 选中颜色变化结束时的事件
  handleCustomPickerAfterChange = (colorObj: IColorResult): void => {
    const { onCustomColorAfterPick } = this.props;
    const color = this.getColorStringColorObj(colorObj);

    if (onCustomColorAfterPick) {
      onCustomColorAfterPick(color);
    }
  };

  // 确认自定义颜色的选择
  handleConfirmCustomColor = () => {
    const { customColor } = this.state;
    this.addRecentColor(customColor);
    this.setState({
      customColor: '',
      showPicker: false,
    });
  };

  addRecentColor = (color: string) => {
    if (!color) return;
    this.setState((state) => {
      let colors = [...state.recentColors];
      const findIdx = colors.indexOf(color);
      if (findIdx >= 0) {
        // 调整已有元素的位置
        const deleteColor = colors.splice(findIdx, 1);
        colors.unshift(...deleteColor);
      } else {
        colors.unshift(color);
      }
      colors = colors.slice(0, MAX_LATEST_COLOR);
      colorUtil.saveRecentColorsToLocalStorage(colors);
      return { recentColors: colors };
    });
  };

  handleSelectChange = (mode: EColorPanelMode) => {
    this.setState({ mode });

    const { onSelectValueChange } = this.props;
    if (typeof onSelectValueChange === 'function') {
      onSelectValueChange(mode);
    }
  };

  getColorData = () => {
    const { mode } = this.state;
    const { showGradient } = this.props;
    if (mode === 'art') {
      return showGradient ? colorDataArtWithGradient : colorDataArt;
    }
    return colorDataNormal;
  };

  renderCustomerColor = () => {
    const {
      clearColor,
      color,
      locale,
      showRecentColors,
      colorBlockText,
      customColorBlockTextColor,
    } = this.props;

    const {
      isPickerAlignRight,
      customColor,
      showPicker,
      recentColors: customerColors,
      mode,
    } = this.state;
    const colorCount = Math.min(customerColors.length, MAX_LATEST_COLOR);
    const limitedColors = customerColors.slice(0, colorCount);

    const LineGroup = mode === 'normal' ? LineGroupNormal : LineGroupArt;

    return (
      <>
        {!colorCount || !showRecentColors ? null : (
          <LatestColorWrap>
            <LatestColorText>
              <TranslateComp
                locale={locale}
                textKey={'we_toolbar_color_picker_latest'}
              />
            </LatestColorText>
            <LineGroup key={`latestColors-${limitedColors[0]}`}>
              {limitedColors.map((curColor, index) => (
                <ColorBlockItem
                  key={`${curColor}-${index}`}
                  colorValue={curColor}
                  clearColor={clearColor}
                  index={index}
                  active={color === curColor}
                  onColorPick={this.handlePick}
                  colorBlockText={colorBlockText}
                  setColorBlockTextColor={customColorBlockTextColor}
                  mode={mode}
                />
              ))}
            </LineGroup>
          </LatestColorWrap>
        )}
        <CustomGroup
          onMouseEnter={this.showHandleCustomPicker}
          onMouseLeave={this.hideHandleCustomPicker}
          ref={this.moreButtonRef}
        >
          <CustomGroupInnerWrap className={classnames({ actived: showPicker })}>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <SomecolorNormal />
              <CustomText>
                <TranslateComp
                  locale={locale}
                  textKey={'we_toolbar_color_picker_more'}
                />
              </CustomText>
              {showPicker && (
                // 点击 更多颜色 面板之外的区域关闭面板，并将颜色添加到 最近颜色 中
                <ClickOuterRef
                  handler={this.handleConfirmCustomColor}
                  on={showPicker}
                  box={this.moreButtonRef.current!}
                >
                  {(customPickerRef) => (
                    <CustomPicker
                      rightSide={isPickerAlignRight}
                      onClick={this.handleCustomClick}
                      ref={customPickerRef}
                    >
                      <SketchPicker
                        color={customColor}
                        onChange={this.handleCustomPickerChange}
                        onChangeComplete={this.handleCustomPickerAfterChange}
                      />
                    </CustomPicker>
                  )}
                </ClickOuterRef>
              )}
            </div>
            <ArrowRightNormal style={{ color: 'rgba(17, 31, 44, 0.4)' }} />
          </CustomGroupInnerWrap>
        </CustomGroup>
      </>
    );
  };

  render() {
    const {
      color,
      clearColor,
      automaticColor,
      picker,
      locale,
      quickSetText,
      showAutomatic,
      colorBlockText,
      customColorPalette,
      customColorBlockTextColor,
      showToggleSelect,
      doubleGapRowIndex = 0,
    } = this.props;
    const { mode } = this.state;

    const doubleGapRowIndexWithMode = mode === 'normal' ? 0 : doubleGapRowIndex;
    const LineGroup = mode === 'normal' ? LineGroupNormal : LineGroupArt;
    const colors = this.getColorData();

    return (
      <PaletteWrapper ref={this.refPalette}>
        <Header>
          {showAutomatic && (
            <DefaultGroup onClick={() => this.handlePick(automaticColor)}>
              <ColorBlockItem
                colorValue={automaticColor}
                clearColor={clearColor}
                index={1}
                active={color === automaticColor}
                onColorPick={this.handlePick}
                colorBlockText={colorBlockText}
                setColorBlockTextColor={customColorBlockTextColor}
              />
              <CustomText>
                {quickSetText || (
                  <TranslateComp
                    locale={locale}
                    textKey={'we_toolbar_color_picker_default'}
                  />
                )}
              </CustomText>
            </DefaultGroup>
          )}
          {showToggleSelect && (
            <Select
              defaultValue={mode}
              style={{ height: 20, fontSize: 12, lineHeight: 20 }}
              onChange={this.handleSelectChange}
              getContainer={() => this.refPalette.current || document.body}
              bordered
              testid="color_panel_mode_select"
            >
              <SelectOption
                value={EColorPanelMode.normal}
                testid="color_panel_normal_mode"
              >
                <TranslateComp
                  locale={locale}
                  textKey={'we_toolbar_color_normal_mode'}
                />
              </SelectOption>
              <SelectOption
                value={EColorPanelMode.art}
                testid="color_panel_art_mode"
              >
                <TranslateComp
                  locale={locale}
                  textKey={'we_toolbar_color_art_mode'}
                />
              </SelectOption>
            </Select>
          )}
        </Header>
        <MainColorGroup>
          {colors.map((data: string[], index: number) => {
            const groupProps = {
              key: `${index}-${data[0]}`,
              style:
                index === doubleGapRowIndexWithMode
                  ? doubleLineStyle
                  : undefined,
            };
            // 渲染一行色块
            return (
              <LineGroup {...groupProps}>
                {data.map((curColor, colIndex) => (
                  <ColorBlockItem
                    key={`${curColor}-${colIndex}`}
                    colorValue={curColor}
                    clearColor={clearColor}
                    index={colIndex}
                    active={color === curColor}
                    onColorPick={this.handlePick}
                    colorBlockText={colorBlockText}
                    setColorBlockTextColor={customColorBlockTextColor}
                    mode={mode}
                  />
                ))}
              </LineGroup>
            );
          })}
        </MainColorGroup>
        {picker ? this.renderCustomerColor() : null}
        {customColorPalette}
      </PaletteWrapper>
    );
  }
}
