import React, { useCallback, useState, useEffect, useMemo } from 'react';
import { IconMenuProps } from '../interface';
import {
  useGetContextLocale,
  useTranslate,
  DropdownAnimation,
} from '@ali/we-util';
import { ToolbarIconContext } from '@ali/we-icon';
import { Dropdown, ArrowDownLineNormal, Tooltip, theme } from '@ali/we-design';
import { ButtonWrap, TextWarp, Warp } from './styled';
import { TOOL_SIZE } from '../../utils/consts';
import useVisibilitySensor from '../../hooks/useVisibilitySensor';
import IconButton from '../iconButton';
import { DisableWrap } from '../styled';
import OnIdle, { OnIdlePlaceholder } from '../onIdle';

export const IconMenu: React.FC<IconMenuProps> = React.memo(
  React.forwardRef((props, ref: React.ForwardedRef<{}>) => {
    const {
      icon,
      title,
      tooltip,
      shortcutText,
      showTitle,
      disabled,
      size,
      overlay,
      onVisibleChange,
      visible,
      active,
      locale,
      titleWidth,
      testid,
      showArrow,
      autoClose,
      disabledTip,
      offsetY,
      overlayStyle,
      placement,
      style,
      offsetX,
      trigger,
      getContainer,
      animationPlacement,
      extraNode,
      role = '',
      zIndex,
    } = props;
    const contextLocale = useGetContextLocale();
    const t = useTranslate(contextLocale);

    const [showOverlay, setShowOverlay] = useState<boolean>(false);

    useEffect(() => setShowOverlay(!!visible), [visible]);

    const handleVisibleChange = useCallback(
      (e: boolean) => {
        setShowOverlay(e);
        onVisibleChange && onVisibleChange(e);
      },
      [onVisibleChange],
    );

    const hideOverlay = useCallback(() => {
      if (autoClose) {
        setShowOverlay(false);
        onVisibleChange && onVisibleChange(false);
      }
    }, [autoClose]);

    const {
      el,
      id,
      isHide,
      fineAdaptation,
      groupKey,
      isInOffScreenWrap,
      mode,
    } = useVisibilitySensor(ref, testid);

    const styleProps = {
      size,
      showTitle,
      active,
      locale: locale || contextLocale,
      showOverlay,
    };

    let tooltipForButton: string | React.ReactNode = '';
    if (typeof tooltip === 'string') {
      tooltipForButton = t(tooltip);
      if (shortcutText != null) {
        tooltipForButton += ` ${shortcutText}`;
      }
    } else {
      tooltipForButton = tooltip;
    }

    const ToolbarIconMemo = useMemo(() => ({ size: size || 'medium' }), [size]);

    const styleWrap = useMemo(
      () => ({
        ...style,
        ...(showOverlay ? { backgroundColor: theme.toolbarButtonClick } : {}),
      }),
      [style, showOverlay],
    );

    const titleNode = useMemo(() => {
      return <div>{title && typeof title !== 'string' ? title : t(title)}</div>;
    }, [t, title]);

    if (isHide) return null;
    return (
      <Tooltip
        disabled={!disabled}
        title={disabledTip}
        getContainer={getContainer}
      >
        <DisableWrap
          disabled={disabled}
          className='icon-menu-layout menu-wrap'
          data-testid={(testid || '') + '_wrap'}
        >
          <Dropdown
            getContainer={getContainer}
            trigger={trigger}
            role={role}
            zIndex={zIndex}
            overlay={
              <OnIdle placeholder={<OnIdlePlaceholder />}>
                <DropdownAnimation
                  visible={disabled ? false : showOverlay}
                  onClick={hideOverlay}
                  placement={animationPlacement}
                >
                  {overlay}
                </DropdownAnimation>
              </OnIdle>
            }
            visible={disabled ? false : showOverlay}
            onVisibleChange={handleVisibleChange}
            offsetY={
              offsetY || (size === TOOL_SIZE.LARGE && showTitle ? -25 : 0)
            }
            testid={isInOffScreenWrap ? '' : testid}
            overlayStyle={overlayStyle}
            placement={placement}
            offsetX={offsetX}
          >
            {showArrow ? (
              <Warp
                ref={el}
                data-id={id}
                data-mode={mode}
                data-testid={isInOffScreenWrap ? '' : testid}
                key={testid}
                data-biz-key={fineAdaptation ? id : null}
                data-group-id={groupKey}
                {...styleProps}
                locale={locale || contextLocale}
              >
                {showTitle && size === TOOL_SIZE.LARGE && (
                  <TextWarp {...styleProps}>
                    <div>{titleNode}</div>
                  </TextWarp>
                )}
                <Tooltip title={tooltipForButton} disabled={showOverlay}>
                  <ButtonWrap
                    {...styleProps}
                    style={styleWrap}
                    className='button-box'
                  >
                    <div className='left-wrap'>
                      <ToolbarIconContext.Provider value={ToolbarIconMemo}>
                        <div>{icon}</div>
                      </ToolbarIconContext.Provider>
                      {showTitle && size !== TOOL_SIZE.LARGE && (
                        <div
                          className='left-wrap-text'
                          style={{ width: titleWidth }}
                        >
                          {titleNode}
                        </div>
                      )}
                    </div>
                    <div className='right-wrap'>
                      <ArrowDownLineNormal />
                    </div>
                  </ButtonWrap>
                </Tooltip>
                {extraNode}
              </Warp>
            ) : (
              <IconButton
                {...props}
                extraNode={extraNode}
                style={styleWrap}
                testid={isInOffScreenWrap ? '' : testid}
                tooltip={showOverlay ? null : tooltipForButton}
              />
            )}
          </Dropdown>
        </DisableWrap>
      </Tooltip>
    );
  }),
);

IconMenu.defaultProps = {
  size: 'medium',
  showArrow: true,
  autoClose: true,
};

export default IconMenu;
