import React, { useMemo, useRef } from 'react';
import { CardPlaceholderH5Props } from './interface';
import { CardPlaceholderContainer } from './styled';
import { CardContainer } from '../container';
import { uuid } from '@ali/we-design';

const delay = 200;

export const PlaceholderH5: React.FC<CardPlaceholderH5Props> = (props) => {
  const {
    height = 65,
    title,
    icon,
    desc,
    type = 'export',
    isSelected = false,
    clickable = true,
    thresholdWidth,
    onMouseDown,
  } = props;

  const id = useMemo(() => uuid(), []);
  const cardPlaceholderContainerRef = useRef(null);

  return type === 'normal' ? (
    <CardPlaceholderContainer
      delay={delay}
      height={height}
      isSelected={isSelected}
      clickable={clickable}
      onMouseDown={onMouseDown}
      ref={cardPlaceholderContainerRef}
      data-id={`card-placeholder-container-${id}`}
      type={type}
    >
      <CardContainer className="card-container" thresholdWidth={thresholdWidth}>
        {icon && <div className="placeholder-icon-box">{icon}</div>}
        <div className="placeholder-right">
          <div className="placeholder-top">
            <div className="placeholder-title" style={!desc ? { lineHeight: '38px' } : {}}>
              {title}
            </div>
          </div>
          {desc && (
            <div className="placeholder-desc">
              <div className="desc-text">{desc}</div>
            </div>
          )}
        </div>
      </CardContainer>
    </CardPlaceholderContainer>
  ) : (
    <CardPlaceholderContainer
      delay={delay}
      height={height}
      clickable={clickable}
      onMouseDown={onMouseDown}
      data-id={`card-placeholder-container-${id}`}
      type={type}
    >
      <CardContainer className="card-container">
        {icon && <div className="placeholder-icon-box">{icon}</div>}
        <div className="placeholder-right">
          <div className="placeholder-top">
            <div className="placeholder-title">{title}</div>
          </div>
        </div>
      </CardContainer>
    </CardPlaceholderContainer>
  );
};
