/* eslint-disable consistent-return */
/* eslint-disable @typescript-eslint/no-explicit-any */
import { v4 as uuid } from 'uuid';
import fastdom from 'fastdom';

const woff =
  'data:font/woff;charset=utf-8;base64,';

/**
 * 20200126 引入 uuid，保证多实例 hook，避免多个hook同时对dom操作造成的判断失效
 * 保证每次调用能拿到同一个值
 */
class FontDetector {
  src: string;

  css: string;

  timer: number;

  private readonly uuid: string;

  private readonly styleUuid: string;

  private readonly iframeUuid: string;

  private detection: boolean[];

  constructor() {
    this.detection = [];
    const uid = uuid();
    this.uuid = `font-${uid}`;
    this.styleUuid = `style-${uid}`;
    this.iframeUuid = `iframe-${uid}`;
    this.css = `
    @font-face {
      font-family: font-detect-0-woff;
      src: url(${woff}) format('woff');
    }
    #${this.uuid} {
      display: inline-block;
      position: fixed;
      left: -1000px;
      top: -1000px;
      font-family: font-detect-0-woff;
    }
    #${this.uuid} .fontspan {
      display: inline-block;
      position: fixed;
      left: -1000px;
      top: -1000px;
    }
    `;
    this.src = '';
    this.inject();
    this.timer = 0;
  }

  inject = (): void => {
    let iframe = document.getElementById(this.iframeUuid);
    if (!iframe) {
      iframe = document.createElement('iframe');
      iframe.id = this.iframeUuid;
      iframe.style.cssText = `
        position: fixed;
        left: -1000px;
        top: -1000px;
        width: 0;
        height: 0;
        border: none;
        margin: 0;
        padding: 0;
        overflow: hidden;
        z-index: -1;
      `;
      document.body.appendChild(iframe);

      const iframeDoc = (iframe as HTMLIFrameElement).contentDocument!;

      const sheet = iframeDoc.createElement('style');
      sheet.setAttribute('type', 'text/css');
      sheet.id = this.styleUuid;
      sheet.innerHTML = this.css;
      const head = iframeDoc.getElementsByTagName('head')[0];
      head.appendChild(sheet);

      const elem = iframeDoc.createElement('div');
      elem.id = this.uuid;
      elem.innerHTML = '0';
      iframeDoc.body.appendChild(elem);
    }
  };

  remove = (): any => {
    const elem = document.getElementById(this.iframeUuid);
    if (elem != null) {
      elem.remove();
    }
  };

  /**
   * 增加500ms的delay避免hooks快速触发导致的回掉失效
   * @param callback
   */
  ready = (callback: any): any => {
    this.timer = window.setTimeout(() => {
      let iframe = document.getElementById(this.iframeUuid);
      if (!iframe) {
        this.inject();
        iframe = document.getElementById(this.iframeUuid);
      }
      // warning: 这里的判断逻辑过于 hack，无法找到ref来自哪里
      if (iframe?.style && iframe.clientWidth === 0) {
        return callback();
      }
    }, 0);
  };

  destroy() {
    if (this.timer) {
      window.clearTimeout(this.timer);
      this.remove();
    }
  }

  getResult = (fonts: string[], callback: any): any => {
    const iframe = document.getElementById(this.iframeUuid);
    const iframeDoc = (iframe as HTMLIFrameElement).contentDocument!;
    const elem = iframeDoc.getElementById(this.uuid) as HTMLElement;
    const fragment = iframeDoc.createDocumentFragment();
    // eslint-disable-next-line no-restricted-syntax
    for (const font of fonts) {
      const el = iframeDoc.createElement('span');
      el.className = 'fontspan';
      el.innerHTML = '0';
      el.style.fontFamily = `'${font}', font-detect-0-woff`;
      fragment.appendChild(el);
    }
    elem.appendChild(fragment);
    // read dom on next cycle
    requestAnimationFrame(() => {
      // 防止当前 ev 有其他 requestAnimationFrame 内修改 dom
      fastdom.measure(() => {
        const results = Array.from(elem.children).map(
          (el) => el.clientWidth > 0,
        );
        this.detection = results;
        callback(results);
        this.remove();
      });
    });
  };

  detect = (fonts: string[], callback: any): any =>
    this.detection.length > 0
      ? callback(this.detection)
      : this.ready(() => this.getResult(fonts, callback));
}

let shared: FontDetector | undefined = undefined;
export const getSharedFontDetector = () => {
  if (!shared) {
    shared = new FontDetector();
  }
  return shared;
};
