import styled, { css } from 'styled-components';
import { withTheme } from '@ali/we-design';
import { SplitButtonProps } from '../interface';
import { TOOL_SIZE } from '../../utils/consts';

export const TitleWrap: any = withTheme(styled.div<SplitButtonProps>`
  font-size: ${({ theme, size }) =>
    size === TOOL_SIZE.LARGE ? theme.ui.fontSizeSmall : theme.ui.fontSizeMenu};
  color: ${({ theme }) => theme.ui.commonActionIconColor};
  font-weight: normal;
  user-select: none;
  cursor: default;
  margin-left: ${({ theme, size }) =>
    size === TOOL_SIZE.LARGE ? '' : theme.ui.marginMini};
  align-items: center;
  display: flex;
  flex: 1;
  text-align: center;
  max-width: 52px;
  min-width: 45px;
  justify-content: center;
  div {
    overflow: hidden;
    text-overflow: ellipsis;
    padding-top: 2px;
    -webkit-line-clamp: 2;
    word-break: break-word;
    line-height: 12px;
  }
  ${({ disabled }) => disabled && 'opacity:.35;cursor:not-allowed;'};
`);

export const LeftWrapper = withTheme(styled.div<SplitButtonProps>`
  display: flex;
  user-select: none;
  padding: ${({ theme, size }) =>
    `${
      size === TOOL_SIZE.MEDIUM ? theme.ui.paddingTiny : theme.ui.paddingMini
    } ${theme.ui.paddingMini}`};

  font-weight: normal;
  flex-direction: ${({ size }) =>
    size === TOOL_SIZE.LARGE ? 'column' : 'row'};
  .split_btn_left_toolbar {
    margin-left: ${({ theme }) => theme.ui.marginMini};
    color: ${({ theme }) => theme.ui.commonActionIconColor};
    font-size: ${({ theme, size }) =>
      size === TOOL_SIZE.LARGE ? theme.ui.fontSizeSmall : '13px'};
    ${({ disabled }) =>
      disabled &&
      css`
        opacity: 0.75;
      `}
  }
`);

const largeAndShowTitleWrap = css<any>`
  max-width: 52px;
  min-width: 45px;
  :hover .split_btn_toolbar {
    background-color: ${({ theme, disabled, activeLeft }) =>
      disabled && !activeLeft
        ? theme.ui.toolbarButtonTransparent
        : theme.ui.toolbarButtonHover};
  }
  :hover .split_btn_toolbar .splitbutton-left {
    border-right: 1px solid
      ${({ theme, disabled, activeLeft }) =>
        disabled && !activeLeft
          ? theme.ui.toolbarButtonTransparent
          : theme.ui.toolbarButtonHover};
  }
  ${({ activeLeft, theme }) =>
    activeLeft &&
    css`
      background-color: ${theme.ui.toolbarButtonTransparent};
      .split_btn_toolbar {
        background-color: ${theme.ui.toolbarButtonHover};
      }
    `};
`;

export const BottomWrapper = withTheme(styled.div<SplitButtonProps>`
  text-align: center;
  display: flex;
  align-items: flex-end;
  height: 24px;
  line-height: 11px;
  font-size: ${({ theme }) => theme.ui.fontSizeMenu};
  color: ${({ theme }) => theme.ui.toolbarButtonIcon};
  justify-content: center;
  ${({ theme, disabled, activeLeft }) =>
    disabled
      ? css`
          opacity: 0.35;
        `
      : css`
          :hover ~ .split_btn_toolbar {
            background-color: ${theme.ui.toolbarButtonHover};
          }
          :hover ~ .split_btn_toolbar .splitbutton-left {
            border-right: 1px solid
              ${({ theme }) => theme.ui.toolbarButtonHover};
          }
          :active ~ .split_btn_toolbar .splitbutton-left {
            background-color: ${disabled
              ? activeLeft
                ? theme.ui.toolbarButtonSelect
                : theme.ui.toolbarButtonTransparent
              : theme.ui.toolbarButtonClick};
            border-right: 1px solid transparent;
          }
        `}
`);

export const ButtonWrap = withTheme(styled.div<SplitButtonProps>`
  position: relative;
  user-select: none;
  flex-direction: column-reverse;
  display: flex;
  cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'default')};
  border-radius: ${({ theme }) => theme.ui.radius};
  ${({ size }) =>
    size === TOOL_SIZE.LARGE &&
    css`
      height: 52px;
    `}
  .splitbutton-left {
    margin: 0;
    padding: 0;
    height: auto;
    border-right: 1px solid transparent;
    ${({ activeLeft, theme }) =>
      activeLeft &&
      css`
        background-color: ${theme.ui.toolbarButtonSelect};
        border-right: 1px solid ${({ theme }) => theme.ui.toolbarButton4};
      `}
  }
  .splitbutton-left:hover {
    padding-right: 0;
    border-right: 1px solid
      ${({ theme, disabled, activeLeft }) =>
        disabled && !activeLeft
          ? theme.ui.toolbarButtonTransparent
          : theme.ui.toolbarButtonHover};
  }
  .splitbutton-left:active {
    background-color: ${({ theme, disabled, activeLeft }) =>
      disabled
        ? activeLeft
          ? theme.ui.toolbarButtonSelect
          : theme.ui.toolbarButtonTransparent
        : theme.ui.toolbarButtonClick};
    ${({ disabled }) =>
      !disabled &&
      css`
        border-right: 1px solid transparent;
      `}
  }
  .splitbutton-right {
    padding: 0;
    width: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .splitbutton-right:hover {
    background-color: ${({ theme, disabled, active }) =>
      disabled
        ? theme.ui.toolbarButtonTransparent
        : active
        ? theme.ui.toolbarButtonClick
        : theme.ui.toolbarButtonHover};
  }
  .splitbutton-right:active {
    background-color: ${({ theme, disabled }) =>
      disabled
        ? theme.ui.toolbarButtonTransparent
        : theme.ui.toolbarButtonClick};
  }
  .split_btn_toolbar .splitbutton-right .weicon {
    transform: rotate(0deg) scale(1);
    font-size: 8px;
    color: ${({ theme }) => theme.ui.toolbarButtonIcon56};
    opacity: ${({ disabled }) => disabled && 0.35};
  }
  .split_btn_toolbar .splitbutton-right .weicon.opened {
    transform: rotate(180deg) scale(1);
    font-size: 8px;
    color: ${({ theme }) => theme.ui.toolbarButtonIcon56};
    opacity: ${({ disabled }) => disabled && 0.35};
  }
  .split_btn_toolbar {
    height: auto;
    display: flex;
    cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'default')};
    margin: auto;
  }
  .split_btn_toolbar:hover {
    background-color: ${({ theme, disabled, activeLeft }) =>
      disabled
        ? activeLeft
          ? theme.ui.toolbarButtonHover
          : theme.ui.toolbarButtonTransparent
        : theme.ui.toolbarButtonHover};
  }
  .weicon {
    font-size: ${({ theme, size }) =>
      size === TOOL_SIZE.LARGE
        ? theme.ui.toolbarFontSizeIconLarge
        : theme.ui.toolbarFontSizeIcon};
  }
  ${({ activeLeft, theme }) =>
    activeLeft &&
    css`
      .split_btn_toolbar {
        background-color: ${theme.ui.toolbarButtonHover};
      }
    `}
  ${({ size, showTitle }) =>
    size === TOOL_SIZE.LARGE && showTitle && largeAndShowTitleWrap};
`);
