import React, { useCallback } from 'react';
import {
  FooterReduceNormal,
  FooterEnlargeNormal,
  Tooltip,
  Select,
  SelectOption,
  SelectOptionGroup,
  IPlacement,
  useControlledValue,
  KEY_CODES,
  classnames,
} from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import {
  RatioSelectWrap,
  RatioSelectInner,
  Button,
  SelectWrap,
  TriggerSpanWrap,
} from './styled';

export interface IRatioSelectProps {
  /**
   * 国际化语言
   */
  locale?: string;
  /**
   * 可见性
   */
  visible?: boolean;
  /**
   * 默认的可见性
   */
  defaultVisible?: boolean;
  /**
   * 可见性变化回调
   */
  onVisibleChange?: (e: boolean) => void;
  /**
   * 变化的差值
   */
  step?: number;
  /**
   * 选中值
   */
  value?: number;
  /**
   * 默认的选中值
   */
  defaultValue?: number;
  /**
   * 值变化
   */
  onChange?: (e: number) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 出现的位置
   */
  placement?: IPlacement;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 自定义值
   */
  customedList?: Array<{ text: string; value: number }>;
  /**
   * 自定义 ratios
   */
  customRatios?: number[];
  animationPlacement?:
    | 'top'
    | 'left'
    | 'right'
    | 'bottom'
    | 'topLeft'
    | 'topRight'
    | 'bottomLeft'
    | 'bottomRight'
    | 'leftTop'
    | 'leftBottom'
    | 'rightTop'
    | 'rightBottom';
  /**
   * 选项的tooltip提示中的快捷键设置
   */
   zoomInShortcutText?: string;
   zoomOutShortcutText?: string;
}

const RATIOS = [0.1, 0.25, 0.5, 0.75, 1, 1.25, 1.5, 1.75, 2];

const triggerStyle: React.CSSProperties = {
  width: 38,
  textAlign: 'center',
  borderRadius: 0,
  fontSize: 13,
  height: '100%',
};

// eslint-disable-next-line max-lines-per-function
export const RatioSelect: React.FC<IRatioSelectProps> = React.memo((props) => {
  const {
    locale,
    onVisibleChange,
    onChange,
    step,
    zIndex,
    placement,
    disabled,
    customedList,
    customRatios,
    animationPlacement,
    zoomInShortcutText,
    zoomOutShortcutText,
  } = props;

  // 国际化文案
  const t = useTranslate(locale);
  const placeholderTitle = t('we_toolbar_ratio_select_placeholder');
  const zoomInTitle = t('we_toolbar_ratio_select_zoom_in');
  const zoomOutTitle = t('we_toolbar_ratio_select_zoom_out');
  const addText = t('we_toolbar_ratio_select_add');
  const reduceText = t('we_toolbar_ratio_select_reduce');

  // visible 受控
  const [visible, setVisible] = useControlledValue(
    props.visible,
    props.defaultVisible,
  );

  // value 受控
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);

  const ratios = customRatios || RATIOS;

  /**
   * 可见性变化
   */
  const handleVisibleChange = useCallback(
    (e: boolean): void => {
      if (onVisibleChange) {
        onVisibleChange(e);
      }
      setVisible(e);
    },
    [setVisible, onVisibleChange],
  );

  /**
   * 值发生变化
   */
  const handleValueChange = useCallback(
    (e: number): void => {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
    },
    [onChange, setValue],
  );

  /**
   * 减少比例
   */
  const handleReduce = useCallback((): void => {
    if (disabled) return;
    // 浮点数精度问题消除
    const newValue = Math.round(value! * 100) - Math.round(step! * 100);
    if (newValue >= ratios[0] * 100) {
      handleValueChange(newValue / 100);
    } else {
      handleValueChange(ratios[0]);
    }
  }, [value, handleValueChange, step]);

  /**
   * 增加比例
   */
  const handleAdd = useCallback((): void => {
    if (disabled) return;
    // 浮点数精度问题消除
    const newValue = Math.round(value! * 100) + Math.round(step! * 100);
    if (newValue <= ratios[ratios.length - 1] * 100) {
      handleValueChange(newValue / 100);
    } else {
      handleValueChange(ratios[ratios.length - 1]);
    }
  }, [value, handleValueChange, step]);

  /**
   * 如果是focus到减少按钮并回车
   */
  const handleMinusKeyDown = useCallback(
    (e: React.KeyboardEvent<HTMLElement>) => {
      if (e.which === KEY_CODES.ENTER) {
        handleReduce();
      }
    },
    [handleReduce],
  );

  /**
   * 如果是focus到增加按钮并回车
   */
  const handleAddKeyDown = useCallback(
    (e: React.KeyboardEvent<HTMLElement>) => {
      if (e.which === KEY_CODES.ENTER) {
        handleAdd();
      }
    },
    [handleAdd],
  );

  /**
   * 不是根据select来渲染
   */
  const renderRatio = useCallback(
    (e: number | undefined) => (
      <TriggerSpanWrap>{Math.round(e! * 100)}%</TriggerSpanWrap>
    ),
    [],
  );

  return (
    <RatioSelectWrap>
      <RatioSelectInner>
        <Tooltip title={zoomOutTitle + `${zoomOutShortcutText ? ` ${zoomOutShortcutText}` : ''}`}>
          <Button
            tabIndex={0}
            onKeyDown={handleMinusKeyDown}
            onClick={handleReduce}
            className={classnames({
              disabled: value === ratios[0] || disabled,
            })}
            data-testid="RatioSelect_minus"
            data-role="footerZoomOut"
          >
            <FooterReduceNormal />
          </Button>
        </Tooltip>
        <SelectWrap>
          <Select
            size="small"
            value={value}
            visible={visible}
            onChange={handleValueChange}
            renderTrigger={renderRatio}
            onVisibleChange={handleVisibleChange}
            zIndex={zIndex}
            style={triggerStyle}
            showArrow={false}
            placement={placement!}
            title={placeholderTitle}
            disabled={disabled}
            testid="RatioSelect"
            animationPlacement={animationPlacement}
          >
            <SelectOptionGroup>
              {ratios.map((ratio) => {
                return (
                  <SelectOption
                    key={`${ratio}`}
                    value={ratio}
                    testid={`RatioSelect_${ratio}`}
                    role={`footerRatioSelectOption${ratio}`}
                  >
                    {ratio * 100}%
                  </SelectOption>
                );
              })}
            </SelectOptionGroup>
            {customedList ? (
              <SelectOptionGroup>
                {customedList.map((item) => {
                  return (
                    <SelectOption
                      key={`${item.text}`}
                      value={item.value}
                      testid={`RatioSelect_${item.text}`}
                      role={`footerRatioSelectOption${item.text}`}
                    >
                      {item.text}
                    </SelectOption>
                  );
                })}
              </SelectOptionGroup>
            ) : (
              <></>
            )}
            <SelectOption
              disabled={value! >= ratios[ratios.length - 1]}
              onSelect={handleAdd}
              testid="RatioSelect_add"
            >
              {addText}
              {step! * 100}%
            </SelectOption>
            <SelectOption
              onSelect={handleReduce}
              // @ts-ignore
              disabled={value <= ratios[0]}
              testid="RatioSelect_reduce"
            >
              {reduceText}
              {step! * 100}%
            </SelectOption>
          </Select>
        </SelectWrap>
        <Tooltip title={zoomInTitle + `${zoomInShortcutText ? ` ${zoomInShortcutText}` : ''}`}>
          <Button
            tabIndex={0}
            onKeyDown={handleAddKeyDown}
            onClick={handleAdd}
            className={classnames({
              disabled: value === ratios[ratios.length - 1] || disabled,
            })}
            data-testid="RatioSelect_plus"
            data-role="footerZoomIn"
          >
            <FooterEnlargeNormal />
          </Button>
        </Tooltip>
      </RatioSelectInner>
    </RatioSelectWrap>
  );
});

RatioSelect.defaultProps = {
  defaultVisible: false,
  step: 0.05,
  defaultValue: 1,
  placement: 'top',
  disabled: false,
};
