import ReactDOM from 'react-dom';

export function getOffset(el: HTMLElement): { left: number; top: number } {
  const left = el.offsetLeft;
  const top = el.offsetTop;
  if (!el.offsetParent) return { left, top };

  const parent = el.offsetParent.tagName.toLocaleLowerCase();
  if (parent === 'body') return { left, top };

  const parentPos = getOffset(el.offsetParent as HTMLElement);
  return {
    left: left + parentPos.left,
    top: top + parentPos.top,
  };
}

export function elementContain(box: HTMLElement | null, el: HTMLElement | null): boolean {
  if (!box) return false;
  let target: HTMLElement | null = el;
  while (target) {
    if (target === box) return true;
    target = target.parentElement;
  }
  return false;
}

const mobileReg = /(android|iphone|symbianos|windows phone|ipad|ipod)/i;
export const isMobile = mobileReg.test(navigator.userAgent.toLocaleLowerCase());

const iosReg = /os ([\\.\\_\d]+) like mac os/i;
export const isIOS = iosReg.test(navigator.userAgent.toLocaleLowerCase());


/**
 * Return if a node is a DOM node. Else will return by `findDOMNode`
 */
export function findDOMNode<T = Element | Text>(
  node: React.ReactInstance | HTMLElement,
): T {
  if (node instanceof HTMLElement) {
    return (node as unknown) as T;
  }
  // eslint-disable-next-line react/no-find-dom-node
  return (ReactDOM.findDOMNode(node) as unknown) as T;
}
