import React, { useState, useCallback } from 'react';
import { InlinePlaceholderProps } from './interface';
import { InlinePlaceholderContainer } from './styled';
import { Dropdown } from '@ali/we-design';
import { DropdownAnimation } from '@ali/we-util';

export const InlinePlaceholder: React.FC<InlinePlaceholderProps> = (props) => {
  const {
    style,
    className,
    icon,
    children,
    // 保留原先的默认入参，防止其他业务方出现问题
    type = 'dropdown',
    visible,
    offsetX,
    offsetY = 4,
    onVisibleChange: onVisibleChangeFromProps,
    overlay = <></>,
    overlayStyle,
    overlayClassName,
    isSelected = false,
    onClick,
    getContainer,
    testid,
    disabled,
    role,
  } = props;

  const [click, setClick] = useState('');
  const [active, setActive] = useState(false);

  const onVisibleChange = (v: boolean) => {
    setActive(v);
    onVisibleChangeFromProps && onVisibleChangeFromProps(v);
  };

  const onMouseDown = useCallback(() => {
    setClick(' click');
  }, [setClick]);

  const onMouseUp = useCallback(() => {
    setClick('');
  }, [setClick]);

  switch (type) {
    case 'normal':
      return (
        <InlinePlaceholderContainer
          style={style}
          className={`${className || ''} ${click} ${
            active || visible ? ' active' : ''
          } ${isSelected ? 'select' : ''}`}
          onMouseDown={disabled ? undefined : onMouseDown}
          onMouseUp={disabled ? undefined : onMouseUp}
          onClick={disabled ? undefined : onClick}
          data-testid={testid}
          data-role={role}
          disabled={disabled}
        >
          <span className="inline-placeholder-icon-box">{icon}</span>
          <span>{children}</span>
        </InlinePlaceholderContainer>
      );
    case 'export':
      return (
        <InlinePlaceholderContainer
          style={style}
          className={`${className} active`}
          onClick={disabled ? undefined : onClick}
          data-testid={testid}
          disabled={disabled}
        >
          <span className="inline-placeholder-icon-box">{icon}</span>
          <span>{children}</span>
        </InlinePlaceholderContainer>
      );
    case 'dropdown':
    default:
      return (
        <Dropdown
          getContainer={getContainer}
          overlay={
            <DropdownAnimation
              overlayStyle={{
                border: 'none',
                borderRadius: '8px',
                boxShadow:
                  '0px 0px 1px rgba(0, 0, 0, 0.24), 0px 8px 24px rgba(0, 0, 0, 0.16)',
                ...overlayStyle,
              }}
              visible={active}
            >
              {overlay}
            </DropdownAnimation>
          }
          overlayClassName={overlayClassName}
          overlayStyle={{
            border: 'none',
            boxShadow: 'none',
            background: 'none',
          }}
          onVisibleChange={onVisibleChange}
          offsetX={offsetX}
          offsetY={offsetY}
          visible={!disabled && visible}
          disableDomAlign
        >
          <InlinePlaceholderContainer
            style={style}
            className={`${className || ''} ${click} ${
              active || visible ? ' active' : ''
            } ${isSelected ? 'select' : ''}`}
            onMouseDown={disabled ? undefined : onMouseDown}
            onMouseUp={disabled ? undefined : onMouseUp}
            onClick={disabled ? undefined : onClick}
            data-testid={testid}
            disabled={disabled}
          >
            <span className="inline-placeholder-icon-box">{icon}</span>
            <span>{children}</span>
          </InlinePlaceholderContainer>
        </Dropdown>
      );
  }
};
