import React from 'react';
import { ToolbarFontColorNormal } from '@ali/we-icon';
import { colorUtil } from './colorUtil';

interface IFontIcon {
  color: string;
  pathColors?: string[];
}

const FontColorIcon: React.FC<IFontIcon> = (props) => {
  const { color, pathColors } = props;

  return colorUtil.isGradientColor(color) ? (
    <GradientColorFontIcon color={color} />
  ) : (
    <ToolbarFontColorNormal pathColors={pathColors} />
  );
};

const GradientColorFontIcon: React.FC<IFontIcon> = (props) => {
  const { color } = props;
  const [startColor, endColor] = colorUtil.transferGradientColor(color);
  return (
    <svg
      width="20px"
      height="20px"
      fill="currentColor"
      aria-hidden="true"
      focusable="false"
      viewBox="0 0 20 20"
    >
      <path
        d="M19 20H1V17H19V20Z"
        fill={`url(#paint0_linear_${startColor}:${endColor})`}
      ></path>
      <path
        d="M7.53848 11L5.93848 15H4.86145L9.53573 3.3143H10.4642L15.1385 15H14.0615L12.4615 11H7.53848ZM7.93848 10H12.0615L9.99997 4.84629L7.93848 10Z"
        fill="inherit"
      />
      <defs>
        <linearGradient
          id={`paint0_linear_${startColor}:${endColor}`}
          x1="4"
          y1="9.5"
          x2="16"
          y2="9.5"
          gradientUnits="userSpaceOnUse"
        >
          <stop stopColor={startColor} />
          <stop offset="1" stopColor={endColor} />
        </linearGradient>
      </defs>
    </svg>
  );
};

export default FontColorIcon;
