/* eslint-disable max-lines-per-function */
import React, { useCallback, useState, useEffect } from 'react';
import useControlledValue from '../../../hooks/useControlledValue';
import { SelectOption, TOptionTitle } from '@ali/we-design';
import TextSelect from '../../../common/textSelect';
import { OptionWrap, TriggerWrap } from './styled';
import { fontFamily, useFontFamily } from './hooks';
import { TextSelectPropsOmit } from '../../../common/interface';
import { useGetContextLocale } from '@ali/we-util';
import { DownloadButton } from './DownloadButton';
import { detectFonts } from './detectFont';

export interface DownloadFontInfo {
  status: 'unDownload' | 'downloading' | 'downloaded';
}

export interface IFontFamilySelectProps extends TextSelectPropsOmit<string> {
  defaultValue?: string;
  value?: string;
  locale?: string;
  onChange?: (e: string) => void;
  disabled?: boolean;
  /**
   * 选择器下方标题
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  handleDownload?: (font: string) => void;
  downloadFontInfos?: Record<string, DownloadFontInfo>;
}

export const FontFamilySelect: React.FC<IFontFamilySelectProps> = React.memo(
  (props) => {
    const {
      locale,
      onChange,
      disabled,
      titles,
      title,
      size,
      active,
      disabledKeys = [],
      width,
      handleDownload,
      downloadFontInfos = {},
      role,
      ...restProps
    } = props;
    const [value, setValue] = useControlledValue(
      props.value,
      props.defaultValue
    );
    const contextLocale = useGetContextLocale();
    const [options, t] = useFontFamily(
      locale || contextLocale
    );
    const [installedRecord, setInstalledRecord] = useState<
      Record<string, boolean>
    >({ default: true });
    // font detected
    const [fontDetected, setFontDetected] = useState(false);

    // 该组件的trigger区域只显示Icon
    const renderTrigger = useCallback(
      (align?: string) => {
        const index = options.findIndex(
          (option: fontFamily) => option.value === align
        );
        const currentOption = options[index];
        const useDefault = props.defaultValue ? options[0] : undefined;
        return (
          <TriggerWrap>
            {currentOption?.title || useDefault?.title || align}
          </TriggerWrap>
        );
      },
      [options]
    );

    const isFontDisable = useCallback(
      (font) => {
        return (
          (!installedRecord[font] &&
            downloadFontInfos[font]?.status !== 'downloaded') ||
          disabledKeys.indexOf(font) > -1
        );
      },
      [installedRecord, downloadFontInfos, disabledKeys]
    );

    const handleChange = useCallback(
      (e: string) => {
        if (onChange) {
          onChange(e);
        }
        setValue(e);
      },
      [onChange, setValue]
    );

    // 首次打开面板/value 不为 default 时，再做 font detect
    const detectFont = useCallback(() => {
      if (fontDetected) { 
        return;
      }
      detectFonts().then((results) => {
        const newInstalledRecord: Record<string, boolean> = {};
        results.forEach((result: boolean, index: number) => {
          if (index !== 0) {
            newInstalledRecord[options[index].value] = result;
          }
        });
        newInstalledRecord.default = true;
        setInstalledRecord(newInstalledRecord);
        setFontDetected(true);
      });
      return () => {
        setInstalledRecord({ default: true });
      };
    }, [fontDetected]);

    useEffect(() => {
      // 字体懒加载会导致字体列表延迟 1-2s 出现(大文档场景)
      if (value && value !== 'default') {
        detectFont();
      }
    }, [value]);

    return (
      <TextSelect
        width={width}
        value={value}
        size={size}
        onChange={handleChange}
        renderTrigger={renderTrigger}
        optionWrapperStyle={{ minWidth: 200 }}
        disabled={disabled}
        testid='FontFamilySelect'
        title={title}
        locale={locale}
        active={active}
        role={role}
        onVisibleChange={(visible) => {
          if (visible) {
            detectFont();
          }
        }}
        {...restProps}
      >
        {options
          // 是否展示在列表：用户已安装、可下载的字体
          .filter((option: fontFamily) => {
            return installedRecord[option.value] || option.installable;
          })
          .map((option: fontFamily) => (
            <SelectOption
              value={option.value}
              key={option.value}
              disabled={isFontDisable(option.value)}
              disabledTip={t('we_toolbar_font_family_UninstallFonts')}
              testid={option.value}
              title={titles![option.value]}
            >
              <OptionWrap disabled={isFontDisable(option.value)}>
                <span style={{ fontFamily: option.value, color: 'inherit' }}>
                  {option.title}
                </span>
              </OptionWrap>
              {
                // 具有下载能力且尚未安装
                handleDownload && !installedRecord[option.value] && (
                  <DownloadButton
                    handleDownload={handleDownload}
                    fontFamily={option.value}
                    status={downloadFontInfos[option.value]?.status}
                  />
                )
              }
            </SelectOption>
          ))}
      </TextSelect>
    );
  }
);

FontFamilySelect.defaultProps = {
  titles: {},
};
