/* eslint-disable @typescript-eslint/explicit-function-return-type */
import React from 'react';
import {
  Dropdown,
  ToolbarCellFillColorNormal,
  ToolbarPenNormal,
} from '@ali/we-design';
import { TranslateComp } from '@ali/we-util';
import { ColorPalette, EColorPanelMode } from './ColorPalette';
import {
  SplitButtonWrapper,
  ColorPaletteWrapper,
  FillColorArea,
} from './styled';
import { isWhite, isTransparent } from '../../../utils';
import SplitButton from '../../../common/splitButton';
import { SplitButtonOmit } from '../../../common/interface';
import FontIcon from './FontIcon';

export interface IColorPickerProps extends SplitButtonOmit {
  /**
   * SplitButton的默认激活状态
   */
  defaultActive?: boolean;
  /**
   * 默认的初始颜色值
   */
  defaultColor?: string;
  /**
   * 当前选中的颜色值
   */
  color?: string;
  /**
   * 自动颜色块的颜色
   */
  automaticColor: string;
  /**
   * 要显示清楚线的颜色块的颜色值
   */
  clearColor?: string;
  /**
   * 是否显示 colorPicker 组件, 即：选择“更多”颜色的组件
   */
  picker?: boolean;
  /**
   * 颜色选中后的回调函数，isAfter 为 true 表明颜色面板中用户停止操作之后触发的事件
   */
  onColorPicker: (color: string, isAfter: boolean) => void;
  /**
   * 颜色选择按钮类型，会覆盖 btnIcon 属性
   */
  btnType?: EColorPickerIcon;
  /**
   * 颜色选择按钮的图标组件
   */
  btnIcon?: React.ReactNode;
  /**
   * SplitButton左侧点击时的回调函数
   */
  onClickLeft?: (color: string) => void;
  /**
   * 本地语言类型，如zh-CN
   */
  locale?: string;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /*
   * 是否显示 最近使用 的颜色
   */
  showRecentColors?: boolean;
  /**
   * 是否显示自动
   */
  showAutomatic?: boolean;
  /**
   * 色块hover出现的字
   */
  colorBlockText?: string;
  /**
   * 自定义色块hover出现的字的颜色
   */
  customColorBlockTextColor?: (color: string) => string;
  /**
   * 自定义颜色面板插槽
   */
  customColorPalette?: React.ReactElement;
  /**
   * 是否显示面板样式切换下拉框, 默认不显示
   */
  showToggleSelect?: boolean;
  /**
   * 面板样式下拉框响应函数函数
   */
  onSelectValueChange?: (mode: EColorPanelMode) => void;
  /**
   * 艺术色板是否显示渐变色, 默认不显示
   */
  showGradient?: boolean;
  /**
   * 艺术色板第几行拥有双倍下间距，有渐变色时默认 1， 其它默认 0
   */
  doubleGapRowIndex?: number;
  /**
   * 获取色板样式, 默认返回 normal
   */
  getColorPanelMode?: () => EColorPanelMode;
}

interface IColorPickerState {
  currentColor: string; // 内部维护的颜色状态值
  currentActive: boolean; // 是否显示下拉框
}

const DEFAULT_COLOR = '#ffffff';
const ICON_DEFAULT_COLOR = 'inherit';

export enum EColorPickerIcon {
  font = 'font',
  background = 'background',
  backgroundfont = 'backgroundfont',
  backgroundsheet = 'backgroundsheet',
}

export class ColorPicker extends React.PureComponent<
  IColorPickerProps,
  IColorPickerState
> {
  constructor(props: IColorPickerProps) {
    super(props);
    this.state = {
      currentColor: props.color || props.defaultColor || DEFAULT_COLOR,
      currentActive: props.defaultActive || false,
    };
  }

  handlePaletteClick = (e: any) => {
    // 阻止冒泡
    e.stopPropagation();
    // 阻止默认行为
    e.preventDefault();
  };

  handleColorPick = (color: string): void => {
    const { onColorPicker } = this.props;
    this.setState({
      currentColor: color,
      currentActive: false,
    });
    if (onColorPicker) {
      onColorPicker(color, true);
    }
  };

  handleCustomColor = (color: string, isAfter: boolean): void => {
    const { onColorPicker } = this.props;
    // 设置 custom color 后立即应用，但 colorPicker 不收起
    this.setState({
      currentColor: color,
    });
    if (onColorPicker) {
      onColorPicker(color, isAfter);
    }
  };

  handleCustomColorPick = (color: string): void => {
    this.handleCustomColor(color, false);
  };

  handleCustomColorAfterPick = (color: string): void => {
    this.handleCustomColor(color, true);
  };

  handleClickLeft = (): void => {
    const { onClickLeft, onColorPicker } = this.props;
    const { currentColor } = this.state;
    if (onClickLeft) {
      onClickLeft(currentColor);
    } else {
      onColorPicker(currentColor, true);
    }
  };

  handleActive = (active: boolean) => {
    this.setState({ currentActive: active });
  };

  getColorPalette = (): React.ReactElement => {
    const {
      color,
      automaticColor,
      clearColor,
      picker,
      locale,
      btnType,
      showRecentColors,
      showAutomatic,
      colorBlockText,
      customColorPalette,
      customColorBlockTextColor,
      showGradient = false,
      showToggleSelect = false,
      doubleGapRowIndex = showGradient ? 1 : 0,
      getColorPanelMode,
      onSelectValueChange,
    } = this.props;
    const { currentColor } = this.state;

    const customText =
      btnType === EColorPickerIcon.font ? (
        <TranslateComp
          locale={locale}
          textKey={'we_toolbar_color_picker_default'}
        />
      ) : (
        <TranslateComp
          locale={locale}
          textKey={'we_toolbar_color_picker_no_color'}
        />
      );

    return (
      <ColorPaletteWrapper onClick={this.handlePaletteClick}>
        <ColorPalette
          color={color || currentColor}
          onPick={this.handleColorPick}
          onCustomColorPick={this.handleCustomColorPick}
          onCustomColorAfterPick={this.handleCustomColorAfterPick}
          automaticColor={automaticColor}
          clearColor={clearColor}
          picker={picker}
          locale={locale}
          showRecentColors={showRecentColors}
          quickSetText={customText}
          showAutomatic={showAutomatic}
          colorBlockText={colorBlockText}
          customColorBlockTextColor={customColorBlockTextColor}
          customColorPalette={customColorPalette}
          showGradient={showGradient}
          showToggleSelect={showToggleSelect}
          doubleGapRowIndex={doubleGapRowIndex}
          getColorPanelMode={getColorPanelMode}
          onSelectValueChange={onSelectValueChange}
        />
      </ColorPaletteWrapper>
    );
  };

  renderCustomTrigger(): React.ReactElement {
    const { children, title } = this.props;
    const { currentActive } = this.state;

    return (
      <Dropdown
        visible={currentActive}
        onVisibleChange={this.handleActive}
        overlay={currentActive ? this.getColorPalette() : null}
        title={title}
      >
        {children as React.ReactElement}
      </Dropdown>
    );
  }

  render() {
    const { currentColor, currentActive } = this.state;
    const {
      btnType,
      btnIcon,
      color,
      children,
      title,
      disabled,
      size,
      active: activeLeft,
    } = this.props;
    // 如果填充部分表现为白色（传入空字符串，白色或透明），则填充的外围边界线需有颜色
    const whiteFilling =
      !currentColor || isWhite(currentColor) || isTransparent(currentColor);
    const pathColors = disabled
      ? undefined
      : [
          !whiteFilling ? currentColor : 'white',
          !whiteFilling ? 'transparent' : ICON_DEFAULT_COLOR,
        ];
    const btnTypeList: Record<string, React.ReactNode> = {
      [EColorPickerIcon.font]: (
        <FontIcon pathColors={pathColors} color={currentColor} />
      ),
      [EColorPickerIcon.background]: (
        <ToolbarCellFillColorNormal pathColors={pathColors} />
      ),
      [EColorPickerIcon.backgroundfont]: (
        <ToolbarPenNormal pathColors={pathColors} />
      ),
      [EColorPickerIcon.backgroundsheet]: (
        <ToolbarCellFillColorNormal pathColors={pathColors} />
      ),
    };

    return children ? (
      this.renderCustomTrigger()
    ) : (
      <SplitButton
        overlay={currentActive ? this.getColorPalette() : <></>}
        onClickLeft={this.handleClickLeft}
        onActiveChange={this.handleActive}
        disabled={disabled}
        testid={`ColorPicker-${btnType}`}
        title={title}
        size={size}
        icon={
          <SplitButtonWrapper>
            {btnType ? (
              btnTypeList[btnType]
            ) : (
              <>
                {btnIcon}
                <FillColorArea color={color || currentColor} />
              </>
            )}
          </SplitButtonWrapper>
        }
        {...this.props}
        active={currentActive}
        activeLeft={activeLeft}
      />
    );
  }
}
