import React, { useCallback, useState, useMemo } from 'react';
import {
  useControlledValue,
  TOptionTitle,
  Menu,
  MenuItem,
  ToolbarSuperscriptNormal,
  ToolbarSubscriptNormal,
} from '@ali/we-design';
import SplitButton from '../../../common/splitButton';
import { useTranslate } from '@ali/we-util';
import { SplitButtonOmit } from '../../../common/interface';

type ScriptType = 'super' | 'sub';

export type IScriptSelectProps = SplitButtonOmit & {
  /**
   * 默认的上标或者下标
   */
  defaultValue?: ScriptType;
  /**
   * 上标或者下标
   */
  value?: ScriptType;
  /**
   * 点击的回调函数
   */
  onClick?: (value: ScriptType, event?: React.MouseEvent) => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 本地语言类型，如zh-CN
   */
  locale?: string;
};

const ScriptSelect: React.FC<IScriptSelectProps> = (props) => {
  const { onClick, title, disabled, size, locale, active: activeLeft } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const [active, setActive] = useState(false);
  const t = useTranslate(locale);

  /**
   * 点击上下标操作
   */
  const handleClickLeft = useCallback(
    (event: React.MouseEvent) => {
      if (onClick) {
        onClick(value!, event);
      }
    },
    [value, onClick],
  );

  const isSuper = value === 'super';
  const superText = t('we_toolbar_superscript'); // 上标文案
  const subText = t('we_toolbar_subscript'); // 下标文案

  const handleChooseScript = useCallback(
    (script: ScriptType): void => {
      if (onClick) {
        onClick(script);
      }
      setValue(script);
    },
    [onClick, setValue],
  );

  const handleChooseSuper = useCallback(() => {
    if (value !== 'super') {
      handleChooseScript('super');
      setActive(false);
    }
  }, [value, handleChooseScript]);

  const handleChooseSub = useCallback(() => {
    if (value !== 'sub') {
      handleChooseScript('sub');
      setActive(false);
    }
  }, [value, handleChooseScript]);

  const chooses = [
    {
      title: superText,
      value: 'super',
      Icon: ToolbarSuperscriptNormal,
      onClick: handleChooseSuper,
    },
    {
      title: subText,
      value: 'sub',
      Icon: ToolbarSubscriptNormal,
      onClick: handleChooseSub,
    },
  ];

  /**
   * 下拉选项
   */
  const DropContent = useMemo(
    () => (
      <Menu showSelect style={{ minWidth: 'auto' }} minWidth={100}>
        {chooses.map((item) => (
          <MenuItem
            testid={`option-${item.value}`}
            menuKey={`option-${item.value}`}
            icon={<item.Icon className="script-icon" />}
            selected={value === item.value}
            onClick={item.onClick}
            style={{ minWidth: 'auto' }}
          >
            <div>{item.title}</div>
          </MenuItem>
        ))}
      </Menu>
    ),
    [value],
  );

  const iconMeno = useMemo(
    () => (isSuper ? <ToolbarSuperscriptNormal /> : <ToolbarSubscriptNormal />),
    [isSuper],
  );

  return (
    <SplitButton
      overlay={DropContent}
      onClickLeft={handleClickLeft}
      onActiveChange={setActive}
      size={size}
      disabled={disabled}
      title={title || (isSuper ? superText : subText)}
      icon={iconMeno}
      testid="ScriptSelect"
      {...props}
      active={active}
      activeLeft={activeLeft}
    />
  );
};

ScriptSelect.defaultProps = {
  defaultValue: 'super',
  titles: {},
};

export default ScriptSelect;
