import React, { useMemo, useState, useCallback } from 'react';
import {
  withTheme,
  useControlledValue,
  ToolbarOrderListNormal,
  ToolbarUnorderListNormal,
} from '@ali/we-design';
import styled from 'styled-components';
import { SplitButtonOmit } from '../../../common/interface';
import SplitButton from '../../../common/splitButton';

import { classnames } from '../../../utils';

export type ListMode = 'ul' | 'ol';

export type IListSetProps = SplitButtonOmit & {
  /**
   * 列表模式
   */
  mode?: ListMode;
  /**
   * 默认选择的列表样式 (非受控模式)，如不传入则默认为 ul/ol 的第一种样式
   */
  defaultListStyle?: string;
  /**
   * 列表样式（受控模式）
   */
  listStyle?: string;
  /**
   * 选择列表样式变化的回调函数
   */
  onChange?: (listStyle: string) => void;
  /**
   *  点击左侧按钮的回调函数
   */
  onClick?: (listStyle: string) => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 是否展示表情列表
   */
  enableSticker?: boolean;
};

