const OverlayContent = styled.div`
  width: 272px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 4px;
`;

const ListStyleItemWrap = styled.div`
  user-select: none;
`;

const ListStyleItem = withTheme(styled.img`
  width: 80px;
  height: 80px;
  margin: 4px;
  border: 1px solid rgba(17, 31, 44, 0.12);
  border-radius: 2px;
  cursor: pointer;
  image-rendering: pixelated;

  &:hover {
    border: 1px solid ${({ theme }) => theme.ui.primaryColor};
  }

  &.actived {
    border: 2px solid ${({ theme }) => theme.ui.primaryColor};
  }
`);

function getListRole(mode: string, key: string): string {
  const prefix = mode === 'ul' ? 'listUnordered' : 'listOrdered';
  return `${prefix}_${key}`;
}

export const ListSet: React.FC<IListSetProps> = (props) => {
  const {
    mode = 'ul',
    defaultListStyle,
    listStyle,
    onChange,
    onClick,
    disabled,
    title,
    active,
    size,
    enableSticker,
  } = props;
  const listStyleArray = useMemo(
    () => {
      if (mode !== 'ul') {
        return DEFAULT_NUMBER_LIST_ARRAY;
      } else {
        return enableSticker
          ? DEFAULT_BULLET_LIST_ARRAY.concat(DEFAULT_STICKER_LIST_ARRAY)
          : DEFAULT_BULLET_LIST_ARRAY;
      }
    },
    [mode],
  );
  const [value, setValue] = useControlledValue(
    listStyle,
    defaultListStyle || listStyleArray[0],
  );
  const [overlayVisible, setOverlayVisible] = useState(false);

  const handleChange = useCallback(
    (e: string) => {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
      setOverlayVisible(false);
    },
    [onChange, setValue],
  );

  const handleVisibleChange = useCallback((visible: boolean) => {
    setOverlayVisible(visible);
  }, []);

  const handleClickLeft = useCallback(() => {
    if (onClick) {
      onClick(value!);
    }
  }, [onClick, value]);

  const getOverlay = useMemo(() => {
    return (
      <OverlayContent data-testid="overlay-ListSet">
        {listStyleArray.map((item) => {
          return (
            <ListStyleItemWrap
              key={item}
              onClick={() => handleChange(item)}
              data-role={getListRole(mode, item)}
              role="button"
            >
              <ListStyleItem
                src={LIST_STYLE_MAP[item]}
                alt="list-style-item"
                className={classnames({ actived: item === value })}
                data-testid={`option-${item}`}
              />
            </ListStyleItemWrap>
          );
        })}
      </OverlayContent>
    );
  }, [value]);

  const renderTriggerIcon = useMemo(
    () =>
      mode === 'ul' ? <ToolbarUnorderListNormal /> : <ToolbarOrderListNormal />,
    [mode],
  );

  return (
    <SplitButton
      overlay={getOverlay}
      onClickLeft={handleClickLeft}
      disabled={disabled}
      onActiveChange={handleVisibleChange}
      testid={`ListSet_${mode}`}
      title={
        title || mode === 'ol'
          ? 'we_toolbar_order_list'
          : 'we_toolbar_unorder_list'
      }
      size={size}
      icon={renderTriggerIcon}
      tooltip={
        mode === 'ol' ? 'we_toolbar_order_list' : 'we_toolbar_unorder_list'
      }
      {...props}
      active={overlayVisible}
      activeLeft={active}
    />
  );
};
