import styled, { css } from 'styled-components';
import { withTheme } from '@ali/we-design';
import { IconButtonProps } from '../interface';
import { TOOL_SIZE } from '../../utils/consts';

export const TitleWrap: any = withTheme(styled.div<IconButtonProps>`
  font-size: ${({ theme, size }) =>
    size === TOOL_SIZE.LARGE ? theme.ui.fontSizeSmall : theme.ui.fontSizeMenu};
  color: ${({ theme }) => theme.ui.commonActionIconColor};
  font-weight: normal;
  user-select: none;
  cursor: default;
  margin-left: ${({ theme, size }) =>
    size === TOOL_SIZE.LARGE ? '' : theme.ui.marginMini};
  align-items: center;
  display: flex;
  flex: 1;
  text-align: center;
  justify-content: end;
  ${({ size }) =>
    size === TOOL_SIZE.LARGE &&
    css`
      max-width: 52px;
      min-width: 45px;
      justify-content: center;
    `}
  div {
    overflow: hidden;
    text-overflow: ellipsis;
    ${({ size }) =>
      size === TOOL_SIZE.MEDIUM &&
      css`
        height: 20px;
        line-height: 20px;
      `}
    ${({ size, showTitle }) =>
      size === TOOL_SIZE.LARGE &&
      showTitle &&
      css<any>`
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        word-break: break-word;
        line-height: 12px;
        padding-top: 2px;
      `}
  }
  ${({ disabled }) => disabled && 'opacity:.35;cursor:not-allowed;'};
`);

const largeAndShowTitleButtonWrap = css<any>`
  height: 52px;
  min-width: 40px;
  max-width: 52px;
  padding: 0px;
  :hover {
    background-color: ${({ theme }) => theme.ui.toolbarButtonTransparent};
  }
  ${({ active, theme }) =>
    active && `background-color: ${theme.ui.toolbarButtonTransparent};`}
  :hover .icon-button {
    background-color: ${({ theme }) => theme.ui.toolbarButtonHover};
    ${({ disabled, theme }) =>
      disabled && `background-color: ${theme.ui.toolbarButtonTransparent};`}
    ${({ active, theme, disabled }) =>
      active &&
      `background-color: ${
        disabled
          ? theme.ui.toolbarButtonSelectDisable
          : theme.ui.toolbarButtonSelectHover
      };`}
  }
  :active {
    ${({ size, showTitle, theme, disabled }) =>
      !disabled &&
      size === TOOL_SIZE.LARGE &&
      showTitle &&
      css`
        .icon-button {
          background-color: ${theme.ui.toolbarButtonClick};
        }
      `}
  }
  .icon-button {
    padding: ${({ theme }) => theme.ui.paddingMini};
    ${({ active, theme, disabled }) =>
      active &&
      `background-color: ${
        disabled
          ? theme.ui.toolbarButtonSelectDisable
          : theme.ui.toolbarButtonSelect
      };`}
  }
  :hover .icon-button:active {
    ${({ disabled, theme }) =>
      !disabled && `background-color :${theme.ui.toolbarButtonClick} `};
  }
  .icon-button:hover,
  .icon-button:focus {
    background-color: ${({ theme }) => theme.ui.toolbarButtonHover};
    ${({ disabled, theme }) =>
      disabled && `background-color: ${theme.ui.toolbarButtonTransparent};`}
    ${({ active, theme, disabled }) =>
      active &&
      `background-color: ${
        disabled
          ? theme.ui.toolbarButtonSelectDisable
          : theme.ui.toolbarButtonSelectHover
      };`}
  }
`;
export const ButtonRefWrap = styled.div``;

export const ButtonWrap = withTheme(styled.div<IconButtonProps>`
  display: inline-flex;
  position: relative;
  flex-direction: ${({ size }) =>
    size === TOOL_SIZE.LARGE ? 'column' : 'row'};
  align-items: center;
  padding: ${({ theme, size }) =>
    size === TOOL_SIZE.MEDIUM ? theme.ui.paddingTiny : theme.ui.paddingMini};
  ${({ active, theme, disabled }) =>
    active &&
    `background-color: ${
      disabled
        ? theme.ui.toolbarButtonSelectDisable
        : theme.ui.toolbarButtonSelect
    };`}
  border-radius: ${({ theme, borderRadius }) =>
    typeof borderRadius !== 'undefined'
      ? `${borderRadius}px`
      : theme.ui.radiusSmall};
  :hover {
    ${({ active, theme, disabled }) =>
      !disabled &&
      `background-color:  ${
        active ? theme.ui.toolbarButtonSelectHover : theme.ui.toolbarButtonHover
      }; `}
  }
  :hover .icon-button {
    background-color: ${({ theme }) => theme.ui.toolbarButtonTransparent};
  }
  :active {
    ${({ theme, disabled }) =>
      !disabled && `background-color :${theme.ui.toolbarButtonClick}`}
  }
  .weicon {
    font-size: ${({ theme, size }) =>
      size === TOOL_SIZE.LARGE
        ? theme.ui.toolbarFontSizeIconLarge
        : theme.ui.toolbarFontSizeIcon};
    ${({ disabled }) =>
      disabled &&
      css`
        opacity: 0.35;
      `}
  }
  .icon-button {
    cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'default')};
    border: none;
    height: auto;
    width: auto;
    padding: 0;
    color: ${({ theme }) => theme.ui.toolbarButtonIcon};
    ${({ disabled, theme }) =>
      disabled && `color:${theme.ui.toolbarButtonIconDisable};`};
    border-radius: 4px;
  }
  .icon-button:hover,
  .icon-button:focus {
    background-color: ${({ theme }) => theme.ui.toolbarButtonTransparent};
  }
  ${TitleWrap} {
  }
  ${({ size, showTitle }) =>
    size === TOOL_SIZE.LARGE && showTitle && largeAndShowTitleButtonWrap};
`);
