import * as React from 'react';
import { ToolbarPictureNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';
import { useTranslate } from '@ali/we-util';

export type ImageButtonProps = IconMenuPropsOmit;

export const ImageButton: React.FC<ImageButtonProps> = React.memo((props) => {
  const [visible, setVisible] = React.useState(false);

  const handleVisibleChange = (value: boolean) => {
    setVisible(value);
  };
  const t = useTranslate(props.locale);
  return (
    <IconMenu
      visible={visible}
      icon={<ToolbarPictureNormal />}
      title={t('we_toolbar_picture')}
      tooltip={t('we_toolbar_picture_tooltip')}
      onVisibleChange={handleVisibleChange}
      testid="ImageButton"
      {...props}
    />
  );
});

ImageButton.defaultProps = {};
