import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconButtonPropsOmit } from '../../../common/interface';
import { IconButtonDefaultProps } from '../../../utils/defaultProps';
import IconButton from '../../../common/iconButton';
import { ToolbarDropdownNormal } from '@ali/we-design';

export type SelectButtonProps = IconButtonPropsOmit & {
  icon?: React.ReactNode;
};

export const SelectButton: React.FC<SelectButtonProps> = React.memo((props) => {
  const t = useTranslate(props.locale);

  return (
    <IconButton
      testid="select_button"
      icon={props.icon || <ToolbarDropdownNormal />}
      title={t('we_toolbar_options_modal_title')}
      tooltip={t('we_toolbar_options_modal_title')}
      {...props}
    />
  );
});

SelectButton.defaultProps = { ...IconButtonDefaultProps };
