/* eslint-disable max-lines-per-function */
import {
  TOptionTitle,
  useControlledValue,
  ToolbarMergeCellNormal,
  ToolbarSplitCellNormal,
  Menu,
  MenuItem,
} from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import React, { useState, useCallback, useMemo } from 'react';
import { SplitButtonOmit } from '../../../common/interface';
import SplitButton from '../../../common/splitButton';
import styled from 'styled-components';
import { Ellipsis } from '../../../utils/styled.mixin';

const TriggerWrap = styled.div`
  display: flex;
  align-items: center;
  .title {
    width: 70px;
    margin-left: 6px;
    ${Ellipsis};
  }
`;

export type CellMergeStatus = 'unmerged' | 'merged';

export type IMergeCellButton = SplitButtonOmit & {
  /**
   * 合并状态（受控）
   */
  status?: CellMergeStatus;
  /**
   * 默认的合并状态（非受控）
   */
  defaultStatus?: CellMergeStatus;
  /**
   * 点击选项的回调函数，回调的入参为选择的选项对应状态
   */
  onSelect?: (e: CellMergeStatus) => void;
  /**
   * 点击左侧的回调函数，回调的入参为单元格 **当前** 的状态
   */
  onClick?: (e: CellMergeStatus) => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 国际化语言
   */
  locale?: string;
  /**
   * 是否显示字
   */
  showTitle?: boolean;
};
interface IOptions {
  title: string;
  Icon: React.ComponentType<any>;
  value: CellMergeStatus;
  handleClick: () => void;
}

export const MergeCellButton: React.FC<IMergeCellButton> = (props) => {
  const {
    onSelect,
    onClick,
    locale,
    defaultStatus = 'unmerged',
    title,
    titles = {},
    disabled,
    active: leftActive,
    size,
    ...restProps
  } = props;
  const [overlayActive, setOverlayActive] = useState(false);
  const [status, setStatus] = useControlledValue(props.status, defaultStatus);
  const t = useTranslate(locale);

  const toggle = (curr: CellMergeStatus | undefined) =>
    curr === 'unmerged' ? 'merged' : 'unmerged';

  const handleClickMerged = () => {
    if (onSelect) {
      onSelect('merged');
    }
    setStatus(toggle);
    setOverlayActive(false);
  };

  const handleClickUnMerged = () => {
    if (onSelect) {
      onSelect('unmerged');
    }
    setStatus('unmerged');
    setOverlayActive(false);
  };

  const options: IOptions[] = [
    {
      title: t('we_toolbar_merge_cell'),
      Icon: ToolbarMergeCellNormal,
      value: 'merged',
      handleClick: handleClickMerged,
    },
    {
      title: t('we_toolbar_split_cell'),
      Icon: ToolbarSplitCellNormal,
      value: 'unmerged',
      handleClick: handleClickUnMerged,
    },
  ];

  const handleClickLeft = useCallback(() => {
    if (onClick) {
      onClick(status!);
    }
    setStatus((curr) => (curr === 'merged' ? 'unmerged' : 'merged'));
  }, [onClick, status, setStatus]);

  const isMerged = status === 'merged';
  const getOverlay = useMemo(() => {
    return (
      <Menu showSelect={true} minWidth={120}>
        {options.map((item, index) => (
          <MenuItem
            key={index}
            icon={
              status === 'unmerged' ? (
                <ToolbarMergeCellNormal />
              ) : (
                <ToolbarSplitCellNormal />
              )
            }
            selected={item.value === 'merged' && isMerged}
            disabled={status === 'unmerged' && item.value === 'unmerged'}
            menuKey={item.value}
            onClick={item.handleClick}
            testid={`option-${item.value}`}
          >
            {item.title}
          </MenuItem>
        ))}
      </Menu>
    );
  }, [options, status, isMerged, titles]);

  const rendertTitle = useMemo(
    () =>
      status === 'unmerged'
        ? t('we_toolbar_merge_cell')
        : t('we_toolbar_split_cell'),
    [status],
  );

  const renderTriggerIcon = useMemo(
    () => (
      <TriggerWrap className="we-ui-merge-cell-button-trigger">
        {status === 'unmerged' ? (
          <ToolbarMergeCellNormal />
        ) : (
          <ToolbarSplitCellNormal />
        )}
      </TriggerWrap>
    ),
    [status],
  );

  return (
    <SplitButton
      {...restProps}
      overlay={getOverlay}
      onClickLeft={handleClickLeft}
      active={overlayActive}
      activeLeft={leftActive}
      onActiveChange={setOverlayActive}
      testid="MergeCellButton"
      title={rendertTitle}
      size={size}
      disabled={disabled}
      icon={renderTriggerIcon}
    />
  );
};
