import { useMemo } from 'react';
import { useTranslate } from '@ali/we-util';
import { getFontFamily } from './getFontFamily';

export interface fontFamily {
  title: string;
  value: string;
  installable: boolean;
}

/**
 * 获取fontFamily, 字体探测结果, 语言转换函数
 * @param locale 本地语言类型
 * @returns {[options, installedRecord, t]}
 */
export const useFontFamily = (locale?: string | undefined) => {
  const t = useTranslate(locale);

  // 字体列表选项
  const options = useMemo(() => {
    const fontFamilies = getFontFamily((s) => t(s));
    return fontFamilies;
  }, [t]);

  return [options, t];
};
