import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconButtonPropsOmit } from '../../../common/interface';
import { IconButtonDefaultProps } from '../../../utils/defaultProps';
import IconButton from '../../../common/iconButton';
import { ToolbarUndoNormal } from '@ali/we-design';

export type UndoButtonProps = IconButtonPropsOmit;

export const UndoButton: React.FC<UndoButtonProps> = React.memo((props) => {
  const t = useTranslate(props.locale);

  return (
    <IconButton
      testid="undo_button"
      icon={<ToolbarUndoNormal />}
      title={t('we_toolbar_undo_button')}
      tooltip={t('we_toolbar_undo_button')}
      {...props}
    />
  );
});

UndoButton.defaultProps = { ...IconButtonDefaultProps };
