import styled, { css } from 'styled-components';
import { withTheme } from '@ali/we-design';
import { IconMenuProps } from '../interface';
import { TOOL_SIZE } from '../../utils/consts';

export const TextWarp = withTheme(styled.div<IconMenuProps>`
  display: flex;
  align-items: center;
  max-width: 52px;
  min-width: 40px;
  text-align: center;
  font-size: ${({ theme, size }) =>
    size === TOOL_SIZE.LARGE ? theme.ui.fontSizeSmall : theme.ui.fontSizeMenu};
  text-align: center;
  height: 24px;
  user-select: none;
  color: ${({ theme }) => theme.ui.commonActionIconColor};
  > div {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-top: 2px;
    -webkit-line-clamp: 2;
    word-break: break-word;
    line-height: 12px;
  }

  :hover ~ div {
    background-color: ${({ theme }) => theme.ui.toolbarButtonHover};
  }
  :active ~ div {
    background-color: ${({ theme }) => theme.ui.toolbarButtonClick};
  }
`);

export const ButtonWrap: any = withTheme(styled.div<
  IconMenuProps & { showOverlay: boolean }
>`
  display: flex;
  padding: 4px 0;
  border-radius: ${({ theme }) => theme.ui.radius};
  > * {
    height: 20px;
    padding: 0 2px;
  }
  :hover {
    background-color: ${({ theme }) => theme.ui.toolbarButtonHover};
  }
  :active {
    background-color: ${({ theme }) => theme.ui.toolbarButtonClick};
  }
  .left-wrap {
    border-right: 1px solid transparent;
    display: flex;
    .weicon {
      font-size: 20px;
      color: ${({ theme }) => theme.ui.commonActionIconColor};
    }
  }
  .left-wrap .left-wrap-text {
    color: ${({ theme }) => theme.ui.commonActionIconColor};
    font-size: ${({ theme, size }) =>
      size === TOOL_SIZE.LARGE
        ? theme.ui.fontSizeSmall
        : theme.ui.fontSizeMenu};
    line-height: 20px;
    padding-left: 2px;
    user-select: none;
    white-space: nowrap;
  }
  .right-wrap {
    display: flex;
    align-items: center;
    .weicon {
      font-size: 8px;
      color: ${({ theme }) => theme.ui.commonBaseColor2};
      height: 8px;
      ${({ showOverlay }) =>
        showOverlay &&
        css`
          transform: rotate(180deg);
        `}
    }
  }
  ${({ active }) =>
    active &&
    css`
      background-color: ${({ theme }) => theme.ui.toolbarButtonHover};
      :hover {
        background-color: ${({ theme }) => theme.ui.toolbarButtonClick};
      }
    `}
  ${({ size }) =>
    size === TOOL_SIZE.SMALL &&
    css`
      padding: 2px 0;
    `}
  ${({ size }) =>
    size === TOOL_SIZE.LARGE &&
    css`
      padding: 2px 0;
      > * {
        height: 24px;
      }
      .left-wrap .weicon {
        font-size: 24px;
      }
    `}
`);

export const Warp: any = withTheme(styled.div<IconMenuProps>`
  display: flex;
  position: relative;
  flex-direction: column-reverse;
  cursor: default;
  ${ButtonWrap} {
    margin: auto;
  }
  ${({ disabled }) =>
    disabled &&
    css`
      opacity: 0.35;
      cursor: not-allowed;
    `}
`);
