import React, { useState, useCallback, useMemo } from 'react';
import {
  TOptionTitle,
  ToolbarIncreaseIndentNormal,
  ToolbarReduceIndentNormal,
  Menu,
  MenuItem,
} from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import { SplitButtonOmit } from '../../../common/interface';
import SplitButton from '../../../common/splitButton';

export interface IIndentButtonProps extends SplitButtonOmit {
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 点击选项的回调函数
   */
  onSelect?: (e: 'increase' | 'decrease', option?: IOptions) => void;
  /**
   * 点击左侧的回调函数
   */
  onClick?: () => void;
  /**
   * 选择器标题
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /*
   * 国际化语言
   */
  locale?: string;
  /**
   * 左侧按钮的 icon
   */
  icon?: React.ReactElement;
}
interface IOptions {
  title: string;
  Icon: React.ComponentType<any>;
  value: 'increase' | 'decrease';
}

export const IndentButton: React.FC<IIndentButtonProps> = (props) => {
  const {
    disabled,
    onSelect,
    onClick,
    title,
    titles,
    locale,
    size,
    active: activeLeft,
    icon,
    ...restProps
  } = props;
  const [active, setActive] = useState(false);
  const t = useTranslate(locale);

  const options: IOptions[] = [
    {
      title: t('we_toolbar_increase_indent'),
      Icon: ToolbarIncreaseIndentNormal,
      value: 'increase',
    },
    {
      title: t('we_toolbar_reduce_indent'),
      Icon: ToolbarReduceIndentNormal,
      value: 'decrease',
    },
  ];

  const handleClickOption = useCallback(
    (e: 'increase' | 'decrease') => {
      if (onSelect)
        onSelect(
          e,
          options.find((o) => o.value === e),
        );
    },
    [onSelect, options],
  );

  const handleClickLeft = useCallback(() => {
    if (onClick) onClick();
  }, [onClick]);

  const getOverlay = useMemo(
    () => (
      <Menu
        onClick={(value) => {
          if (value === 'increase' || value === 'decrease')
            handleClickOption(value);
        }}
      >
        {options.map((item) => (
          <MenuItem
            menuKey={item.value}
            icon={<item.Icon></item.Icon>}
            testid={`option-${item.value}`}
          >
            {item.title}
          </MenuItem>
        ))}
      </Menu>
    ),
    [options, handleClickOption, titles],
  );

  return (
    <SplitButton
      overlay={getOverlay}
      onActiveChange={setActive}
      activeLeft={activeLeft}
      disabled={disabled}
      testid="IndentButton"
      title={title}
      size={size}
      icon={icon || <ToolbarIncreaseIndentNormal />}
      {...restProps}
      onClickLeft={handleClickLeft}
      active={active}
    />
  );
};

IndentButton.defaultProps = {
  titles: {},
};
