import React from 'react';
import { Tooltip,
  ToolbarBorderAllNormal,
  ToolbarBorderInnerNormal,
  ToolbarBorderLeftNormal,
  ToolbarBorderOuterNormal,
  ToolbarBorderRightNormal,
  ToolbarBorderTopNormal,
  ToolbarBorderVerticalNormal,
  ToolbarBorderHorizonNormal,
  ToolbarBorderBottomNormal,
} from '@ali/we-design';
import { TranslateProvider } from '@ali/we-util';
import { BorderTypeWrapper, IconWrapper } from './styled';
import { isWhite } from '../../../utils';


export enum BorderType {
  top = 'top',
  bottom = 'bottom',
  left = 'left',
  right = 'right',
  none = 'none',
  outer = 'outer',
  inner = 'inner',
  horizon = 'horizon',
  vertical = 'vertical',
  all = 'all',
  unselected = 'unselected',
}

export interface IBorderTypePanelProps {
  /**
   * 国际化
   */
  locale?: string;
  /**
   * 选中的边框类型
   */
  borderType?: BorderType;
  /**
   * 边框图标的大小
   */
  borderIconSize?: number;
  /**
   * 图标中边框部分的颜色
   */
  borderSelectedColor?: string;
  /**
   * BorderTypeWrapper 的样式
   */
  style?: React.CSSProperties;
  /**
   * 边框类型变化时的回调函数
   */
  onBorderTypeChange: (type: BorderType) => void;
}

type TypeUnselected = 'unselected';

const UNSELECTED = 'unselected';

export class BorderTypePanel extends React.PureComponent<
IBorderTypePanelProps,
{
  // 当前选中的边框类型
  currentBorderType: BorderType | TypeUnselected;
}
> {
  static defaultProps = {
    borderIconSize: 20,
  };

  constructor(props: IBorderTypePanelProps) {
    super(props);
    const currentBorderType = props.borderType ? props.borderType : UNSELECTED;
    this.state = {
      currentBorderType,
    };
  }

  handleBorderTypeChange = (borderType: BorderType) => {
    this.setState({ currentBorderType: borderType });
    const { onBorderTypeChange } = this.props;
    if (onBorderTypeChange) {
      onBorderTypeChange(borderType);
    }
  };

  render() {
    const {
      borderType,
      borderIconSize = 20,
      borderSelectedColor = '#000000',
      style,
    } = this.props;
    const { currentBorderType } = this.state;
    const activeBorderType = borderType || currentBorderType;

    const defaultWrapperStyle = {
      width: '100px',
      height: '100px',
    };
    const wrapperStyle = { ...defaultWrapperStyle, ...(style || {}) };

    const iconStyle: React.CSSProperties = {
      width: borderIconSize,
      height: borderIconSize,
      fontSize: borderIconSize,
      boxSizing: 'border-box',
      padding: '1px 1px',
    };

    const ICON_DEFAULT_COLOR = 'rgba(58, 60, 63, 1)';

    const pathColors = [
      borderSelectedColor || ICON_DEFAULT_COLOR,
      // 当选中颜色为白色时，选中边框的外边缘为黑色，否则选中边框外边缘与内部颜色一致
      !borderSelectedColor || isWhite(borderSelectedColor)
        ? ICON_DEFAULT_COLOR
        : borderSelectedColor,
    ];

    const borderList: Record<string, React.ReactNode> = {
      [BorderType.outer]: (
        <ToolbarBorderOuterNormal pathColors={pathColors} style={iconStyle} />
      ),
      [BorderType.inner]: (
        <ToolbarBorderInnerNormal pathColors={pathColors} style={iconStyle} />
      ),
      [BorderType.all]: (
        <ToolbarBorderAllNormal pathColors={pathColors} style={iconStyle} />
      ),
      [BorderType.left]: (
        <ToolbarBorderLeftNormal pathColors={pathColors} style={iconStyle} />
      ),
      [BorderType.vertical]: (
        <ToolbarBorderVerticalNormal
          pathColors={pathColors}
          style={iconStyle}
        />
      ),
      [BorderType.right]: (
        <ToolbarBorderRightNormal pathColors={pathColors} style={iconStyle} />
      ),
      [BorderType.top]: (
        <ToolbarBorderTopNormal pathColors={pathColors} style={iconStyle} />
      ),
      [BorderType.horizon]: (
        <ToolbarBorderHorizonNormal pathColors={pathColors} style={iconStyle} />
      ),
      [BorderType.bottom]: (
        <ToolbarBorderBottomNormal pathColors={pathColors} style={iconStyle} />
      ),
    };

    return (
      <BorderTypeWrapper style={wrapperStyle}>
        {Object.keys(borderList).map((key) => {
          const iconComp = borderList[key];
          const tipTitle = `we_toolbar_border_${BorderType[key as BorderType]}`;
          return (
            <TranslateProvider key={key}>
              {(t) => (
                <Tooltip title={t(tipTitle)}>
                  <IconWrapper
                    active={activeBorderType === key}
                    onClick={() =>
                      this.handleBorderTypeChange(key as BorderType)
                    }
                  >
                    {iconComp}
                  </IconWrapper>
                </Tooltip>
              )}
            </TranslateProvider>
          );
        })}
      </BorderTypeWrapper>
    );
  }
}
