import { useTranslate } from '@ali/we-util';
import * as React from 'react';
import { ToolbarFontInsertNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';

export type InsertTextMenuButtonProps = IconMenuPropsOmit;

export const InsertTextMenuButton: React.FC<InsertTextMenuButtonProps> = React.memo(
  (props) => {
    const [visible, setVisible] = React.useState(false);

    const handleVisibleChange = (value: boolean) => {
      setVisible(value);
    };
    const t = useTranslate(props.locale);

    return (
      <IconMenu
        visible={visible}
        icon={<ToolbarFontInsertNormal />}
        title={t('we_toolbar_add_text')}
        tooltip={t('we_toolbar_add_text')}
        onVisibleChange={handleVisibleChange}
        testid="InsertTextMenuButton"
        {...props}
      />
    );
  },
);

InsertTextMenuButton.defaultProps = {};
