import { useState, useCallback } from 'react';

type UseControlledValueReturned<VT> = [VT, React.Dispatch<React.SetStateAction<VT>>];

const useControlledValue = <VT>(
  propsValue: VT, propsDefaultValue: VT,
): UseControlledValueReturned<VT> => {
  // 存进state中
  const [value, setValue] = useState<VT>(propsValue === undefined ? propsDefaultValue : propsValue);

  // 如果是value不是undefined，说明受控
  const noop = useCallback(() => {}, []);
  if (propsValue !== undefined) {
    return [propsValue, noop];
  }

  return [value, setValue];
};

export default useControlledValue;
