import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconButtonPropsOmit } from '../../../common/interface';
import { IconButtonDefaultProps } from '../../../utils/defaultProps';
import IconButton from '../../../common/iconButton';
import { ToolbarCheckBoxNormal } from '@ali/we-design';

export type CheckboxButtonProps = IconButtonPropsOmit;

export const CheckboxButton: React.FC<CheckboxButtonProps> = React.memo((props) => {
  const t = useTranslate(props.locale);

  return (
    <IconButton
      icon={<ToolbarCheckBoxNormal />}
      title={t('we_toolbar_checkbox')}
      tooltip={t('we_toolbar_checkbox')}
      testid="CheckboxButton"
      {...props}
    />
  );
});

CheckboxButton.defaultProps = { ...IconButtonDefaultProps };
