import { useTranslate } from '@ali/we-util';
import * as React from 'react';
import { UpanddownNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';

export type SortMenuButtonProps = IconMenuPropsOmit;

export const SortMenuButton: React.FC<SortMenuButtonProps> = React.memo(
  (props) => {
    const [visible, setVisible] = React.useState(false);

    const handleVisibleChange = (value: boolean) => {
      setVisible(value);
    };
    const t = useTranslate(props.locale);

    return (
      <IconMenu
        visible={visible}
        icon={<UpanddownNormal />}
        title={t('we_toolbar_sort')}
        tooltip={t('we_toolbar_sort')}
        onVisibleChange={handleVisibleChange}
        testid="SortMenuButton"
        {...props}
      />
    );
  },
);

SortMenuButton.defaultProps = {};
