import React, { useMemo, useCallback } from 'react';
import { SelectOption, TOptionTitle, useControlledValue, FooterPageViewNormal, FooterPagingViewNormal, FooterWebViewNormal } from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import { OptionWrap } from './styled';

import { IconSelectPropsOmit, IconSelect } from '../../../../common';
import { IconSelectDefaultProps } from '../../../../utils/defaultProps';

export type PageView = 'page_layout' | 'normal_layout' | 'web_layout';

export interface IPageViewSelectProps extends IconSelectPropsOmit<PageView> {
  /**
   * 默认的页面视图，当设置了value，该项忽略
   */
  defaultValue?: PageView;
  /**
   * 当前页面视图
   */
  value?: PageView;
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * 页面视图改变时的回调函数
   */
  onChange?: (view: PageView) => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 是否禁止
   */
  disabled?: boolean;
  /**
   * 是否允许使用 web 模式
   */
  disableWebLayout?: boolean;
}

const PageViewSelect: React.FC<IPageViewSelectProps> = (props) => {
  const {
    locale,
    onChange,
    title,
    titles,
    disabled,
    size,
    disableWebLayout,
    ...restProps
  } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  // 页面视图的选项
  const options = useMemo(
    () =>
      [
        {
          title: t('we_toolbar_view_page_view'),
          value: 'normal_layout',
          Icon: FooterPageViewNormal,
        },
        {
          title: t('we_toolbar_view_paging_view'),
          value: 'page_layout',
          Icon: FooterPagingViewNormal,
        },
      ].concat(
        disableWebLayout
          ? []
          : {
              title: t('we_toolbar_view_web_view'),
              value: 'web_layout',
              Icon: FooterWebViewNormal,
            }
      ),
    [t, disableWebLayout]
  );

  // 该组件的trigger区域显示Icon+文字
  const renderTrigger = useCallback(
    (selectedView?: string) => {
      const index = options.findIndex((option) => option.value === selectedView);
      const currentOption = options[index];
      return (
        <>
          {currentOption && (
            <>
              <currentOption.Icon style={{ marginRight: 1 }} />
              {currentOption.title}
            </>
          )}
        </>
      );
    },
    [options],
  );

  const handleChange = useCallback(
    (e: PageView) => {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
    },
    [setValue, onChange],
  );

  return (
    <IconSelect
      {...restProps}
      size={size}
      value={value}
      onChange={handleChange}
      renderTrigger={renderTrigger}
      borderRadius={0}
      title={title}
      animationPlacement={'bottom'}
      testid="pagingIcon"
      disabled={disabled}
    >
      {options.map((option) => (
        <SelectOption
          value={option.value}
          key={option.value}
          testid={option.value}
          title={titles![option.value]}
        >
          <OptionWrap>
            <option.Icon className="icon" />
            <span>{option.title}</span>
          </OptionWrap>
        </SelectOption>
      ))}
    </IconSelect>
  );
};

PageViewSelect.defaultProps = {
  defaultValue: 'normal_layout',
  titles: {},
  disabled: false,
  ...IconSelectDefaultProps,
};

export default PageViewSelect;
