import React from 'react';
import { FooterWrapper } from './styled';

export interface FooterLayoutProps {
  /**
   * 自定义高度
   */
  height?: number;
  /**
   * 背景色值
   */
  backgroundColor?: string;
}

export const FooterLayout: React.FC<FooterLayoutProps> = React.memo((props) => {
  const { children, height, backgroundColor } = props;
  return (
    <FooterWrapper backgroundColor={backgroundColor} height={height}>
      {children}
    </FooterWrapper>
  );
});

FooterLayout.defaultProps = {
  height: 28,
};
