import React, { useState } from 'react';
import { Dropdown } from '@ali/we-design';
import { DropdownAnimation } from '@ali/we-util';
import { ButtonProps } from './interface';
import { ButtonContainer } from './styled';

export const ButtonCardLayout: React.FC<ButtonProps> = (props) => {
  const {
    icon,
    children,
    type = 'link',
    onVisibleChange,
    overlay = <></>,
    overlayStyle,
    overlayClassName,
    onClick,
    getContainer,
  } = props;

  const [active, setActive] = useState(false);

  const handleClick = (e: React.MouseEvent) => {
    onClick && onClick(e);
  };

  const handleOnVisibleChange = (e: boolean) => {
    setActive(e);
    onVisibleChange && onVisibleChange(e);
  };

  return (
    <Dropdown
      trigger="hover"
      offsetY={4}
      getContainer={getContainer}
      overlay={
        <DropdownAnimation
          visible={active}
          overlayStyle={{
            borderRadius: '8px',
            padding: 0,
            ...overlayStyle,
          }}
        >
          {overlay}
        </DropdownAnimation>
      }
      overlayClassName={overlayClassName}
      overlayStyle={{
        border: 'none',
        boxShadow: 'none',
        background: 'none',
      }}
      onVisibleChange={handleOnVisibleChange}
    >
      <ButtonContainer onClick={handleClick} type={type}>
        {icon && <div className="button-icon-box">{icon}</div>}
        <div className="button-text-box">
          {children}
        </div>
      </ButtonContainer>
    </Dropdown>
  );
};
