import { useTranslate } from '@ali/we-util';
import * as React from 'react';
import { ToolbarTodoListNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';

export type TaskMenuButtonProps = IconMenuPropsOmit;

export const TaskMenuButton: React.FC<TaskMenuButtonProps> = React.memo(
  (props) => {
    const [visible, setVisible] = React.useState(false);

    const handleVisibleChange = (value: boolean) => {
      setVisible(value);
    };
    const t = useTranslate(props.locale);
    return (
      <IconMenu
        visible={visible}
        icon={<ToolbarTodoListNormal />}
        title={t('we_toolbar_task_list')}
        tooltip={t('we_toolbar_task_list')}
        onVisibleChange={handleVisibleChange}
        testid="TaskMenuButton"
        {...props}
      />
    );
  },
);

TaskMenuButton.defaultProps = {};
