import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconButtonPropsOmit } from '../../../common/interface';
import { IconButtonDefaultProps } from '../../../utils/defaultProps';
import IconButton from '../../../common/iconButton';
import { ToolbarPercentNormal } from '@ali/we-design';

export type PercentButtonProps = IconButtonPropsOmit;

export const PercentButton: React.FC<PercentButtonProps> = React.memo((props) => {
  const t = useTranslate(props.locale);

  return (
    <IconButton
      testid="percent_button"
      icon={<ToolbarPercentNormal />}
      title={t('we_toolbar_percent')}
      tooltip={t('we_toolbar_percent')}
      {...props}
    />
  );
});

PercentButton.defaultProps = { ...IconButtonDefaultProps };
