import { ArrowRightNormal, Dropdown } from '@ali/we-design';
import React, { ReactElement, useEffect, useMemo, useRef, useState } from 'react';
import { CardLayout, InlineToolbar } from '../../inlineToolbar';
import ResizeObserver from 'resize-observer-polyfill';
import {
  ClickAreaContainer,
  DesCription,
  IconContainer,
  PlaceholderWrapper,
  RightContainer,
  Title,
  TitleWrapper,
} from './styled';

import { MoreMenuConfiguration } from '../toolbar';

export interface ICardPlaceholder {
  height?: number;
  title?: string;
  desc?: string;
  testid?: string;
  icon?: ReactElement;
  // export 控制 title 样式，为导出时的样式
  type?: 'export' | 'normal';
  // 占位符是否被选中 选中有蓝色边框
  isSelected?: boolean;
  // inlintoolbar 配置项, 这里负责透传
  moreMenuConfig?: {
    options: MoreMenuConfiguration[][];
  };
  // 边缘点击事件
  onEdgeClick?: () => void;
  // 中心点击事件
  onCenterClick?: () => void;
  // 是否支持点击
  clickable?: boolean;
  // 弹层
  overlay?: React.ReactElement;
  // 控制弹层是否可见 + 是否 action 状态
  visible?: boolean;
  // 可见性变化回调
  onVisibleChange?: (e: boolean) => void;
  // 偏移 x
  offsetX?: number;
  // 偏移Y
  offsetY?: number;
  // 透传弹层样式
  overlayStyle?: React.CSSProperties;
}
export const CardPlaceholder: React.FC<ICardPlaceholder> = React.memo((props) => {
  const {
    height,
    title,
    desc,
    icon,
    type,
    overlay,
    isSelected,
    moreMenuConfig,
    clickable,
    visible,
    offsetX,
    offsetY,
    testid,
    overlayStyle,
    onEdgeClick,
    onCenterClick,
    onVisibleChange,
  } = props;

  const elRef = useRef<HTMLElement>();
  const [width, setWidth] = useState<number>(0);
  const [toolbarWidth, setToolbarWidth] = useState<number>(0);
  const [toolbarVisible, setToolbarVisible] = useState<boolean>(false);

  // overlay 宽度跟随
  useEffect(() => {
    if (!elRef.current) return;
    const myObserver = new ResizeObserver((entries: ResizeObserverEntry[]) => {
      entries.forEach((entry) => {
        const { width } = entry.contentRect;
        if (width === 0) return;
        const floorWidth = Math.floor(width);
        setWidth(floorWidth);
      });
    });
    myObserver.observe(elRef.current);
    return () => myObserver.disconnect();
  }, [elRef]);

  // 阻塞 overlay click 事件，否则会影响 onEdgeClick
  const overlayClone = useMemo(() => {
    return overlay ? (
      <div
        onClick={(e) => {
          e.preventDefault();
          e.stopPropagation();
        }}
      >
        {React.cloneElement(overlay)}
      </div>
    ) : null;
  }, [overlay]);

  const handleCenterClick = (e: React.MouseEvent<HTMLElement>) => {
    e.preventDefault();
    e.stopPropagation();
    clickable && onCenterClick && onCenterClick();
  };

  const handleEdgeClick = (_e: React.MouseEvent<HTMLElement>) => {
    clickable && onEdgeClick && onEdgeClick();
  };

  const handleVisibleChange = (e: boolean) => {
    onVisibleChange && onVisibleChange(e);
  };

  const handleToolbarWidthChange = (width: number) => {
    setToolbarWidth(width + 8 - 20);
  };

  const handleToolbarVisiable = (e: boolean) => {
    setToolbarVisible(e);
  };

  const defaultoverlayStyle = useMemo(
    () => ({
      border: 0,
      boxShadow: '0px 0px 1px rgb(0 0 0 / 24%), 0px 8px 24px rgb(0 0 0 / 16%)',
      background: 'white',
      width: width === 0 ? -1 : width,
      ...overlayStyle,
    }),
    [width, overlayStyle],
  );

  const isSingleLine = !desc;

  const placeholder = useMemo(
    () => (
      <ClickAreaContainer onClick={handleCenterClick}>
        {icon && (
          <IconContainer>
            {React.cloneElement(icon, { className: 'placeholder-icon' })}
          </IconContainer>
        )}
        <RightContainer isSingleLine={isSingleLine}>
          <TitleWrapper>
            <Title
              className="placeholder-title"
              isSingleLine={isSingleLine}
              toolbarWidth={toolbarWidth}
              toolbarVisible={toolbarVisible}
              type={type}
            >
              {title}
            </Title>
            {type === 'export' && <ArrowRightNormal />}
          </TitleWrapper>
          {!isSingleLine && <DesCription>{desc}</DesCription>}
        </RightContainer>
      </ClickAreaContainer>
    ),
    [title, icon, desc, toolbarWidth, toolbarVisible],
  );

  const toolbar = useMemo(
    () =>
      (moreMenuConfig && !visible ? (
        <InlineToolbar moreMenuConfig={moreMenuConfig} />
      ) : undefined),
    [moreMenuConfig],
  );

  const dropdown = useMemo(
    () => (
      <PlaceholderWrapper
        ref={elRef}
        data-testid={testid}
        height={height}
        isSelected={isSelected}
        clickable={clickable}
        visible={visible}
        onClick={handleEdgeClick}
      >
        {overlay ? (
          <Dropdown
            visible={visible}
            overlay={overlayClone}
            offsetX={-11 + offsetX!}
            offsetY={15 + offsetY!}
            overlayStyle={defaultoverlayStyle}
            trigger={'click'}
            onVisibleChange={handleVisibleChange}
          >
            {placeholder}
          </Dropdown>
        ) : (
          placeholder
        )}
      </PlaceholderWrapper>
    ),
    [height, placeholder, visible, overlay],
  );

  return clickable ? (
    <CardLayout
      onToolbarWidthChange={handleToolbarWidthChange}
      onHoverChange={handleToolbarVisiable}
      toolbar={toolbar}
      isSelected={isSelected}
      style={{ width: '100%' }}
    >
      {dropdown}
    </CardLayout>
  ) : (
    dropdown
  );
});

CardPlaceholder.defaultProps = {
  height: 65,
  offsetX: 0,
  offsetY: 0,
  clickable: true,
  type: 'normal',
};
