/* eslint-disable @typescript-eslint/no-explicit-any */
export const runNextTime = (fn: FrameRequestCallback): void => {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const ref =
    window.requestAnimationFrame ||
    (window as any).webkitRequestAnimationFrame ||
    (window as any).mozRequestAnimationFrame ||
    (window as any).oRequestAnimationFrame ||
    (window as any).msRequestAnimationFrame;
  if (ref) {
    ref(fn);
  } else {
    setTimeout(fn, 0);
  }
};

export const pick = <T extends any, K extends keyof T>(
  target: T,
  fields: K[],
): { [k in keyof T]: T[k] } => {
  const newTarget = {} as { [k in keyof T]: T[k] };
  for (let i = 0; i < fields.length; i += 1) {
    const key = fields[i];
    newTarget[key] = target[key];
  }
  return newTarget;
};

export const omit = <T extends Record<string, any>, K extends keyof T>(
  target: T,
  fields: K[],
): { [k in Exclude<keyof T, K>]: T[k] } => {
  const newTarget = { ...target };
  for (let i = 0; i < fields.length; i += 1) {
    const key = fields[i];
    delete newTarget[key];
  }
  return newTarget;
};

export const throttle = (callback: (...args: any[]) => void, delay: number) => {
  let canExecute = true;
  return (...args: any[]) => {
    if (canExecute) {
      canExecute = false;
      callback(...args);
      setTimeout(() => {
        canExecute = true;
      }, delay);
    }
  };
};

export const debounce = (callback: (...args: any[]) => void, delay: number) => {
  let debounceTimer: number;
  const handler = (...args: any[]): any => {
    clearTimeout(debounceTimer);
    debounceTimer = window.setTimeout(() => callback(...args), delay);
  };
  handler.cancel = () => {
    if (debounceTimer) {
      clearTimeout(debounceTimer);
      debounceTimer = 0;
    }
  };
  return handler;
};

export const uuid = ((): (() => string) => {
  let id = 0;
  return (): string => (+new Date()).toString(36) + id++;
})();

export const uniq = (targets: any[]): any[] => {
  if (!Array.isArray(targets)) {
    return targets;
  }
  return Array.from(new Set(targets));
};

export const uniqBy = (targets: any[], key: string): any[] => {
  if (!Array.isArray(targets)) {
    return targets;
  }
  const results: any[] = [];
  const resultValues: any[] = [];
  const targetKeyValues = targets.map((item) => item[key]);
  targetKeyValues.forEach((item, index) => {
    if (resultValues.indexOf(item) === -1) {
      resultValues.push(item);
      results.push(targets[index]);
    }
  });
  return results;
};

export const typeOf = (target: any) => {
  return Object.prototype.toString.call(target).slice(8, -1).toLowerCase();
};

export const isEqual = (target: any, ref: any): boolean => {
  if (typeOf(target) !== typeOf(ref)) {
    return false;
  }
  if (typeOf(target) !== 'array' && typeOf(target) !== 'object') {
    return target === ref;
  }
  if (Array.isArray(target)) {
    if (target.length !== ref.length) {
      return false;
    }
    return target.every((t) => ref.indexOf(t) !== -1);
  }
  const targetKeys = Object.keys(target);
  const refKeys = Object.keys(ref);
  if (targetKeys.length !== refKeys.length) {
    return false;
  }
  return targetKeys.every((k) => target[k] === ref[k]);
};

export const camelCase = (target: string): string => {
  return target.replace(/_([a-zA-Z])/g, (...args) => args[1].toUpperCase());
};

export const classnames = (...args: any[]): string => {
  const names: string[] = [];
  args.forEach((item) => {
    if (!item) return;
    if (typeof item === 'string') {
      names.push(item);
      return;
    }
    Object.keys(item).forEach((k) => {
      if (item[k]) names.push(k);
    });
  });
  return names.join(' ');
};

export const getAlignPointsAndOffsets = (placement: IPlacement, offset = 8) => {
  switch (placement) {
    case 'right':
      return { points: ['cl', 'cr'], offsets: [offset, 0] };
    case 'rightTop':
      return { points: ['tl', 'tr'], offsets: [offset, 0] };
    case 'rightBottom':
      return { points: ['bl', 'br'], offsets: [offset, 0] };
    case 'left':
      return { points: ['cr', 'cl'], offsets: [-offset, 0] };
    case 'leftTop':
      return { points: ['tr', 'tl'], offsets: [-offset, 0] };
    case 'leftBottom':
      return { points: ['br', 'bl'], offsets: [-offset, 0] };
    case 'top':
      return { points: ['bc', 'tc'], offsets: [0, -offset] };
    case 'topRight':
      return { points: ['br', 'tr'], offsets: [0, -offset] };
    case 'topLeft':
      return { points: ['bl', 'tl'], offsets: [0, -offset] };
    case 'bottom':
      return { points: ['tc', 'bc'], offsets: [0, offset] };
    case 'bottomRight':
      return { points: ['tr', 'br'], offsets: [0, offset] };
    case 'bottomLeft':
      return { points: ['tl', 'bl'], offsets: [0, offset] };
  }
};

// 检测是否为白色
export const isWhite = function (c: string | null) {
  if (!c) return false;
  return tinycolor(c).toHex() === 'ffffff';
};

// 检测是否透明
export const isTransparent = function (c: string) {
  if (!c) return false;
  return tinycolor(c).getAlpha() === 0;
};

export function get(source: object, path: string, defaultValue: any) {
  // a[3].b -> a.3.b
  const paths = path.replace(/\[(\d+)\]/g, '.$1').split('.');
  let result = source;
  for (const p of paths) {
    result = Object(result)[p];
    if (result === undefined) {
      return defaultValue;
    }
  }
  return result;
}
