import styled, { css, keyframes } from 'styled-components';

interface CardPlaceholderContainerProps {
  height: number | string;
  width?: number | string;
  isSelected?: boolean;
  clickable: boolean;
  type: 'normal' | 'export' | 'failure';
  delay: number;
}

const light = {
  level3_base_color: 'rgba(23, 26, 29, 0.4)',
  level1_base_color: 'rgba(23,26,29,0.6)',
  level2_base_color: 'rgba(23, 26, 29, 0.8)',
  hover_bg_color: 'rgba(126, 134, 142, 0.12)',
  border_normal: 'rgba(0, 0, 0, 0.16)',
  line_light_color: 'rgba(126, 134, 142, 0.16)',
  bg_doc_color: 'rgba(245,246,248,1)',
  bg_doc_light_color: 'rgba(245,246,248,1)',
  blue1_color: 'rgba(0,137,255,1)',
  bg_select: 'rgba(126,134,142,0.10)',
};

const changeBgColor = keyframes`
0% {
  background-color: ${light.line_light_color};
}

60% {
  background-color: ${light.line_light_color};
}

to {
  background-color: ${light.bg_select};
}
}`;

const selectBorder = css`
  border: 1px solid ${light.blue1_color};
  box-shadow: 0 0 0 3px rgba(0, 106, 254, 0.12);
`;
const normalBorder = css`
  border: 1px dashed ${light.border_normal};
`;

export const CardPlaceholderContainer = styled.div<CardPlaceholderContainerProps>`
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  height: ${({ height }) =>
    typeof height === 'number' ? height + 'px' : height};
  border-radius: 8px;
  color: ${light.level3_base_color};
  transition: ${({ delay }) => delay}ms;
  transition-timing-function: step-end;
  ${({ type }) =>
    type === 'failure'
      ? css`
          background: #f6f9fe;
        `
      : css`
          background: ${light.bg_doc_color};
        `}
  cursor: pointer;

  ${({ isSelected, type }) =>
    isSelected ? selectBorder : type === 'normal' && normalBorder}

  &:hover {
    .card-container {
      .placeholder-icon-box {
        font-size: 16px;
        svg {
          color: ${light.level2_base_color};
        }
      }
      .placeholder-title {
        color: ${light.level2_base_color};
      }
    }
    ${({ isSelected, type }) =>
      isSelected ? selectBorder : type === 'normal' && normalBorder}
  }

  ${({ type, isSelected }) =>
    !isSelected &&
    type === 'normal' &&
    css`
      &:hover {
        border: 1px dashed ${light.blue1_color};
      }
    `}

  ${({ isSelected }) =>
    !isSelected &&
    css`
      &.active {
        background: ${light.line_light_color};
        border: 1px dashed ${light.border_normal};
      }
    `}

  ${({ clickable }) =>
    !clickable &&
    css`
      pointer-events: none;
    `}

    &.select {
    animation: ${changeBgColor} 250ms cubic-bezier(0.39, 0.575, 0.565, 1)
      forwards;
    border-color: ${light.line_light_color};
    .card-container {
      .placeholder-title {
        color: ${light.level2_base_color};
      }
      .placeholder-icon-box {
        font-size: 16px;
        svg {
          color: ${light.level2_base_color};
        }
      }
    }

    ${({ isSelected }) => isSelected && selectBorder}
  }

  &:active {
    background-color: ${light.line_light_color};
    border-color: ${light.line_light_color};
    .card-container {
      .placeholder-icon-box {
        font-size: 16px;
        svg {
          color: ${light.level1_base_color};
          transition: 0;
        }
      }
      .placeholder-title {
        color: ${light.level1_base_color};
        transition: 0;
      }
    }
    ${({ isSelected }) => isSelected && selectBorder}
  }

  * {
    box-sizing: border-box;
  }

  .card-container {
    padding: 0 0 12px 12px;
    width: 100%;
    height: 100%;
    display: flex;

    .placeholder-icon-box {
      font-size: 16px;
      flex: none;
      width: 40px;
      height: 40px;
      ${({ type }) =>
        type !== 'failure' &&
        css`
          padding: 11px 10px;
          background: ${light.hover_bg_color};
        `}
      margin-top: 12px;
      margin-right: 12px;
      border-radius: 4px;

      img {
        object-fit: contain;
        width: 100%;
        height: 100%;
      }

      svg {
        transition: ${({ delay }) => delay}ms;
        transition-timing-function: step-end;
        font-size: 1.2em;
        color: ${light.level3_base_color};
      }
    }

    .placeholder-right {
      flex: 1 1 auto;
      height: 100%;
      overflow: hidden;
      display: flex;
      flex-direction: column;

      .placeholder-top {
        width: 100%;
        display: flex;
        flex: 1 1 auto;

        .placeholder-title {
          flex: 1 1 auto;
          padding-right: 12px;
          margin-top: 12px;
          align-self: center;
          transition: ${({ delay }) => delay}ms;
          transition-timing-function: step-end;

          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          font-style: normal;
          ${({ type }) =>
            type === 'normal'
              ? css`
                  font-size: 16px;
                `
              : type === 'failure'
              ? css`
                  font-weight: 500;
                  font-size: 12px;
                  color: #111f2c;
                `
              : css`
                  font-size: 14px;
                  line-height: 38px;
                  color: ${light.level1_base_color};
                `}
          line-height: 22px;
        }

        .placeholder-toolbar {
          margin: 4px 4px 0 0;
          flex: none;
        }
      }

      .placeholder-desc {
        flex: none;
        width: 100%;
        padding-right: 12px;
        overflow: hidden;
        display: inline-flex;

        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;

        ${({ type }) =>
          type === 'failure'
            ? css`
                color: #111f2c;
              `
            : css`
                color: rgba(17, 31, 44, 0.4);
              `}

        .desc-text {
          flex: 0 1 auto;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }

        .refresh {
          flex: none;
          margin-left: 5px;
          color: ${light.blue1_color};
          &:hover {
            color: rgba(0, 101, 213, 1);
          }
          cursor: pointer;
        }
      }
    }
  }
`;
