import React, { useContext, useMemo, useRef, useCallback } from 'react';
import { GroupWrapper as GroupWrapperEl } from './styled';
import {
  ToolbarContext,
  GroupWrapperContext,
  OverLayContext,
} from '../contexts';
import { GroupWrapperProps, LAYOUT_TYPE } from '../interface';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';


export const GroupWrapper: React.FC<GroupWrapperProps> = React.memo((props) => {
  const {
    layoutType,
    fineAdaptation,
    groupKey,
    hideBorder,
    clearPadding,
  } = props;
  const { mode, hideBlocks = [], isInOffScreenWrap } = useContext(
    ToolbarContext,
  );
  const { inOverlay, overlayHideBlocks } = useContext(OverLayContext);

  const el = useRef<HTMLElement>();

  const controller = useMemo<GroupWrapperProps>(
    () => ({
      fineAdaptation,
      groupKey,
    }),
    [fineAdaptation, groupKey],
  );

  const isHide = useCallback((): boolean => {
    if (inOverlay) {
      return overlayHideBlocks.indexOf(`${mode}-${groupKey}`) !== -1;
    } else {
      return hideBlocks.indexOf(`${mode}-${groupKey}`) !== -1;
    }
  }, [inOverlay, overlayHideBlocks, hideBlocks]);

  return (
    <GroupWrapperContext.Provider value={controller}>
      <CSSTransitionGroup
        transitionName="group-wrapper"
        transitionEnterTimeout={200}
        transitionLeaveTimeout={200}
        className="group-transition"
      >
        {!isHide() && (
          <GroupWrapperEl
            className="group-wrapper"
            data-group-key={!fineAdaptation ? `${mode }-${ groupKey}` : undefined}
            data-testid={
              !isInOffScreenWrap ? `${mode }_GroupWrapper` : undefined
            }
            data-mode={mode}
            ref={el}
            mode={mode}
            layoutType={layoutType}
            hideBorder={hideBorder}
            clearPadding={clearPadding}
          >
            {props.children}
          </GroupWrapperEl>
        )}
      </CSSTransitionGroup>
    </GroupWrapperContext.Provider>
  );
});

GroupWrapper.defaultProps = {
  layoutType: LAYOUT_TYPE.row,
  fineAdaptation: false,
};
