import React from 'react';
import { FooterBoxWrapper } from './styled';

export type AlignType = 'left' | 'right';
export interface FooterBoxProps {
  horizonAlign: AlignType;
}

export const FooterBox: React.FC<FooterBoxProps> = React.memo((props) => {
  const { horizonAlign, children } = props;
  return <FooterBoxWrapper align={horizonAlign}>{children}</FooterBoxWrapper>;
});
