import React, { useMemo, useCallback } from 'react';
import {
  TOptionTitle,
  ToolbarAlignCenterNormal,
  ToolbarAlignJustifyNormal,
  ToolbarAlignLeftNormal,
  ToolbarAlignRightNormal,
} from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import { IconSelectPropsOmit, Omit } from '../../../common/interface';
import useControlledValue from '../../../hooks/useControlledValue';
import { IconSelectDefaultProps } from '../../../utils/defaultProps';

import IconSelect from '../../../common/iconSelect';
import { TriggerWrap, OptionWrap } from './styled';

export type HorizontalAlign = 'left' | 'center' | 'right' | 'justify';

export interface IHorizontalAlignSelectProps
  extends IconSelectPropsOmit<HorizontalAlign> {
  /**
   * 默认的对齐方式，当设置了value，该项忽略
   */
  defaultValue?: HorizontalAlign;
  /**
   * 对齐方式
   */
  value?: HorizontalAlign;
  /**
   * 对齐方式修改时的回调函数
   */
  onChange?: (align: HorizontalAlign) => void;
  /**
   * 禁用的选项列表
   */
  disabledKeys?: HorizontalAlign[];
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
}

interface ISelectOption {
  title: string;
  value: HorizontalAlign;
  Icon: React.ComponentType<any>;
}

export const HorizontalAlignSelect: React.FC<Omit<
  IHorizontalAlignSelectProps,
  'bordered'
>> = (props) => {
  const {
    locale,
    onChange,
    disabledKeys,
    title,
    titles,
    active,
    showTitle,
    size,
    role,
    ...restProps
  } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  // 垂直对齐的选项
  const options: ISelectOption[] = useMemo(
    () => [
      {
        title: t('we_toolbar_align_left'),
        value: 'left',
        Icon: ToolbarAlignLeftNormal,
      },
      {
        title: t('we_toolbar_align_center'),
        value: 'center',
        Icon: ToolbarAlignCenterNormal,
      },
      {
        title: t('we_toolbar_align_right'),
        value: 'right',
        Icon: ToolbarAlignRightNormal,
      },
      {
        title: t('we_toolbar_align_justify'),
        value: 'justify',
        Icon: ToolbarAlignJustifyNormal,
      },
    ],
    [t]
  );

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback(
    (align?: string) => {
      const index = options.findIndex((option) => option.value === align);
      const currentOption = options[index];
      return (
        <TriggerWrap style={{ minWidth: 16 }}>
          {currentOption && <currentOption.Icon />}
        </TriggerWrap>
      );
    },
    [options]
  );

  const handleChange = useCallback(
    (e) => {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
    },
    [onChange, setValue]
  );

  return (
    <IconSelect
      value={value}
      onChange={handleChange}
      renderTrigger={renderTrigger}
      testid='HorizontalAlignSelect'
      title={title}
      active={active}
      size={size}
      showTitle={showTitle}
      disabledKeys={disabledKeys}
      role={role}
      {...restProps}
    >
      {options.map((option) => (
        <OptionWrap
          value={option.value}
          key={option.value}
          testid={option.value}
          disabled={disabledKeys?.includes(option.value)}
          title={titles![option.value]}
        >
          <option.Icon className='icon' />
          <span>{option.title}</span>
        </OptionWrap>
      ))}
    </IconSelect>
  );
};

HorizontalAlignSelect.defaultProps = {
  defaultValue: 'left',
  titles: {},
  ...IconSelectDefaultProps,
};
