import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconButtonPropsOmit } from '../../../common/interface';
import { IconButtonDefaultProps } from '../../../utils/defaultProps';
import IconButton from '../../../common/iconButton';
import { ToolbarCurrencyNormal } from '@ali/we-design';

export type CurrencyButtonProps = IconButtonPropsOmit;

export const CurrencyButton: React.FC<CurrencyButtonProps> = React.memo((props) => {
  const t = useTranslate(props.locale);

  return (
    <IconButton
      testid="currency_button"
      icon={<ToolbarCurrencyNormal />}
      title={t('we_toolbar_currency')}
      tooltip={t('we_toolbar_currency')}
      {...props}
    />
  );
});

CurrencyButton.defaultProps = { ...IconButtonDefaultProps };
