import { createContext } from 'react';

export const InlineToolbarContext = createContext<{
  // toolbar 是否折叠
  isFolded?: boolean;

  // toolbar 显示状态改变回调
  onVisibleChange?: (e: boolean) => void;

  // toolbar 是否应该显示
  isToolbarVisible?: boolean | null;

  // toolbar 初始状态是否显示
  toolbarDefault?: boolean;
}>({});
