import React, { useCallback, useMemo } from 'react';
import { SplitButtonProps } from '../interface';
import { SplitButton as SplitButtonUI, Tooltip } from '@ali/we-design';
import { useGetContextLocale, useTranslate } from '@ali/we-util';
import { ToolbarIconContext } from '@ali/we-icon';
import { ButtonWrap, LeftWrapper, TitleWrap } from './styled';
import { TOOL_SIZE } from '../../utils/consts';
import { classnames } from '../../utils';
import useVisibilitySensor from '../../hooks/useVisibilitySensor';
import { DisableWrap } from '../styled';
import OnIdle, { OnIdlePlaceholder } from '../onIdle';

export const SplitButton: React.FC<SplitButtonProps> = React.memo(
  React.forwardRef((props, ref: React.ForwardedRef<{}>) => {
    const {
      icon,
      title,
      showTitle,
      disabled,
      overlay,
      size,
      onClickLeft,
      activeLeft,
      onActiveChange,
      active,
      disabledTip,
      shortcutText,
      tooltip,
      locale,
      testid,
      extraNode,
      getContainer,
      ...restProps
    } = props;
    const contextLocale = useGetContextLocale();
    const t = useTranslate(contextLocale);

    const {
      el,
      id,
      isHide,
      fineAdaptation,
      groupKey,
      isInOffScreenWrap,
      mode,
    } = useVisibilitySensor(ref, testid);

    const onTextClick = useCallback(
      (e: React.MouseEvent<HTMLElement>) => {
        onClickLeft && onClickLeft(e);
      },
      [onClickLeft]
    );

    const styleProps = {
      size,
      active: activeLeft,
      locale: locale || contextLocale,
      showTitle,
    };

    const OverlayEl = useMemo(() => {
      const dataTestId = testid ? `overlay-${testid}` : '';
      if (!overlay) return <div data-testid={dataTestId} />;
      return (
        <OnIdle placeholder={<OnIdlePlaceholder />}>
          {React.cloneElement(overlay, { 'data-testid': dataTestId })}
        </OnIdle>
      );
    }, [overlay, testid]);

    let tooltipForButton = t(tooltip);
    if (shortcutText != null) {
      tooltipForButton += ` ${shortcutText}`;
    }

    const ToolbarIconMemo = useMemo(() => ({ size: size || 'medium' }), [size]);

    const titleNode = useMemo(() => {
      return <div>{title && typeof title !== 'string' ? title : t(title)}</div>;
    }, [t, title]);

    if (isHide) return null;

    return (
      <Tooltip
        disabled={!disabled}
        title={disabledTip}
        getContainer={getContainer}
      >
        <DisableWrap
          disabled={disabled}
          className={classnames('split-button-layout')}
        >
          <ButtonWrap
            ref={el}
            data-id={id}
            data-mode={mode}
            data-biz-key={fineAdaptation ? id : null}
            data-group-id={groupKey}
            {...styleProps}
            active={active}
            activeLeft={activeLeft}
            data-testid={isInOffScreenWrap ? '' : testid}
          >
            {showTitle && size === TOOL_SIZE.LARGE && (
              <TitleWrap {...styleProps} onClick={onTextClick}>
                <div>{titleNode}</div>
              </TitleWrap>
            )}
            <SplitButtonUI
              bordered={false}
              defaultActive={activeLeft}
              active={active}
              overlay={OverlayEl}
              className="split_btn_toolbar"
              onClickLeft={onTextClick}
              onActiveChange={onActiveChange}
              testid={isInOffScreenWrap ? '' : testid}
              getContainer={getContainer}
              {...restProps}
            >
              <ToolbarIconContext.Provider value={ToolbarIconMemo}>
                <Tooltip title={disabled ? disabledTip : tooltipForButton}>
                  {showTitle ? (
                    <LeftWrapper
                      {...styleProps}
                      className={classnames({ activeLeft })}
                    >
                      {icon}
                      {size !== TOOL_SIZE.LARGE && (
                        <div className="split_btn_left_toolbar">
                          {titleNode}
                        </div>
                      )}
                    </LeftWrapper>
                  ) : (
                    <LeftWrapper
                      {...styleProps}
                      className={classnames({ activeLeft })}
                    >
                      {icon}
                    </LeftWrapper>
                  )}
                </Tooltip>
              </ToolbarIconContext.Provider>
            </SplitButtonUI>
            {extraNode}
          </ButtonWrap>
        </DisableWrap>
      </Tooltip>
    );
  })
);

SplitButton.defaultProps = {
  size: 'medium',
};

export default SplitButton;
