import React from 'react';
import { AbstractCardLayoutProps, DescriptionConfig } from './interface';
import { Toolbar } from '../toolbar';
import { CardContainer } from '../container';
import { AbstractCardLayoutContainer, Skeleton } from './styled';
import { useTranslate } from '@ali/we-util';

export const AbstractCardLayout: React.FC<AbstractCardLayoutProps> = (
  props,
) => {
  const {
    mainActions,
    toolbarConfigs,
    icon,
    title,
    customLayout,
    line = 'double',
    isShowMainActionTitle = true,
    style,
    mainActionStyle,
    toolbarOffsetX = 4,
    toolbarOffsetY = 4,
    onClick,
    isSelected = false,
    toolbarVisible,
    thresholdWidth,
    lineCount = 1,
    desc = [],
    titleClick,
    background,
    img,
    color = '#F0F6FE',
    type = 'normal',
    failureText = '',
    refresh = false,
    refreshClick,
    locale,
    blur = true,
    getContainer,
    height,
    testid,
    buttonStyle = 'default',
  } = props;

  const t = useTranslate(locale);

  const stopPropagation = (e: React.MouseEvent) => {
    e.stopPropagation();
    e.preventDefault();
  };

  const handleTitleClick = (e: React.MouseEvent) => {
    if (titleClick) {
      stopPropagation(e);
      titleClick(e);
    }
  };

  const abstractCardLayoutRender = (
    icon: React.ReactNode,
    title: string | React.ReactNode,
    desc: DescriptionConfig[] = [],
    descNode: React.ReactNode,
    lineCount: number,
    line: 'single' | 'double' | 'multi',
    customLayout?: React.ReactNode,
  ) => {
    return (
      <AbstractCardLayoutContainer
        data-testid={testid ? `Abstract_card_container_${testid}` : undefined}
        toolbarOffset={[toolbarOffsetX, toolbarOffsetY]}
        className="card-layout-wrapper"
        line={line}
        blur={blur}
        lineCount={lineCount}
        type={type}
        style={
          isSelected
            ? {
                boxShadow:
                  '0px 0px 0px 1px #0089FF,0px 0px 0px 3px rgba(0, 106, 254, 0.12)',
                ...style,
              }
            : {
                ...style,
              }
        }
      >
        <CardContainer
          thresholdWidth={thresholdWidth}
          className="card-layout-container"
          toolbarVisible={toolbarVisible}
        >
          <div
            className="card-layout-top"
            style={background === 'color' ? { background: color } : {}}
          >
            {background === 'img' && (
              <div
                className="background-img"
                style={{
                  backgroundImage: `url(${img})`,
                }}
              />
            )}
            <div
              className="card-layout-head"
              onClick={onClick}
              style={{ height: height || 'auto' }}
            >
              <div className="card-layout-icon">{icon}</div>
              <div className="card-layout-right">
                <div className="card-layout-text">
                  <div
                    className={`card-layout-title ${
                      titleClick && 'link-style'
                    }`}
                    onClick={handleTitleClick}
                  >
                    {title}
                  </div>
                  <div
                    className="card-layout-toolbar"
                    onClick={stopPropagation}
                  >
                    {toolbarConfigs && (
                      <Toolbar
                        toolbarConfigs={toolbarConfigs}
                        getContainer={getContainer}
                        buttonStyle={buttonStyle}
                      />
                    )}
                  </div>
                </div>
                {line !== 'single' && (
                  <div className="card-layout-desc">
                    {type === 'normal'
                      ? desc.map((item: DescriptionConfig, index: number) => {
                          const style: React.CSSProperties = {
                            height: 20 * (item.line || 1),
                            lineClamp: item.line || 1,
                            overflowWrap:
                              item.line === 2 ? 'break-word' : 'normal',
                            wordBreak: item.line === 2 ? 'break-all' : 'normal',
                            WebkitLineClamp: item.line || 1,
                          };
                          return (
                            <div className="item" key={index}>
                              {item.onClick ? (
                                <a
                                  className="text"
                                  style={style}
                                  onClick={(e) => {
                                    stopPropagation(e);
                                    item.onClick && item.onClick(e);
                                  }}
                                >
                                  {item.text}
                                </a>
                              ) : (
                                <span className="text" style={style}>
                                  {item.text}
                                </span>
                              )}
                            </div>
                          );
                        })
                      : descNode}
                  </div>
                )}
              </div>
            </div>
            {line !== 'single' && customLayout && (
              <div onClick={onClick} className="card-layout-preview-img">
                {customLayout}
              </div>
            )}
          </div>
          {line !== 'single' &&
            type === 'normal' &&
            mainActions &&
            mainActions.length > 0 && (
              <div
                className="card-layout-man-action-container"
                style={{ ...mainActionStyle }}
              >
                {mainActions.map((item, index) => {
                  const { icon, title, onClick } = item;
                  return (
                    <div
                      className="main-action-box"
                      onClick={onClick}
                      key={index}
                    >
                      <div className="content-box">
                        {icon}
                        {isShowMainActionTitle && (
                          <span className="title">{title}</span>
                        )}
                      </div>
                    </div>
                  );
                })}
              </div>
            )}
          {type === 'loading' && mainActions && mainActions.length > 0 && (
            <div
              className="card-layout-man-action-container"
              style={{ ...mainActionStyle }}
            >
              {mainActions.map((_item, index) => {
                return (
                  <div className="main-action-box" key={index}>
                    <div className="content-box">
                      <Skeleton width={'63px'} height={'10px'} />
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </CardContainer>
      </AbstractCardLayoutContainer>
    );
  };

  const failureTextRender = (
    text: string,
    refresh: boolean,
    refreshClick: ((e: React.MouseEvent) => void) | undefined,
  ) => (
    <div className="card-layout-failure-box">
      <div className="text" style={{ color: 'rgba(17, 31, 44, 0.4)' }}>
        {text}
      </div>
      {refresh && (
        <div className="refresh" onClick={refreshClick}>
          {t('we_toolbar_refresh')}
        </div>
      )}
    </div>
  );

  switch (type) {
    case 'failure':
      return abstractCardLayoutRender(
        icon || failureImg,
        title,
        [],
        failureTextRender(failureText, refresh, refreshClick),
        1,
        'double',
        customLayout,
      );
    case 'loading':
      return abstractCardLayoutRender(
        icon || fileImg(),
        <Skeleton
          width={`${line === 'single' ? 220 : 99}px`}
          marginTop={'4px'}
          height={'12px'}
        />,
        [],
        // @ts-ignore
        Array.apply('', {
          length: height === undefined ? 1 : (height - 32) / 16,
        }).map((_, i) => (
          <Skeleton
            key={i}
            width={i % 2 ? '60%' : '80%'}
            marginTop={'8px'}
            height={'8px'}
          />
        )),
        height === undefined ? 1 : (height - 32) / 16,
        'multi',
        customLayout ? defaultCustomLayout : null,
      );
    case 'export':
      return <div />;
    default:
      return abstractCardLayoutRender(
        icon,
        title,
        desc,
        <></>,
        lineCount,
        line,
        customLayout,
      );
  }
};

AbstractCardLayout.defaultProps = {
  line: 'double',
  isShowMainActionTitle: true,
  toolbarOffsetX: 4,
  toolbarOffsetY: 4,
  isSelected: false,
  lineCount: 1,
  desc: [],
  color: '#F0F6FE',
  type: 'normal',
  failureText: '',
  refresh: false,
  blur: true,
  thresholdWidth: -Infinity,
};

const defaultCustomLayout = (
  <Skeleton width={'100%'} height={'147px'} borderRadius={'0'} />
);

const fileImg = () => (
  <img
    style={{
      position: 'relative',
      top: 1,
      width: 40,
      height: 40,
      objectFit: 'contain',
    }}
    src="https://img.alicdn.com/imgextra/i1/O1CN01Xv6Ewz1E3btiJKBZN_!!6000000000296-2-tps-118-118.png"
  />
);

const failureImg = (
  <img
    style={{
      position: 'relative',
      top: 1,
      width: 36,
      height: 36,
      objectFit: 'contain',
    }}
    src="https://img.alicdn.com/imgextra/i4/O1CN01rvr2PW1s7o9n7HXsG_!!6000000005720-2-tps-117-118.png"
  />
);
