import React from 'react';

import ColorSelectedIcon from './ColorSelectedIcon';
import ClearColorIcon from './ClearColorIcon';
import { ColorBoxWrapper } from './styled';

export interface IColorBoxArt {
  color: string;
  isClear: boolean;
  active: boolean;
  handleClick: React.MouseEventHandler;
  getContrastingColor: (color: string) => string;
}

const ColorBoxArt: React.FC<IColorBoxArt> = (props) => {
  const { handleClick, isClear, color, active, getContrastingColor } = props;
  return (
    <ColorBoxWrapper color={color} onClick={handleClick} isClear={isClear}>
      {isClear ? (
        <ClearColorIcon />
      ) : (
        active && <ColorSelectedIcon color={getContrastingColor(color)} />
      )}
    </ColorBoxWrapper>
  );
};

export default ColorBoxArt;
