import { useState, useRef, useCallback, useEffect } from 'react';

export default function useDelayedState<T = any>(initialValue: T) {
  const [state, setState] = useState(initialValue);
  const timer = useRef<any>(null);
  const setDelayedState = useCallback((value: T, delay?: number) => {
    if (timer.current) {
      clearTimeout(timer.current);
    }
    if (delay) {
      timer.current = setTimeout(() => setState(value), delay);
    } else {
      setState(value);
    }
  }, []);

  useEffect(() => {
    // 组件卸载时，保证 timer 被正确清理
    return () => {
      if (timer.current) {
        clearTimeout(timer.current);
      }
    };
  }, []);

  return [state, setDelayedState] as [T, typeof setDelayedState];
}
