import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconMenuPropsOmit } from '../../../../common/interface';
import IconMenu from '../../../../common/iconMenu';
import { ToolbarMoreNormal } from '@ali/we-design';

export type MoreButtonProps = IconMenuPropsOmit;

export const MoreButton: React.FC<MoreButtonProps> = React.memo((props) => {
  const { locale, animationPlacement } = props;

  const t = useTranslate(locale);

  return (
    <IconMenu
      testid="more_button"
      showArrow={false}
      offsetY={4}
      overlayStyle={{
        backgroundColor: 'transparent',
        boxShadow: 'none',
        border: 'none',
      }}
      icon={<ToolbarMoreNormal />}
      animationPlacement={animationPlacement}
      title={t('we_toolbar_more_button')}
      {...props}
    />
  );
});

MoreButton.defaultProps = {};
