import React, { useCallback, useMemo, useState } from 'react';
import { TextSelectProps } from '../interface';
import { Select as SelectUI, Tooltip, Dropdown } from '@ali/we-design';
import { useGetContextLocale, useTranslate } from '@ali/we-util';
import { ButtonWrap, TextWarp } from './styled';
import { TOOL_SIZE } from '../../utils/consts';

import { classnames } from '../../utils';
import useVisibilitySensor from '../../hooks/useVisibilitySensor';
import { DisableWrap } from '../styled';
import { ToolbarIconContext } from '@ali/we-icon';

type OmitProps = 'children' | 'onChange' | 'locale';

export interface ITextMenuProps<T = any>
  extends Omit<TextSelectProps<T>, OmitProps> {
  overlay?: React.ReactElement;
  onVisibleChange?: (v: boolean) => void;
}

export const TextMenu: React.FC<ITextMenuProps> = React.memo(
  React.forwardRef((props, ref: React.ForwardedRef<{}>) => {
    const {
      title,
      showTitle,
      disabled,
      size,
      active,
      disabledTip,
      renderTrigger,
      value,
      mode,
      bordered,
      rules,
      style,
      tooltip,
      shortcutText,
      width,
      testid,
      overlay,
      visible: overlayVisible,
      onVisibleChange,
      getContainer,
      zIndex,
      ...restProps
    } = props;

    const contextLocale = useGetContextLocale();
    const t = useTranslate(contextLocale);
    const [visible, setVisible] = useState(false);

    const {
      el,
      id,
      isHide,
      fineAdaptation,
      groupKey,
      isInOffScreenWrap,
    } = useVisibilitySensor(ref, testid);

    const styleProps = {
      size,
      active,
      bordered,
      showTitle,
      mode,
    };

    const titleNode = useMemo(() => {
      return <div>{title && typeof title !== 'string' ? title : t(title)}</div>;
    }, [t, title]);

    const makeTrigger = useCallback(
      (val) => (
        <>
          {renderTrigger && renderTrigger(val)}{' '}
          {showTitle && size !== TOOL_SIZE.LARGE ? (
            <TextWarp {...styleProps}>{titleNode}</TextWarp>
          ) : null}
        </>
      ),
      [renderTrigger, title, showTitle]
    );

    const finalTrigger =
      mode === 'search' || mode === 'input' ? undefined : makeTrigger;

    // 解决 父组件 box-sizing 失效问题
    const cssStyle = useMemo(
      () => ({
        width,
        height: size === TOOL_SIZE.SMALL ? 24 : 28,
        ...style,
      }),
      [style]
    );

    let tooltipForButton = t(tooltip);
    if (shortcutText != null) {
      tooltipForButton += ` ${shortcutText}`;
    }

    const handleOverLayVisibleChange = useCallback((v: boolean) => {
      setVisible(v);
      onVisibleChange && onVisibleChange(v);
    }, []);

    const openOverLay = useCallback(() => {
      setVisible(true);
      onVisibleChange && onVisibleChange(true);
    }, []);

    const triggerLayVisible = useCallback(() => {
      setVisible(!visible);
      onVisibleChange && onVisibleChange(!visible);
    }, [visible]);

    const ToolbarIconMemo = useMemo(() => ({ size: size || 'medium' }), [size]);

    if (isHide) return null;

    const visibleState =
      overlayVisible === undefined ? visible : overlayVisible;

    return (
      <ToolbarIconContext.Provider value={ToolbarIconMemo}>
        <Tooltip
          disabled={!disabled}
          title={disabledTip}
          getContainer={getContainer}
        >
          <DisableWrap
            disabled={disabled}
            className={classnames('text-select-layout')}
          >
            <Dropdown
              visible={visibleState}
              placement="bottomLeft"
              overlay={overlay || null}
              onVisibleChange={handleOverLayVisibleChange}
              getContainer={getContainer}
              zIndex={zIndex}
            >
              <ButtonWrap
                ref={el}
                data-id={id}
                onClick={triggerLayVisible}
                data-mode={mode}
                data-biz-key={fineAdaptation ? id : null}
                data-group-id={groupKey}
                {...styleProps}
              >
                {showTitle && size === TOOL_SIZE.LARGE && (
                  <TextWarp {...styleProps} onClick={openOverLay}>
                    {titleNode}
                  </TextWarp>
                )}
                {/* @ts-ignore */}
                <SelectUI
                  {...restProps}
                  className="select-toolbar"
                  value={value}
                  mode={mode}
                  style={cssStyle}
                  bordered={bordered}
                  renderTrigger={finalTrigger}
                  title={disabled ? disabledTip : tooltipForButton}
                  visible={visibleState}
                  rules={rules}
                  testid={isInOffScreenWrap ? '' : testid}
                  showErrorState={false}
                  getContainer={getContainer}
                />
              </ButtonWrap>
            </Dropdown>
          </DisableWrap>
        </Tooltip>
      </ToolbarIconContext.Provider>
    );
  })
);

TextMenu.defaultProps = {
  size: 'medium',
  renderTrigger: (k: any) => <>{k}</>,
};

export default TextMenu;
