import styled from 'styled-components';
import React, { useCallback, useMemo } from 'react';
import { SelectOption, useControlledValue, withTheme,
  ToolbarTableAscendingNormal,
  ToolbarTableDescendingNormal,
  ToolbarConstituencyAscendingNormal,
  ToolbarConstituencyDescendingNormal,
} from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import { IconSelectPropsOmit } from '../../../common/interface';
import IconSelect from '../../../common/iconSelect';


const OptionWrap = withTheme(styled.div`
  display: flex;
  align-items: center;
  span {
    margin-right: ${({ theme }) => theme.ui.marginSmall};
  }
`);

export const SORT_ORDERS = ['ascSheet', 'descSheet', 'ascRange', 'descRange'];
export type SortType = 'ascSheet' | 'descSheet' | 'ascRange' | 'descRange';

export interface ISortProps extends IconSelectPropsOmit<string> {
  onSortChange?: (e: SortType) => void;
  disabled?: boolean;
}

export const SortDropdown: React.FC<ISortProps> = React.memo((props) => {
  const { onSortChange, disabled, locale, ...restProps } = props;

  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  const options = useMemo(
    () => [
      {
        title: t('we_toolbar_table_ascending'),
        value: 'ascSheet',
        Icon: ToolbarTableAscendingNormal,
      },
      {
        title: t('we_toolbar_table_descending'),
        value: 'descSheet',
        Icon: ToolbarTableDescendingNormal,
      },
      {
        title: t('we_toolbar_constituency_ascending'),
        value: 'ascRange',
        Icon: ToolbarConstituencyAscendingNormal,
      },
      {
        title: t('we_toolbar_constituency_descending'),
        value: 'descRange',
        Icon: ToolbarConstituencyDescendingNormal,
      },
    ],
    [t],
  );

  // 该组件的trigger区域显示Icon+文字
  const renderTrigger = useCallback(
    (selectedView?: string) => {
      const index = options.findIndex((option) => option.value === selectedView);
      const currentOption = options[index] || options[0];
      return <>{currentOption && <currentOption.Icon />}</>;
    },
    [options],
  );

  const handleChange = useCallback(
    (e: SortType) => {
      setValue(e);
      onSortChange && onSortChange(e);
    },
    [onSortChange],
  );

  return (
    <IconSelect
      disabled={disabled}
      value={value}
      title={t('we_toolbar_sort')}
      renderTrigger={renderTrigger}
      selectable={false}
      onChange={handleChange}
      {...restProps}
    >
      {options.map((option) => (
        <SelectOption value={option.value} key={option.value}>
          <OptionWrap>
            <option.Icon className="icon" />
            <span>{option.title}</span>
          </OptionWrap>
        </SelectOption>
      ))}
    </IconSelect>
  );
});
