import React, { useMemo, useCallback } from 'react';
import { SelectOption, TOptionTitle } from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import { TextSelectPropsOmit } from '../../../common/interface';
import useControlledValue from '../../../hooks/useControlledValue';
import TextSelect from '../../../common/textSelect';
import { SelectTrigger } from './styled';

export interface ITextTypeSelectOption {
  title: string;
  type: string;
  fontSize: number | string;
  fontWeight: number;
  lineHeight: number | string;
}

export type TextType =
  | 'paragraph'
  | 'heading-1'
  | 'heading-2'
  | 'heading-3'
  | 'heading-4'
  | 'heading-5'
  | 'heading-6';

export interface ITextTypeSelectProps extends TextSelectPropsOmit<TextType> {
  /**
   * 默认的文本类型，如设置了 value，则该属性忽略
   */
  defaultValue?: TextType;
  /**
   * 文本类型
   */
  value?: TextType;
  /**
   * 设置
   */
  locale?: string;
  /**
   * 文本类型变化回调函数，type TextType
   */
  onChange?: (type: TextType) => void;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 选择器下方标题
   */
  title?: string | React.ReactNode;
}

// eslint-disable-next-line max-lines-per-function
const TextTypeSelect: React.FC<ITextTypeSelectProps> = (props) => {
  const {
    locale,
    onChange,
    bordered,
    disabled,
    titles,
    title,
    role,
    ...restProps
  } = props;
  const translate = useTranslate(locale);

  // 文字类型列表：正文、标题1-6
  const options = useMemo<ITextTypeSelectOption[]>(
    () => [
      {
        title: translate('we_toolbar_font_family_ts_Body'),
        type: 'paragraph',
        fontSize: 14,
        fontWeight: 400,
        lineHeight: '1.45',
      },
      {
        title: translate('we_toolbar_font_family_ts_Title_1'),
        type: 'heading-1',
        fontSize: '20pt',
        fontWeight: 500,
        lineHeight: '1.45',
      },
      {
        title: translate('we_toolbar_font_family_ts_Title_2'),
        type: 'heading-2',
        fontSize: '16pt',
        fontWeight: 500,
        lineHeight: '1.45',
      },
      {
        title: translate('we_toolbar_font_family_ts_Title_3'),
        type: 'heading-3',
        fontSize: '14pt',
        fontWeight: 500,
        lineHeight: '1.45',
      },
      {
        title: translate('we_toolbar_font_family_ts_Title_4'),
        type: 'heading-4',
        fontSize: '12pt',
        fontWeight: 500,
        lineHeight: '1.45',
      },
      {
        title: translate('we_toolbar_font_family_ts_Title_5'),
        type: 'heading-5',
        fontSize: '12pt',
        fontWeight: 500,
        lineHeight: '1.45',
      },
      {
        title: translate('we_toolbar_font_family_ts_Title_6'),
        type: 'heading-6',
        fontSize: '12pt',
        fontWeight: 500,
        lineHeight: '1.45',
      },
    ],
    [translate],
  );

  // 受控模式处理
  // eslint-disable-next-line prefer-const
  let [value, setValue] = useControlledValue(props.value, props.defaultValue);

  /**
   * 文字类型变化回调函数
   *
   * @param {TextType} type
   * @return {void}
   */
  const handleChange = useCallback(
    (type: TextType) => {
      if (onChange) {
        onChange(type);
      }
      setValue(type);
    },
    [setValue, onChange],
  );

  /**
   * 渲染下拉选择的显示区
   *
   * @param {TextType} type
   * @return {React.ReactNode}
   */
  const renderTrigger = useCallback(
    (type = 'paragraph'): React.ReactElement => {
      const index = options.findIndex((op) => op.type === type);
      const currentOption = options[index];
      return (
        <SelectTrigger>
          {currentOption?.title || options[0].title}
        </SelectTrigger>
      );
    },
    [options],
  );

  return (
    <TextSelect
      title={title}
      value={value as TextType}
      // @ts-ignore
      onChange={handleChange}
      renderTrigger={renderTrigger}
      bordered={bordered}
      disabled={disabled}
      testid="TextTypeSelect"
      role={role}
      selectable
      {...restProps}
    >
      {
        // 目前列表要求只显示 1-4
        options.slice(0, 5).map((option) => {
          return (
            <SelectOption
              key={option.type}
              value={option.type}
              testid={option.type}
              title={titles![option.type]}
              role={option.type}
            >
              <span
                style={{
                  fontSize: option.fontSize,
                  fontWeight: option.fontWeight,
                  lineHeight: option.lineHeight,
                }}
              >
                {option.title}
              </span>
            </SelectOption>
          );
        })
      }
    </TextSelect>
  );
};

TextTypeSelect.defaultProps = {
  onChange: (): void => {},
  defaultValue: 'paragraph',
  titles: {},
  selectable: true,
};

export default TextTypeSelect;
