/* eslint-disable max-lines-per-function */
import React, { useState, useCallback, useMemo } from 'react';
import { StrokeNormal } from '@ali/we-icon';
import { SplitButtonOmit } from '../../../common/interface';
import SplitButton from '../../../common/splitButton';
import BorderPickerOverlay, { IBorderPickerOverlayProps } from './BorderPickerOverlay';

interface IBorderPickerProps extends SplitButtonOmit, IBorderPickerOverlayProps {
  /**
   * 国际化语言
   */
  locale?: string;
  /**
   * 点击左侧按钮的回调
   */
  onClick?: () => void;
  /**
   * 工具的 title 文案
   */
   title?: string | React.ReactNode;
   /**
    * 工具的 tooltip 文案
    */
   tooltip?: string | React.ReactNode;
  /**
   * 右侧悬浮窗是否显示
   */
  onActiveChange?: (visible: boolean) => void;
  /**
   * 自定义滚动容器
   */
  getContainer?: () => HTMLElement;
}

const DEFAULT_COLOR = 'inherit';

export const BorderPicker: React.FC<IBorderPickerProps> = (props) => {
  const {
    color,
    onColorPick,
    lineSize,
    defaultLineSize,
    lineSizeMap,
    onLineSizeChange,
    lineStyle,
    defaultLineStyle,
    lineStyleMap,
    onLineStyleChange,
    locale,
    disabled,
    title,
    tooltip,
    onClick,
    size,
    showAutomatic,
    automaticColor,
    clearColor,
    picker,
    testid,
    onActiveChange,
    getContainer,
    role,
    showToggleSelect,
    onSelectValueChange,
    showGradient,
    getColorPanelMode,
    doubleGapRowIndex,
  } = props;

  const [dropdownVisible, setDropdownVisible] = useState(false);
  const [sizeSelectVisible, setSizeSelectVisible] = useState(false);
  const [styleSelectVisible, setStyleSelectVisible] = useState(false);

  const handleDropdownVisible = useCallback(
    (e: boolean) => {
      if (!e && (sizeSelectVisible || styleSelectVisible)) {
        return;
      }
      onActiveChange && onActiveChange(e);
      setDropdownVisible(e);
    },
    [setDropdownVisible, onActiveChange, sizeSelectVisible, styleSelectVisible],
  );

  const handleClick = useCallback(() => {
    if (onClick) {
      onClick();
    }
  }, [onClick]);

  const pathColors = useMemo(() => ([
    // 当选中颜色为白色时，选中边框的外边缘为黑色，否则选中边框外边缘与内部颜色一致
    !color
      ? DEFAULT_COLOR
      : color,
    DEFAULT_COLOR,
  ]), [color]);

  const visible = dropdownVisible || sizeSelectVisible || styleSelectVisible;

  const handleLineSizeChange = React.useCallback((change: string) => {
    onLineSizeChange && onLineSizeChange(change);
  }, [onLineSizeChange]);

  const handleLineStyleChange = React.useCallback((change: string) => {
    onLineStyleChange && onLineStyleChange(change);
  }, [onLineStyleChange]);

  return (
    <SplitButton
      overlay={
        <BorderPickerOverlay
          color={color}
          onColorPick={onColorPick}
          showAutomatic={showAutomatic}
          automaticColor={automaticColor}
          clearColor={clearColor}
          picker={picker}
          lineSizeMap={lineSizeMap}
          lineSize={lineSize}
          defaultLineSize={defaultLineSize}
          onLineSizeChange={handleLineSizeChange}
          sizeSelectVisible={sizeSelectVisible}
          onSizeSelectVisibleChange={setSizeSelectVisible}
          lineStyleMap={lineStyleMap}
          lineStyle={lineStyle}
          defaultLineStyle={defaultLineStyle}
          onLineStyleChange={handleLineStyleChange}
          styleSelectVisible={styleSelectVisible}
          onStyleSelectVisibleChange={setStyleSelectVisible}
          locale={locale}
          showToggleSelect={showToggleSelect}
          getColorPanelMode={getColorPanelMode}
          showGradient={showGradient}
          onSelectValueChange={onSelectValueChange}
          doubleGapRowIndex={doubleGapRowIndex}
        />
      }
      active={visible}
      onClickLeft={handleClick}
      onActiveChange={handleDropdownVisible}
      disabled={disabled}
      testid={testid || 'BorderPicker'}
      title={title}
      tooltip={tooltip}
      size={size}
      icon={<StrokeNormal pathColors={pathColors} />}
      getContainer={getContainer}
      role={role}
    />
  );
};
