import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconMenuPropsOmit } from '../../../common/interface';
import IconMenu from '../../../common/iconMenu';
import { ToolbarLockNormal } from '@ali/we-design';

export type LockMenuButtonProps = IconMenuPropsOmit & {
  icon?: React.ReactNode;
};

export const LockMenuButton: React.FC<LockMenuButtonProps> = React.memo(
  (props) => {
    const t = useTranslate(props.locale);

    return (
      <IconMenu
        testid="LockMenuButton"
        icon={props.icon || <ToolbarLockNormal />}
        title={t('we_toolbar_lock')}
        tooltip={t('we_toolbar_lock')}
        {...props}
      />
    );
  },
);

LockMenuButton.defaultProps = {};
