import { useTranslate } from '@ali/we-util';
import * as React from 'react';
import { ToolbarFilterNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';

export type FilterMenuButtonProps = IconMenuPropsOmit & {
  icon?: React.ReactNode;
};

export const FilterMenuButton: React.FC<FilterMenuButtonProps> = React.memo(
  (props) => {
    const [visible, setVisible] = React.useState(false);

    const handleVisibleChange = (value: boolean) => {
      setVisible(value);
    };
    const t = useTranslate(props.locale);

    return (
      <IconMenu
        visible={visible}
        icon={props.icon || <ToolbarFilterNormal />}
        title={t('we_toolbar_filter_button')}
        tooltip={t('we_toolbar_filter_button')}
        onVisibleChange={handleVisibleChange}
        testid="FilterMenuButton"
        {...props}
      />
    );
  }
);

FilterMenuButton.defaultProps = {};
