import { useRef, useEffect } from 'react';
import { elementContain } from '../utils/dom';

export default <T extends HTMLElement = HTMLElement>(
  handler: any | undefined,
  on = true,
  eventName?: 'mousedown' | 'click',
  box?: HTMLElement,
) => {
  const ref = useRef<T>(null);
  const callback = useRef<any>(() => {});
  callback.current = handler || (() => {});

  useEffect(() => {
    /**
     * 点击处理
     */
    const handleClickBody = (event: MouseEvent): void => {
      const isOuter =
        !ref.current ||
        !elementContain(box ? box : ref.current, event.target as HTMLElement);
      if (isOuter) {
        callback.current(event);
      }
    };
    if (on) {
      document.body.addEventListener(
        eventName || 'mousedown',
        handleClickBody,
        { capture: true },
      );
    }
    return () => {
      if (on) {
        document.body.removeEventListener(
          eventName || 'mousedown',
          handleClickBody,
          { capture: true },
        );
      }
    };
  }, [on, eventName]);

  return ref;
};
