import React, { useEffect, useCallback, useRef, useState } from 'react';
import { SpaceBetweenLayoutOffScreenProps, BlockType } from '../interface';
import {
  OffScreenWrapper as OffScreenWrapperEl,
  CenterWrapper,
  LeftWrapper,
} from './styled';
import { GroupWrapperContext } from '../contexts';

export const SpaceBetweenLayoutOffScreenWrapper = React.memo(
  (props: SpaceBetweenLayoutOffScreenProps) => {
    const { leftModule, centerModule, onBlockChange, needGetPositions } = props;
    const [needRender, setNeedRender] = useState<boolean>(false);
    const outElRef = useRef<Element>();

    useEffect(() => {
      setNeedRender(true);
    }, [needGetPositions]);

    useEffect(() => {
      let timer: number;
      if (outElRef.current && needRender) {
        onBlockChange(registerEl(outElRef.current));
        timer = window.setTimeout(() => {
          // setNeedRender(false)
        }, 500);
      }
      return () => {
        timer && clearTimeout(timer);
      };
    }, [needRender]);

    const registerEl = useCallback((outElRef): BlockType[] => {
      const blocks: BlockType[] = [];
      const bizEls: NodeListOf<HTMLElement> = outElRef.querySelectorAll(
        '*[data-biz-key]',
      );
      bizEls.forEach((el: HTMLElement) => {
        if (el.clientWidth !== 0) {
          const id = el.getAttribute('data-biz-key') || '';
          const groupId = el.getAttribute('data-group-id') || '';
          const val: BlockType = {
            id,
            offset:
              el.offsetLeft + el.clientWidth + (groupId === 'center' ? 80 : 0),
            elType: 'biz',
            groupId,
            width: el.clientWidth,
          };
          blocks.push(val);
        }
      });
      return blocks;
    }, []);

    return (
      <OffScreenWrapperEl ref={outElRef}>
        {needRender && (
          <>
            <GroupWrapperContext.Provider
              value={{ fineAdaptation: true, groupKey: 'left' }}
            >
              <LeftWrapper data-group-key={'left'}>{leftModule}</LeftWrapper>
            </GroupWrapperContext.Provider>

            <GroupWrapperContext.Provider
              value={{ fineAdaptation: true, groupKey: 'center' }}
            >
              <CenterWrapper data-group-key="center">
                {centerModule}
              </CenterWrapper>
            </GroupWrapperContext.Provider>
          </>
        )}
      </OffScreenWrapperEl>
    );
  },
);
