import React, { useMemo, useState, useEffect, useCallback } from 'react';
import {
  MoreMenuConfiguration,
  ToolbarProps,
  ToolbarConfigsProps,
  ToolbarCardType,
} from './interface';
import {
  StyleIcon,
  AbstractSmallIcon,
  PreviewSmallIcon,
  TextSmallIcon,
} from './styleSelectIcon';
import {
  Menu,
  MenuItem,
  MenuDivider,
  SubMenu,
  ToolbarMoreNormal,
  MenuGroupNormal,
  ToolbarArrowDownTriangleNormal,
  ArrowDownLineNormal,
  EMenuMode,
  SelectGallery,
  RefreshNormalNormal,
  ToolbarDeleteNormal,
  ToolbarCopyNormal,
  ToolbarCutNormal,
  ToolbarDownloadNormal,
  OpenNormal,
} from '@ali/we-design';
import { IconButton, IconMenu } from '../../../common';
import {
  InlineToolbarBlock,
  InlineToolbarContainer,
  MenuTitleIconBox,
  SelectGalleryBox,
  StyleSelectFontBox,
  IconBox,
  TitleWithLeftMargin,
  StyleSelectIconContainer,
  MenuTriggerButton,
} from './styled';
import { useTranslate } from '@ali/we-util';
import { NopicNormal } from '@ali/we-icon';
import { InlineToolbarContext } from './inlineToolbarContext';

// 根据 toolbar 按钮类型转 icon
// 依次为：风格展示、更多按钮、自定义菜单、一般按钮
const typeToIcon = {
  style: <StyleIcon />,
  more: <ToolbarMoreNormal />,
  menu: <MenuGroupNormal />,
  button: <ToolbarArrowDownTriangleNormal />,
};

// 根据 menu 的 name 转 icon
// 这是 toolbar「更多」功能中的 menu
const nameToIcon = {
  download: <ToolbarDownloadNormal />,
  open: <OpenNormal />,
  refresh: <RefreshNormalNormal />,
  delete: <ToolbarDeleteNormal />,
  copy: <ToolbarCopyNormal />,
  cut: <ToolbarCutNormal />,
};

// 风格展示，根据类型转 icon
// 依次为：文本、摘要卡片、预览卡片
const styleTypeToIcon = {
  text: <TextSmallIcon />,
  abstract: <AbstractSmallIcon />,
  preview: <PreviewSmallIcon />,
  artCard: <PreviewSmallIcon />, // TODO 待更新为 art 所需 icon
};

const clearStyle: React.CSSProperties = {
  width: 'unset',
  height: 'unset',
  padding: 'unset',
  margin: 'unset',
  minWidth: 'unset',
  borderRadius: 'unset',
};

function FallbackImage({ src, alt }: { src: string; alt?: string }) {
  const [loadFailed, setLoadState] = useState(false);

  const handleOnError = useCallback(() => {
    setLoadState(true);
  }, []);

  return loadFailed ? (
    <NopicNormal style={{ width: '40px', height: '40px', color: '#9FA1A3' }} />
  ) : (
    <img src={src} onError={handleOnError} alt={alt} />
  );
}

export const Toolbar: React.FC<ToolbarProps> = (props) => {
  const {
    isFolded: isFoldedFromContext,
    onVisibleChange,
    isToolbarVisible,
    toolbarDefault,
  } = React.useContext(InlineToolbarContext);
  const {
    toolbarConfigs: toolbar,
    locale,
    isFolded = isFoldedFromContext,
    getContainer,
    buttonStyle = 'default',
  } = props;
  const t = useTranslate(locale);
  // 风格选择状态
  const [styleSelectValue, setStyleSelectValue] = useState(
    Number(toolbar?.find((item) => item.type === 'style')?.defaultValue),
  );
  const [className, setClassName] = useState('');
  const [display, setDisplay] = useState<null | 'none' | 'block'>(null);

  // 风格切换中，大图资源
  const nameToTextAndImage = useMemo(
    () => ({
      text: {
        title: t('we_toolbar_text'),
        icon:
          'https://img.alicdn.com/imgextra/i4/O1CN01SVRR9O1cHOarEhMi5_!!6000000003575-2-tps-180-140.png',
      },
      abstract: {
        title: t('we_toolbar_abstract'),
        icon:
          'https://img.alicdn.com/imgextra/i2/O1CN01fPfwUo1RSskY9PviT_!!6000000002111-2-tps-180-140.png',
      },
      preview: {
        title: t('we_toolbar_preview'),
        icon:
          'https://img.alicdn.com/imgextra/i1/O1CN01I7NSjK1RPfypgAwaG_!!6000000002104-2-tps-180-140.png',
      },
      artCard: {
        title: t('we_toolbar_artCard'),
        icon:
          'https://img.alicdn.com/imgextra/i4/O1CN01FpZNeM1uZPJXxerke_!!6000000006051-2-tps-378-278.png',
      },
    }),
    [],
  );

  if (!toolbar) return <></>;

  // 根据 MoreMenuConfiguration[][] 进行「更多菜单」渲染
  const moreMenuRender = (menu: MoreMenuConfiguration[][]) => {
    const menuItemList: React.ReactElement[] = [];
    menu.forEach((items: MoreMenuConfiguration[], index: number) => {
      // 如果 toolbar 折叠，即其他功能需要整合进「更多菜单」中
      // 需要在上方添加一根分割线 <MenuDivider/>
      if (index + Number(isFolded) > 0 && toolbar.length > 1) {
        menuItemList.push(<MenuDivider key={`divider-${index}`} />);
      }
      // 对于每一组
      items.forEach((item: MoreMenuConfiguration, innerIndex: number) => {
        // 获取 name
        const { name = 'refresh' } = item;
        // 如果 name 为预定菜单选项
        // 则为其提供标题、icon 等信息
        const {
          title = t(`we_toolbar_${name}`),
          icon = nameToIcon[name],
          onClick,
          testId,
          role,
        } = item;
        menuItemList.push(
          <MenuItem
            key={`more-menu-${index}-${innerIndex}`}
            menuKey={`more-menu-${index}-${innerIndex}`}
            icon={icon}
            testid={testId}
            role={role}
            onClick={onClick}
          >
            {title}
          </MenuItem>,
        );
      });
    });
    return menuItemList;
  };

  // 自定义菜单渲染
  const normalMenuRender = (index: number, toolbar: ToolbarConfigsProps) => {
    // type === 'menu'
    const { type } = toolbar;
    const { title, icon = typeToIcon[type], overlay, active, testId } = toolbar;
    return (
      <MenuTriggerButton key={`submenu-${index}`} active={active}>
        <SubMenu
          key={`submenu-${index}`}
          overlayStyle={{ padding: 0 }}
          title={<TitleWithLeftMargin>{title}</TitleWithLeftMargin>}
          icon={icon}
        >
          <MenuItem
            key={`menu-{index}`}
            testid={testId}
            hoverable={false}
            menuKey={`menu-${index}`}
            style={{ ...clearStyle }}
          >
            {overlay}
          </MenuItem>
        </SubMenu>
      </MenuTriggerButton>
    );
  };

  // 风格切换菜单渲染
  const styleMenuRender = (index: number, toolbar: ToolbarConfigsProps) => {
    const { type } = toolbar;
    const {
      icon = typeToIcon[type],
      onChange,
      options,
      optionsTestId,
      testId,
    } = toolbar;
    // 若没有提供 options 选项，也不进行渲染
    if (!options || options.length < 1) {
      return <></>;
    }
    // 结构：SubMenu - MenuItem - SelectGallery（风格切换组件）
    return (
      <SubMenu
        key={`submenu-${index}`}
        icon={
          <MenuTitleIconBox>
            {icon}
            <ArrowDownLineNormal />
          </MenuTitleIconBox>
        }
        testid={testId}
        title=''
        overlayStyle={{ padding: 0 }}
      >
        <MenuItem
          key='style-select'
          hoverable={false}
          menuKey='style-select'
          style={{ ...clearStyle }}
        >
          <SelectGallery
            groups={[
              {
                title: t('we_toolbar_style_select'),
                showDivider: false,
                items: options.map((name: ToolbarCardType, index: number) => {
                  const { title, icon } = nameToTextAndImage[name];
                  // 自定义组件（风格切换每一个 item）
                  const node = (
                    <SelectGalleryBox key={index}>
                      <StyleSelectIconContainer>
                        <FallbackImage src={icon} />
                      </StyleSelectIconContainer>
                      <StyleSelectFontBox>{title}</StyleSelectFontBox>
                    </SelectGalleryBox>
                  );
                  return {
                    id: index,
                    node,
                    testID: optionsTestId && optionsTestId[name],
                  };
                }),
              },
            ]}
            style={{
              height: 151,
              border: 'none',
              boxShadow: 'none',
              padding: '2px 12px',
            }}
            itemsPerRow={options.length}
            value={styleSelectValue}
            onChange={(id) => {
              setStyleSelectValue(id);
              onChange && onChange(id);
            }}
          />
        </MenuItem>
      </SubMenu>
    );
  };

  const toolbarList: React.ReactElement[] = [];
  // 如果折叠或者只有一个按钮，那么直接显示，否则：
  if (!isFolded || toolbar.length < 2) {
    for (let i = 0; i < toolbar.length; i++) {
      // 类型：button（一般 button），menu（自定义菜单），style（风格切换），more（更多菜单）
      const { type } = toolbar[i];
      // toolbar 除一般 button 外，用 menu 实现
      // button 独立于 menu 外
      if (type === 'button') {
        const {
          title,
          icon = typeToIcon[type],
          onClick,
          active,
          testId,
        } = toolbar[i];
        toolbarList.push(
          <IconButton
            key={`button-${i}`}
            icon={icon}
            active={active}
            onClick={onClick}
            tooltip={title}
            testid={testId}
          />,
        );
      } else {
        // 非 button 组件用 menu 实现
        const menuList: React.ReactElement[] = [];
        let j = i;
        for (; j < toolbar.length && toolbar[j].type !== 'button'; j++) {
          const { type } = toolbar[j];
          if (type === 'menu') {
            menuList.push(normalMenuRender(j, toolbar[j]));
          } else if (type === 'style') {
            menuList.push(styleMenuRender(j, toolbar[j]));
          } else if (type === 'more') {
            const { menu } = toolbar[j];
            if (!menu) {
              continue;
            }
            menuList.push(
              <SubMenu
                menuKey='more-menu'
                key='more-menu'
                icon={<ToolbarMoreNormal />}
                title=''
              >
                {moreMenuRender(menu)}
              </SubMenu>,
            );
          }
        }
        i = j + 1;
        // TODO: 给 Menu 添加 getContainer
        toolbarList.push(
          <Menu
            getContainer={getContainer}
            key={`menu-${i}`}
            onSubmenuOpenedKeysChange={(keys: string[]) =>
              onVisibleChange && onVisibleChange(Boolean(keys.length > 0))
            }
            className='toolbar-menu'
            style={{ height: 28 }}
            mode={EMenuMode.horizontal}
            destroyOverlayWhenInvisible
          >
            {menuList}
          </Menu>,
        );
      }
    }
  } else {
    // 只渲染一个按钮
    // 1. 只有一个按钮
    // 2. 折叠
    let menuItemList: React.ReactElement[] = [];
    toolbar.forEach((item, index) => {
      const { type } = item;
      if (type === 'more') {
        const { menu } = item;
        if (!menu) {
          return;
        }
        menuItemList = menuItemList.concat(moreMenuRender(menu));
      } else if (type === 'style') {
        // 与非折叠状态不同
        // style 作为 SubMenu 二级菜单渲染
        const { options, onChange, optionsTestId, testId } = item;
        if (!options || options.length < 1) {
          return;
        }
        menuItemList.push(
          <SubMenu
            icon={
              <IconBox size={20}>
                <StyleIcon />
              </IconBox>
            }
            testid={testId}
            menuKey='style_select'
            key='style_select'
            title={t('we_toolbar_style_select')}
            showSelect
          >
            {options.map((item, index) => {
              return (
                <MenuItem
                  icon={styleTypeToIcon[item]}
                  testid={optionsTestId && optionsTestId[item]}
                  menuKey={index + ''}
                  key={index}
                  onClick={() => {
                    onChange && onChange(index);
                    setStyleSelectValue(index);
                  }}
                >
                  {t(`we_toolbar_${item}`)}
                </MenuItem>
              );
            })}
          </SubMenu>,
        );
      } else if (type === 'button') {
        // button 也作为一个菜单选项渲染
        const { icon, title, testId, active = false, onClick } = item;
        menuItemList.push(
          MenuSelectRectRender(
            <MenuItem
              icon={icon}
              key={`button-${index}`}
              menuKey={`button-${index}`}
              testid={testId}
              onClick={(_, e) => {
                onClick && onClick(e);
              }}
            >
              {title}
            </MenuItem>,
            active,
            index,
          ),
        );
      } else {
        // 自定义菜单
        const { title, icon, overlay, testId, active = false } = item;
        menuItemList.push(
          MenuSelectRectRender(
            <SubMenu
              key={`subment-${index}`}
              overlayStyle={{ padding: 0 }}
              icon={icon}
              title={title}
            >
              <MenuItem
                hoverable={false}
                testid={testId}
                style={{ ...clearStyle }}
                menuKey={`button-${index}`}
                key={`button-${index}`}
              >
                {overlay}
              </MenuItem>
            </SubMenu>,
            active,
            index,
          ),
        );
      }
    });
    toolbarList.push(
      <IconMenu
        getContainer={getContainer}
        key='icon-menu'
        icon={<ToolbarMoreNormal />}
        showArrow={false}
        trigger='click'
        onVisibleChange={onVisibleChange}
        overlay={
          <Menu
            className='toolbar-menu'
            selectedKeys={[styleSelectValue + '']}
            autoClose={true}
          >
            {menuItemList}
          </Menu>
        }
      />,
    );
  }

  useEffect(() => {
    // hover 控制 toolbar 显示
    if (toolbarDefault === undefined || toolbarDefault === null) {
      if (isToolbarVisible === true) {
        setDisplay('block');
        setTimeout(() => {
          setClassName('fade-in');
        }, 20);
      } else if (isToolbarVisible === false) {
        setClassName('fade-out');
        setTimeout(() => {
          setDisplay('none');
          setClassName('');
        }, 150);
      }
    }
  }, [isToolbarVisible]);

  return useMemo(
    () => (
      <InlineToolbarBlock
        toolbarDefault={toolbarDefault}
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
        }}
        onMouseDown={(e) => e.stopPropagation()}
        className={className}
        style={display ? { display } : {}}
      >
        <InlineToolbarContainer buttonStyle={buttonStyle}>
          {toolbarList}
        </InlineToolbarContainer>
      </InlineToolbarBlock>
    ),
    [
      isFolded,
      toolbar,
      locale,
      display,
      className,
      styleSelectValue,
      buttonStyle,
    ],
  );
};

// 统一修改 Menu 选择样式
const MenuSelectRectRender = (
  children: React.ReactElement,
  active: boolean,
  index: number = 0,
) => (
  <div
    key={`menuSelectRectRender-${index}`}
    style={{
      width: '100%',
      height: 34,
      position: 'relative',
      boxSizing: 'border-box',
    }}
  >
    <div
      style={{
        position: 'absolute',
        width: '100%',
        height: '100%',
        padding: '0 4px',
        boxSizing: 'border-box',
      }}
    >
      <div
        style={{
          width: '100%',
          height: '100%',
          borderRadius: 4,
          backgroundColor: active ? 'rgba(17,31,44,0.08)' : 'unset',
        }}
      />
    </div>
    {children}
  </div>
);
