import styled from 'styled-components';
import { withTheme } from '@ali/we-design';
import { Ellipsis } from '../../../utils/styled.mixin';
import { IconButtonProps } from '../../../common/interface';
import { TOOL_SIZE } from '../../../utils';

export const TriggerWrap = withTheme(styled.div<IconButtonProps>`
  ${Ellipsis}
  margin-right: 4px;
  font-size: ${({ theme, size }) =>
    (size === TOOL_SIZE.LARGE ? theme.ui.fontSizeSmall : theme.ui.fontSizeMenu)};
  color: ${({ theme }) => theme.ui.commonActionIconColor};
  line-height: 20px;
`);

export const OptionWrap = withTheme(styled.div<any>`
  color: ${({ theme, disabled }) => (disabled ? theme.ui.disabledColor : theme.ui.textColor)};
`);
