import React, { useMemo, useCallback } from 'react';
import {
  SelectOption,
  TOptionTitle,
  ToolbarAutoWrapNormal,
  ToolbarOverflowNormal,
  ToolbarLineClipNormal,
} from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import { IconSelectPropsOmit } from '../../../common/interface';
import useControlledValue from '../../../hooks/useControlledValue';
import { IconSelectDefaultProps } from '../../../utils/defaultProps';
import IconSelect from '../../../common/iconSelect';
import { OptionWrap, TriggerWrap } from './styled';

export type TableWrap = 'overflow' | 'auto-wrap' | 'line-clip';

export interface ITableWrapSelectProps extends IconSelectPropsOmit<TableWrap> {
  /**
   * 默认的换行方式，当设置了value，该项忽略
   */
  defaultValue?: TableWrap;
  /**
   * 换行方式
   */
  value?: TableWrap;
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * select 样式
   */
  style?: React.CSSProperties;
  /**
   * 对齐方式修改时的回调函数
   */
  onChange?: (align: TableWrap) => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 是否现实选中值
   */
  showTitle?: boolean;
  /**
   *  是否禁用
   */
  disabled?: boolean;
}

const TableWrapSelect: React.FC<ITableWrapSelectProps> = (props) => {
  const {
    locale,
    style,
    onChange,
    title,
    titles,
    showTitle,
    disabled,
    ...restProps
  } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  // 垂直对齐的选项
  const options = useMemo(
    () => [
      {
        title: t('we_toolbar_overflow'),
        value: 'overflow',
        Icon: ToolbarOverflowNormal,
      },
      {
        title: t('we_toolbar_auto_wrap'),
        value: 'auto-wrap',
        Icon: ToolbarAutoWrapNormal,
      },
      {
        title: t('we_toolbar_line_clip'),
        value: 'line-clip',
        Icon: ToolbarLineClipNormal,
      },
    ],
    [t],
  );

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback(
    (align?: string) => {
      const index = options.findIndex((option) => option.value === align);
      const currentOption = options[index];
      return (
        <TriggerWrap
          className="we-ui-table-wrap-select-trigger"
          size={props.size}
          style={{ minWidth: 16 }}
        >
          {currentOption && (
            <>
              <currentOption.Icon />
              {showTitle && (
                <span className="title">{currentOption.title}</span>
              )}
            </>
          )}
        </TriggerWrap>
      );
    },
    [options, showTitle],
  );

  const handleChange = useCallback(
    (e: TableWrap) => {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
    },
    [setValue, onChange],
  );

  return (
    <IconSelect
      value={value}
      onChange={handleChange}
      style={style}
      renderTrigger={renderTrigger}
      title={title}
      disabled={disabled}
      testid="TableWrapSelect"
      {...restProps}
    >
      {options.map((option) => (
        <SelectOption
          value={option.value}
          key={option.value}
          title={titles![option.value]}
          testid={'item_' + option.value}
        >
          <OptionWrap>
            <option.Icon className="icon" />
            <span>{option.title}</span>
          </OptionWrap>
        </SelectOption>
      ))}
    </IconSelect>
  );
};

TableWrapSelect.defaultProps = {
  defaultValue: 'overflow',
  titles: {},
  ...IconSelectDefaultProps,
};

export default TableWrapSelect;
