import * as React from 'react';
import { ToolbarFormulasNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';
import { useTranslate } from '@ali/we-util';

export type FormulaButtonProps = IconMenuPropsOmit;

export const FormulaButton: React.FC<FormulaButtonProps> = React.memo((props) => {
  const [visible, setVisible] = React.useState(false);

  const handleVisibleChange = (value: boolean) => {
    setVisible(value);
  };
  const t = useTranslate(props.locale);
  return (
    <IconMenu
      visible={visible}
      icon={<ToolbarFormulasNormal />}
      title={t('we_toolbar_formulas')}
      tooltip={t('we_toolbar_formulas_tooltip')}
      onVisibleChange={handleVisibleChange}
      testid="FormulaButton"
      {...props}
    />
  );
});

FormulaButton.defaultProps = {};
