import styled, { css } from 'styled-components';
import { withTheme } from '@ali/we-design';
import { TextSelectProps } from '../interface';
import { TOOL_SIZE } from '../../utils/consts';

export const TextWarp: any = withTheme(styled.div<TextSelectProps<string>>`
  margin-left: ${({ theme }) => theme.ui.marginMini};
  cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'default')};
  ${({ theme, active, disabled }) =>
    !disabled &&
    css`
      :hover ~ .select-toolbar {
        background-color: ${active
          ? theme.ui.toolbarButtonClick
          : theme.ui.toolbarButtonHover};
      }
      :active ~ .select-toolbar {
        background-color: ${disabled
          ? active
            ? theme.ui.toolbarButtonSelect
            : theme.ui.toolbarButtonTransparent
          : theme.ui.toolbarButtonClick};
      }
    `}
`);

export const ButtonWrap = withTheme(styled.div<TextSelectProps<string>>`
  display: flex;
  flex-direction: ${({ size }) =>
    size === TOOL_SIZE.LARGE ? 'column-reverse' : 'row'};
  .select-toolbar {
    height: auto;
    padding: 0;
    display: flex;
    margin: auto;
    background-color: ${({ bordered, theme }) =>
      bordered ? theme.ui.colorGray1 : theme.ui.toolbarButtonTransparent};
    cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'default')};
    ${({ active, theme, disabled }) =>
      active &&
      `background-color: ${
        disabled
          ? theme.ui.toolbarButtonSelectDisable
          : theme.ui.toolbarButtonSelect
      };`}

    &.focused {
      background-color: #fff;
      &:hover {
        background-color: #fff;
      }
    }
  }
  ${({ disabled, theme, active }) =>
    disabled &&
    css`
      .select-toolbar.disabled {
        background-color: ${active
          ? theme.ui.toolbarButtonSelectDisable
          : 'transparent'};
      }
    `}
  .select_trigger_wrap {
    width: 100%;
  }
  .select_left_wrap {
    padding: ${({ theme, size }) =>
      `${
        size === TOOL_SIZE.SMALL
          ? `${theme.ui.paddingMini} ${theme.ui.paddingSmall}`
          : theme.ui.paddingTiny
      }`};
    padding-right: 0;
    font-size: ${({ theme, size }) =>
      size === TOOL_SIZE.LARGE
        ? theme.ui.fontSizeSmall
        : theme.ui.fontSizeMenu};
    color: ${({ theme }) => theme.ui.commonActionIconColor};
    > div {
      font-size: ${({ theme, size }) =>
        size === TOOL_SIZE.LARGE
          ? theme.ui.fontSizeSmall
          : theme.ui.fontSizeMenu};
      margin-right: 0;
    }
  }
  .select_left_wrap label {
    padding: 0;
    height: 20px;
    min-width: ${({ mode }) =>
      mode === 'search' || mode === 'input' ? '35px' : '0'};
  }
  .select_left_wrap label input {
    height: 20px;
  }
  .select_right_wrap {
    width: 16px;
    display: flex;
    justify-content: center;
  }
  .select_right_wrap .weicon {
    font-size: 8px;
    margin: 0;
    transform: rotate(0deg) scale(1);
  }
  .select_right_wrap .weicon.opened {
    font-size: 8px;
    margin: 0;
    transform: rotate(180deg) scale(1);
  }

  .select-toolbar:hover {
    ${({ active, theme, disabled }) =>
      !disabled &&
      `background-color:  ${
        active ? theme.ui.toolbarButtonSelectHover : theme.ui.toolbarButtonHover
      }; `}
  }

  .select-toolbar:active {
    ${({ theme, disabled }) =>
      !disabled && `background-color :${theme.ui.toolbarButtonClick}`}
  }
`);
