import React, { useEffect, useCallback, useRef, useState } from 'react';
import fastdom from 'fastdom';
import { OffScreenWrapperProps, MODE_TYPE, BlockType } from '../interface';
import { OffScreenWrapper as OffScreenWrapperEl } from './styled';
import { ToolbarContext } from '../contexts';

export const OffScreenWrapper = React.memo((props: OffScreenWrapperProps) => {
  const {
    mode,
    doubleLineToolbar,
    singleLineToolbar,
    onBlockChange,
    needGetPositions,
    id,
  } = props;
  const [needRender, setNeedRender] = useState<boolean>(false);
  const [laterNeedRender, setLaterNeedRender] = useState<boolean>(false);
  const outElRef = useRef<Element>();
  const createTimer = useRef<number>(0);
  const destroyTimer = useRef<number>(0);
  const registersLasterNum = useRef<number>(0);
  const destroy = 2000;

  useEffect(() => {
    const timer = 0;
    if (destroyTimer.current) {
      window.clearTimeout(destroyTimer.current);
      destroyTimer.current = window.setTimeout(() => {
        setLaterNeedRender(false);
        setNeedRender(false);
        destroyTimer.current = 0;
      }, destroy);
    } else {
      setLaterNeedRender(false);
      setNeedRender(false);
      setNeedRender(true);
    }
    return () => {
      timer && window.clearTimeout(timer);
    };
  }, [needGetPositions]);

  useEffect(() => {
    if (outElRef.current && needRender) {
      registerEl((blocks) => {
        onBlockChange(blocks);
        createTimer.current = window.setTimeout(() => {
          setLaterNeedRender(true);
        }, 200);
      });
    }
    return () => {
      createTimer.current && window.clearTimeout(createTimer.current);
    };
  }, [needRender]);

  useEffect(() => {
    if (laterNeedRender && outElRef.current) {
      registerEl((blocks) => {
        onBlockChange(blocks);
        destroyTimer.current = window.setTimeout(() => {
          setLaterNeedRender(false);
          setNeedRender(false);
          destroyTimer.current = 0;
        }, destroy);
      });
    }
  }, [laterNeedRender]);

  useEffect(() => {
    registerEl((registers) => {
      registersLasterNum.current !== registers.length;
      registers.filter((o) => o.mode === 'singleLine').length > 0 &&
        registers.filter((o) => o.mode === 'doubleLine').length > 0 &&
        destroyTimer.current &&
        onBlockChange(registers);
      registersLasterNum.current = registers.length;
    });
  });

  const registerEl = useCallback(
    (cb: (blocks: BlockType[]) => void = () => {}) => {
      fastdom.measure(() => {
        const outElLeft =
          outElRef.current?.getBoundingClientRect().left || -10000;
        const blocks: BlockType[] = [];
        const groupEls: NodeListOf<HTMLElement> = document.querySelectorAll(
          `#${id}_offScreen *[data-group-key]`,
        );
        groupEls.forEach((el: HTMLElement) => {
          if (el.clientWidth !== 0) {
            const id = el.getAttribute('data-group-key') || '';
            const index = blocks.findIndex((o) => o.id === id);
            const { left, width } = el.getBoundingClientRect();
            const val: BlockType = {
              id,
              offset: left + width - outElLeft,
              elType: 'group',
              width,
              mode: el.getAttribute('data-mode'),
              hasFontSizeMaxSelect:
                el.querySelectorAll('.font-size-max-select-layout').length !==
                0,
            };
            if (index === -1) {
              blocks.push(val);
            } else {
              blocks.splice(index, 1, val);
            }
          }
        });
        const bizEls: NodeListOf<HTMLElement> = document.querySelectorAll(
          `#${id}_offScreen *[data-biz-key]`,
        );
        bizEls.forEach((el: HTMLElement) => {
          if (el.clientWidth !== 0) {
            const id = el.getAttribute('data-biz-key') || '';
            const index = blocks.findIndex((o) => o.id === id);
            const { left, width } = el.getBoundingClientRect();
            const val: BlockType = {
              id,
              offset: left + width - outElLeft,
              elType: 'biz',
              groupId: el.getAttribute('data-group-id') || '',
              width,
              mode: el.getAttribute('data-mode'),
            };
            if (index === -1) {
              blocks.push(val);
            } else {
              blocks.splice(index, 1, val);
            }
          }
        });
        cb(blocks);
      });
    },
    [],
  );

  return (
    <OffScreenWrapperEl ref={outElRef} id={`${id}_offScreen`}>
      {needRender && (
        <ToolbarContext.Provider
          value={{
            mode,
            hideBlocks: [],
            isInOffScreenWrap: true,
          }}
        >
          {mode === MODE_TYPE.doubleLine
            ? doubleLineToolbar
            : singleLineToolbar}
        </ToolbarContext.Provider>
      )}

      {laterNeedRender && (
        <ToolbarContext.Provider
          value={{
            mode: mode === MODE_TYPE.doubleLine ? 'singleLine' : 'doubleLine',
            hideBlocks: [],
            isInOffScreenWrap: true,
          }}
        >
          {mode !== MODE_TYPE.doubleLine
            ? doubleLineToolbar
            : singleLineToolbar}
        </ToolbarContext.Provider>
      )}
    </OffScreenWrapperEl>
  );
});
