import React, { useMemo, useCallback } from 'react';
import {
  SelectOption,
  TOptionTitle,
  ToolbarVerticalAlignBottomNormal,
  ToolbarVerticalAlignMiddleNormal,
  ToolbarVerticalAlignTopNormal,
} from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import { OptionWrap, TriggerWrap } from './styled';
import { IconSelectPropsOmit } from '../../../common/interface';
import useControlledValue from '../../../hooks/useControlledValue';
import { IconSelectDefaultProps } from '../../../utils/defaultProps';
import IconSelect from '../../../common/iconSelect';

export type VerticalAlign = 'middle' | 'top' | 'bottom';

export interface IVerticalAlignSelectProps
  extends IconSelectPropsOmit<VerticalAlign> {
  /**
   * 默认的对齐方式，当设置了value，该项忽略
   */
  defaultValue?: VerticalAlign;
  /**
   * 对齐方式
   */
  value?: VerticalAlign;
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * 对齐方式修改时的回调函数
   */
  onChange?: (align: VerticalAlign) => void;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 禁用的选项列表
   */
  disabledKeys?: VerticalAlign[];
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
}

interface ISelectOption {
  title: string;
  value: VerticalAlign;
  Icon: React.ComponentType<any>;
}

const VerticalAlignSelect: React.FC<IVerticalAlignSelectProps> = (props) => {
  const { locale, onChange, disabledKeys, title, titles, ...restProps } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  // 垂直对齐的选项
  const options: ISelectOption[] = useMemo(
    () => [
      {
        title: t('we_toolbar_vertical_align_top'),
        value: 'top',
        Icon: ToolbarVerticalAlignTopNormal,
      },
      {
        title: t('we_toolbar_vertical_align_middle'),
        value: 'middle',
        Icon: ToolbarVerticalAlignMiddleNormal,
      },
      {
        title: t('we_toolbar_vertical_align_bottom'),
        value: 'bottom',
        Icon: ToolbarVerticalAlignBottomNormal,
      },
    ],
    [t],
  );

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback(
    (align?: string) => {
      const index = options.findIndex((option) => option.value === align);
      const currentOption = options[index];
      return (
        <TriggerWrap style={{ minWidth: 16 }}>
          {currentOption && <currentOption.Icon />}
        </TriggerWrap>
      );
    },
    [options],
  );

  const handleChange = useCallback(
    (e: VerticalAlign) => {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
    },
    [setValue, onChange],
  );

  return (
    <IconSelect
      value={value}
      onChange={handleChange}
      renderTrigger={renderTrigger}
      title={title}
      testid="VerticalAlignSelect"
      {...restProps}
    >
      {options.map((option) => (
        <SelectOption
          value={option.value}
          key={option.value}
          disabled={disabledKeys?.includes(option.value)}
          title={titles![option.value]}
        >
          <OptionWrap>
            <option.Icon className="icon" />
            <span>{option.title}</span>
          </OptionWrap>
        </SelectOption>
      ))}
    </IconSelect>
  );
};

VerticalAlignSelect.defaultProps = {
  defaultValue: 'top',
  titles: {},
  ...IconSelectDefaultProps,
};

export default VerticalAlignSelect;
