import React, { useCallback, useMemo } from 'react';
import {
  SelectOption,
  SelectOptionGroup,
  TOptionTitle,
  ToolbarLineHeightNormal,
} from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import { OptionWrap, TriggerWrap } from './styled';
import { IconSelectPropsOmit, Omit } from '../../../common/interface';
import useControlledValue from '../../../hooks/useControlledValue';
import { IconSelectDefaultProps } from '../../../utils/defaultProps';
import IconSelect from '../../../common/iconSelect';
import OnIdle, { OnIdlePlaceholder } from '../../../common/onIdle';

export const LINE_HEIGHT_DEFAULT_VALUES = [1, 1.15, 1.5, 2, 2.5, 3];

export interface ILineHeightSelectProps extends IconSelectPropsOmit<number> {
  /**
   * 默认的行高，当设置了value，该项忽略
   */
  defaultValue?: number;
  /**
   * 行高
   */
  value?: number;
  /**
   * 行高值列表
   */
  lineHeightValues?: number[];
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * 对齐方式修改时的回调函数
   */
  onChange?: (lineHeight: number) => void;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /*
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 选中 “自定义行高” 项的回调函数， 不传（或传入的不是函数）则无 “自定义行高” 选项
   */
  onCustomize?: () => void;
}

const LineHeightSelect: React.FC<Omit<
  ILineHeightSelectProps,
  'bordered' | 'disabledKeys' | 'errored' | 'rules' | 'visible'
>> = (props) => {
  const {
    lineHeightValues,
    disabled,
    onChange,
    onCustomize,
    title,
    titles,
    locale,
    ...restProps
  } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  const handleChange = useCallback(
    (e: number) => {
      if (e !== -1) {
        if (onChange) {
          onChange(e);
        }
        setValue(e);
      } else if (onCustomize) {
        onCustomize();
      }
    },
    [onChange, setValue, onCustomize],
  );

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback(
    () => (
      <TriggerWrap>
        <ToolbarLineHeightNormal />
      </TriggerWrap>
    ),
    [],
  );

  const renderOptions = useMemo(
    () =>
      (lineHeightValues || LINE_HEIGHT_DEFAULT_VALUES).map((lineHeight) => (
        <SelectOption
          value={lineHeight}
          key={`${lineHeight}`}
          testid={`${lineHeight}`}
          title={titles![lineHeight]}
        >
          <OptionWrap>
            <span>{lineHeight}</span>
          </OptionWrap>
        </SelectOption>
      )),
    [lineHeightValues, titles],
  );

  return (
    <IconSelect
      value={value}
      onChange={handleChange}
      renderTrigger={renderTrigger}
      disabled={disabled}
      testid="LineHeightSelect"
      title={title}
      {...restProps}
    >
      <></>
      <OnIdle placeholder={<OnIdlePlaceholder />}>
        <SelectOptionGroup>{renderOptions}</SelectOptionGroup>
        {typeof onCustomize === 'function' &&
          ((
            <SelectOption value={-1} key="selectLineHeight">
              <OptionWrap className="pseudo-option">
                <span>
                  {t('we_toolbar_line_height_select_customized_line_height')}
                </span>
              </OptionWrap>
            </SelectOption>
          ) as any)}
      </OnIdle>
    </IconSelect>
  );
};

LineHeightSelect.defaultProps = {
  defaultValue: 1,
  titles: {},
  ...IconSelectDefaultProps,
};

export default LineHeightSelect;
