import React, {
  useMemo,
  useRef,
  useState,
  useEffect,
  useCallback,
} from 'react';
import {
  ToolbarArrowDownNormal as ArrowDownNormal,
  ToolbarMoreNormal,
  Dropdown,
} from '@ali/we-design';
import { ToolbarContext, OverLayContext } from '../contexts';
import {
  ToolbarController,
  ToolbarLayoutProps,
  MODE_TYPE,
  BlockType,
} from '../interface';
import {
  ToolbarWrapper,
  ToolbarLeftWrapper,
  ToolbarRightWrapper,
  MoreOverlay,
} from './styled';
import { OffScreenWrapper } from './OffScreenWrapper';
import ResizeObserver from 'resize-observer-polyfill';
import { debounce, uuid } from '../../../utils';
import IconButton from '../../../common/iconButton';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { useControlledValue } from '../../../hooks';
import { useGetContextLocale, useTranslate } from '@ali/we-util';

export { GroupWrapper as ToolbarGroupWrapper } from './GroupWrapper';
export { RowWrapper as ToolbarRowWrapper } from './RowWrapper';
export { ColumnWrapper as ToolbarColumnWrapper } from './ColumnWrapper';

export const ToolbarLayout: React.FC<ToolbarLayoutProps> = React.memo(
  (props) => {
    const {
      onModeChange,
      defaultMode,
      doubleLineToolbar,
      singleLineToolbar,
      locale,
      safeDistance = 100,
      hideSwitchBtn,
      backgroundColor,
    } = props;
    const elRef = useRef<HTMLElement>();
    const leftElRef = useRef<Element>();
    const lockModeRef = useRef<boolean>(false);
    const leftElResizeLock = useRef<boolean>(false);
    const [containment, setContainment] = useState<HTMLElement>();
    const [blocks, setBlocks] = useState<BlockType[]>([]);
    const [hideBlocks, setHideBlocks] = useState<string[]>([]);
    const [overlayHideBlocks, setOverlayShowBlocks] = useState<string[]>([]);
    const timer = useRef<number>(0);
    const [mode, setMode] = useControlledValue(props.mode, defaultMode);
    const elRefWidth = useRef<number>(0);
    const [elWidth, setElwidth] = useState<number>(0);
    const [leftWrapWidth, setLeftWrapWidth] = useState<number>(0);
    const [moreVisible, setMoreVisible] = useState<boolean>(false);
    const [moreOverlayCss, setMoreOverlayCss] = useState<string>();
    const [needReSize, setNeedReSize] = useState<number>();
    const [needGetPositions, setNeedGetPositions] = useState<number>(0);
    const [
      fontSizeMaxSelectSafeWidth,
      setFontSizeMaxSelectSafeWidth,
    ] = useState<number>(0);

    const contextLocale = useGetContextLocale();
    const t = useTranslate(locale || contextLocale);
    const id = useMemo(() => uuid(), []);
    const rightWrapWidth = 50;

    /**
     * 文字标题组件动态渲染逻辑
     */
    const resizeFontSizeMaxSelect = useCallback(
      (width: number) => {
        const fontSizeMaxSelect = blocks.find((o) => o.hasFontSizeMaxSelect);
        if (fontSizeMaxSelect && mode === fontSizeMaxSelect.mode) {
          const _blocks = blocks.filter(
            (o) => o.mode === fontSizeMaxSelect.mode,
          );
          const lastBlock = _blocks[_blocks.length - 1];
          if (
            lastBlock.offset + lastBlock.width <
            width - rightWrapWidth - safeDistance
          ) {
            setFontSizeMaxSelectSafeWidth(
              width -
                rightWrapWidth -
                lastBlock.offset -
                lastBlock.width +
                fontSizeMaxSelect.width,
            );
          } else {
            setFontSizeMaxSelectSafeWidth(fontSizeMaxSelect.width);
          }
        }
      },
      [blocks, safeDistance, mode],
    );

    // useEffect(() => console.log(blocks), [blocks]);

    /**
     * 可视区域组件变化导致重新计算
     */
    useEffect(() => {
      setNeedGetPositions(new Date().getTime());
    }, [needReSize]);

    const layoutResize = useCallback(
      (width: number) => {
        if (elRef.current) {
          const hideEl: BlockType[] = [];
          const showEl: BlockType[] = [];
          blocks.forEach((o) => {
            o.offset + safeDistance + rightWrapWidth < width
              ? showEl.push(o)
              : hideEl.push(o);
          });
          setHideBlocks(
            hideEl
              .map((o) => o.id)
              .concat(
                Array.from(new Set(hideEl.map((o) => o.groupId || ''))).filter(
                  (key) =>
                    key && showEl.findIndex((o) => o.groupId === key) === -1,
                ),
              ),
          );

          setOverlayShowBlocks(
            showEl
              .map((o) => o.id)
              .concat(
                Array.from(new Set(showEl.map((o) => o.groupId || ''))).filter(
                  (key) =>
                    key && hideEl.findIndex((o) => o.groupId === key) === -1,
                ),
              ),
          );
          resizeFontSizeMaxSelect(width);
        }
      },
      [elRef, blocks, safeDistance, mode],
    );

    const controller = useMemo<ToolbarController>(
      () => ({
        mode,
        containment,
        hideBlocks,
        width: elWidth,
        layoutResize,
        leftWrapWidth,
        fontSizeMaxSelectSafeWidth,
      }),
      [
        mode,
        containment,
        hideBlocks,
        elWidth,
        layoutResize,
        fontSizeMaxSelectSafeWidth,
      ],
    );

    /**
     * 切换单双行模式
     */
    const modeChange = useCallback(() => {
      // 1s内 不允许重复点击
      if (!lockModeRef.current) {
        const _mode =
          mode === MODE_TYPE.singleLine
            ? MODE_TYPE.doubleLine
            : MODE_TYPE.singleLine;
        setMode(_mode);
        onModeChange && onModeChange(_mode);
        lockModeRef.current = true;
        setTimeout(() => {
          lockModeRef.current = false;
        }, 600);
      }
    }, [mode, onModeChange, layoutResize, elRefWidth]);

    /**
     * 初始化绑定监听容器变化事件
     */
    useEffect(() => {
      if (!elRef.current) return;
      setContainment(elRef.current);
      const handleResize = debounce((w, lockMode) => {
        if (elRef.current && !lockMode) {
          setElwidth(w);
          layoutResize(w);
          leftElRef.current && setLeftWrapWidth(leftElRef.current.clientWidth);
        }
      }, 50);

      const resizeObserver = new ResizeObserver(
        (entries: ResizeObserverEntry[]) => {
          const entrie = entries.find(
            (o) => o.target.getAttribute('data-id') === `ToolbarWrapper_${id}`,
          );
          // Toolbar宽度变化时触发
          if (entrie) {
            elRefWidth.current = entrie.contentRect.width;
            handleResize(elRefWidth.current, lockModeRef.current);
            leftElResizeLock.current = true;
            clearTimeout(timer.current);
            timer.current = window.setTimeout(() => {
              leftElResizeLock.current = false;
            }, 500);
          } else if (
            //  Toolbar宽度没有变化但是组件新增或者减少时触发
            entries.length === 1 &&
            entries[0].target.getAttribute('data-id') ===
              `ToolbarLeftWrapper_${id}` &&
            !leftElResizeLock.current
          ) {
            setNeedReSize(new Date().getTime());
          }
        },
      );

      if (resizeObserver && elRef.current && leftElRef.current) {
        resizeObserver.observe(elRef.current);
        resizeObserver.observe(leftElRef.current);
      }
      return () => {
        if (resizeObserver) {
          resizeObserver.disconnect();
          if (typeof handleResize.cancel === 'function') {
            handleResize.cancel();
          }
        }
        if (timer.current) clearTimeout(timer.current);
      };
    }, [elRef, layoutResize, resizeFontSizeMaxSelect]);

    const dropdownContextMemo = useMemo(
      () => ({
        inOverlay: true,
        overlayHideBlocks,
        ignoreRegister: true,
      }),
      [overlayHideBlocks],
    );

    const toolbarContextMemo = useMemo(
      () => ({
        inOverlay: false,
        overlayHideBlocks: [],
        ignoreRegister: true,
      }),
      [],
    );

    const renderMoreBtn = useMemo(() => {
      if (hideBlocks.filter((o) => o.startsWith(mode || '')).length == 0) {
        return null;
      }

      /**
       * 展开收起动画
       */
      const moreTransition = () => {
        setMoreOverlayCss(moreVisible ? 'scale-out' : 'scale-up');
        !moreVisible && setTimeout(() => setMoreOverlayCss(''), 150);
        setTimeout(() => setMoreVisible(!moreVisible), !moreVisible ? 0 : 500);
      };

      return (
        <Dropdown
          visible={moreVisible}
          overlayStyle={{
            backgroundColor: 'transparent',
            boxShadow: 'none',
            border: 'none',
          }}
          overlay={
            <MoreOverlay className={moreOverlayCss}>
              <ToolbarLeftWrapper>
                <OverLayContext.Provider value={dropdownContextMemo}>
                  {mode === MODE_TYPE.doubleLine
                    ? doubleLineToolbar
                    : singleLineToolbar}
                </OverLayContext.Provider>
              </ToolbarLeftWrapper>
            </MoreOverlay>
          }
          onVisibleChange={moreTransition}
          placement="bottomRight"
          overlayAutoAlign
          overlayAutoAlignDelay={200}
        >
          <div>
            <IconButton
              icon={<ToolbarMoreNormal />}
              size={mode === MODE_TYPE.singleLine ? 'medium' : 'large'}
              title={t('we_toolbar_more')}
              testid="toolbar_more"
              showTitle={mode !== MODE_TYPE.singleLine}
              className="more-btn"
            />
          </div>
        </Dropdown>
      );
    }, [
      hideBlocks,
      overlayHideBlocks,
      mode,
      doubleLineToolbar,
      singleLineToolbar,
      moreVisible,
      moreOverlayCss,
    ]);

    return (
      <>
        <OffScreenWrapper
          onBlockChange={setBlocks}
          needGetPositions={needGetPositions}
          mode={mode}
          doubleLineToolbar={doubleLineToolbar}
          singleLineToolbar={singleLineToolbar}
          id={id}
        />
        <ToolbarContext.Provider value={controller}>
          <ToolbarWrapper
            data-id={`ToolbarWrapper_${id}`}
            role={`toolbar${mode}`}
            ref={elRef}
            mode={mode}
            backgroundColor={backgroundColor}
          >
            <ToolbarLeftWrapper
              data-id={`ToolbarLeftWrapper_${id}`}
              ref={leftElRef}
            >
              <OverLayContext.Provider value={toolbarContextMemo}>
                <CSSTransitionGroup
                  transitionName={'toolbar-left'}
                  transitionEnterTimeout={500}
                  transitionLeaveTimeout={300}
                >
                  {mode === MODE_TYPE.singleLine ? (
                    <div key="single" className="single">
                      {singleLineToolbar}
                      {renderMoreBtn}
                    </div>
                  ) : (
                    <div key="double" className="double">
                      {doubleLineToolbar}
                      {renderMoreBtn}
                    </div>
                  )}
                </CSSTransitionGroup>
              </OverLayContext.Provider>
            </ToolbarLeftWrapper>
            {!hideSwitchBtn && (
              <ToolbarRightWrapper
                onClick={modeChange}
                mode={mode}
                backgroundColor={backgroundColor}
              >
                <div className="icon-wrap">
                  <ArrowDownNormal />
                </div>
              </ToolbarRightWrapper>
            )}
          </ToolbarWrapper>
        </ToolbarContext.Provider>
      </>
    );
  },
);

ToolbarLayout.defaultProps = {
  defaultMode: MODE_TYPE.singleLine,
  safeDistance: 100,
  hideSwitchBtn: false,
};
