import React, { useState } from 'react';
import { MoreMenuConfiguration } from '../cardLayout';
import { InlineToolbarProps } from './interface';
import {
  ToolbarContainer,
  SelectGalleryBox,
  StyleSelectFontBox,
  ChildrenBoxSize,
  IconBox,
  MenuFoldedContentContainer,
  MoreMenuIconContainer,
  StyleSelectIconContainer,
} from './styled';
import {
  ToolbarMoreNormal,
  SelectGallery,
  Menu,
  MenuItem,
  MenuDivider,
  SubMenu,
  // inline toolbar icons
  TemplateCloseNormal,
  RefreshNormalNormal,
  ToolbarCopyNormal,
  ToolbarDeleteNormal,
  ToolbarCutNormal,
  ToolbarDownloadNormal,
  OpenNormal,
} from '@ali/we-design';
import { useTranslate } from '@ali/we-util';
import { IconMenu } from '../iconMenu';
import {
  StyleIcon,
  TextIcon,
  PreviewIcon,
  CardIcon,
  CardSmallIcon,
  PreviewSmallIcon,
  TextSmallIcon,
} from './styleSelectIcon';
import { InlineToolbarContext } from './inlineToolbarContext';

export const InlineToolbar: React.FC<InlineToolbarProps> = (props) => {
  const { width: widthFromContext, onVisibleChange } = React.useContext(
    InlineToolbarContext,
  );
  const minWidth = 323;
  const {
    type,
    children,
    width: widthFromProps = 0,
    moreMenuConfig = { options: [] },
    styleSelectConfig = { options: [] },
    locale,
    getContainer,
  } = props;
  const width = widthFromContext || widthFromProps;
  const [styleSelectedValue, setStyleSelectedValue] = useState(
    styleSelectConfig.defaultValue || 0,
  );
  // const [isStyleSelectShow, setIsStyleSelectShow] = useState(false);
  // const [isMoreMenuShow, setIsMoreMenuShow] = useState(false);

  const t = useTranslate(locale);

  const menuGroupRender = (items: MoreMenuConfiguration[]) =>
    items.map((item, index) => {
      let {
        name = `other-${index}`,
        icon = <TemplateCloseNormal />,
        title = 'Error: Title needs',
        onClick,
      } = item;
      switch (name) {
        case 'download':
          icon = <ToolbarDownloadNormal />;
          title = t('we_toolbar_download');
          break;
        case 'open':
            icon = <OpenNormal />;
            title = t('we_toolbar_open');
            break;
        case 'refresh':
          icon = <RefreshNormalNormal />;
          title = t('we_toolbar_refresh');
          break;
        case 'delete':
          icon = <ToolbarDeleteNormal />;
          title = t('we_toolbar_delete');
          break;
        case 'copy':
          icon = <ToolbarCopyNormal />;
          title = t('we_toolbar_copy');
          break;
        case 'cut':
          icon = <ToolbarCutNormal />;
          title = t('we_toolbar_cut');
          break;
      }
      return (
        <MenuItem key={name} menuKey={name} icon={icon} onClick={onClick}>
          {title}
        </MenuItem>
      );
    });

  const getStyleSelectSubMenuIcon = (key: string) => {
    switch (key) {
      case 'text':
        return (
          <IconBox>
            <TextSmallIcon />
          </IconBox>
        );
      case 'card':
        return (
          <IconBox>
            <CardSmallIcon />
          </IconBox>
        );
      case 'preview':
        return (
          <IconBox>
            <PreviewSmallIcon />
          </IconBox>
        );
      default:
        return <IconBox>233</IconBox>;
    }
  };

  const moreMenuOverlay = () => {
    const { options } = moreMenuConfig;
    const { onChange } = styleSelectConfig;
    return (
      <ChildrenBoxSize>
        <Menu
          style={{ width: '100%' }}
          selectedKeys={[`${styleSelectedValue }`]}
        >
          {width < minWidth && (
            <>
              {children && (
                <MenuFoldedContentContainer>
                  {children}
                </MenuFoldedContentContainer>
              )}
              {children && styleSelectConfig.options.length > 0 && (
                <MenuDivider />
              )}
              {styleSelectConfig.options.length > 0 && (
                <SubMenu
                  icon={
                    <IconBox>
                      <StyleIcon />
                    </IconBox>
                  }
                  menuKey="style_select"
                  key="style_select"
                  title={t('we_toolbar_style_select')}
                  showSelect
                >
                  {styleSelectConfig.options.map((item, index) => {
                    return (
                      <MenuItem
                        icon={getStyleSelectSubMenuIcon(item)}
                        menuKey={`${index }`}
                        key={index}
                        onClick={(key) => {
                          onChange && onChange(key);
                          setStyleSelectedValue(index);
                        }}
                      >
                        {t(`we_toolbar_${item}`)}
                      </MenuItem>
                    );
                  })}
                </SubMenu>
              )}
            </>
          )}
          {(children || styleSelectConfig.options.length > 0) &&
            width < minWidth &&
            options.length > 0 && <MenuDivider />}
          {options.length > 0 &&
            options.reduce((pre: React.ReactElement[], items, index) => {
              if (index !== 0) {
                pre = pre.concat([<MenuDivider key={index} />]);
              }
              return pre.concat(menuGroupRender(items));
            }, [])}
        </Menu>
      </ChildrenBoxSize>
    );
  };

  const getNode = (item: string | React.ReactElement) => {
    let text: any = '';
    let image: React.ReactNode = <></>;
    switch (item) {
      case 'text':
        text = t('we_toolbar_text');
        image = <TextIcon />;
        break;
      case 'card':
        text = t('we_toolbar_card');
        image = <CardIcon />;
        break;
      case 'preview':
        text = t('we_toolbar_preview');
        image = <PreviewIcon />;
        break;
      default:
        text = item;
        image = <></>;
        break;
    }

    return (
      <SelectGalleryBox key={text}>
        {image}
        <StyleSelectFontBox>{text}</StyleSelectFontBox>
      </SelectGalleryBox>
    );
  };

  const moreMenuRender = () => (
    <MoreMenuIconContainer
      onClick={(e: React.MouseEvent) => e.stopPropagation()}
      onMouseDown={(e: React.MouseEvent) => e.stopPropagation()}
      // onMouseEnter={() => {clearTimeout(moreMenuClose); setIsStyleSelectShow(false); setIsMoreMenuShow(true)}}
      // onMouseLeave={() => moreMenuClose = setTimeout(() => setIsMoreMenuShow(false), 300)}
    >
      <IconMenu
        key="more-menu"
        placement="bottomLeft"
        getContainer={getContainer}
        // visible={isMoreMenuShow}
        // autoClose={false}
        icon={<ToolbarMoreNormal />}
        overlay={moreMenuOverlay()}
        showArrow={false}
        onVisibleChange={onVisibleChange}
      />
    </MoreMenuIconContainer>
  );

  const StyleSelectRender = () => {
    const { options, onChange } = styleSelectConfig;
    return options?.length > 0 ? (
      <StyleSelectIconContainer
        onClick={(e) => e.stopPropagation()}
        onMouseDown={(e) => e.stopPropagation()}
        // onMouseEnter={() => {clearTimeout(styleSelectClose); setIsMoreMenuShow(false);setIsStyleSelectShow(true)}}
        // onMouseLeave={() => styleSelectClose = setTimeout(() => setIsStyleSelectShow(false), 300)}
      >
        <IconMenu
          key="style-select"
          placement="bottomLeft"
          // autoClose={false}
          // visible={isStyleSelectShow}
          getContainer={getContainer}
          overlay={
            <SelectGallery
              groups={[
                {
                  title: t('we_toolbar_style_select'),
                  showDivider: false,
                  items: options.map(
                    (item: string | React.ReactElement, index: number) => {
                      return {
                        id: index,
                        node: getNode(item),
                        testID: `inlineToolbarSeletOptions-${index}`,
                      };
                    },
                  ),
                },
              ]}
              itemsPerRow={options.length}
              value={styleSelectedValue}
              onChange={(e) => {
                setStyleSelectedValue(e);
                onChange && onChange(`${e }`);
              }}
            />
          }
          icon={<StyleIcon />}
          overlayStyle={{
            border: 0,
            boxShadow: 'unset',
            backgroundColor: 'unset',
          }}
          onVisibleChange={onVisibleChange}
        />
      </StyleSelectIconContainer>
    ) : (
      <></>
    );
  };

  const menuCount =
    React.Children.count(children) +
    Number(styleSelectConfig.options.length > 0) +
    Number(moreMenuConfig.options.length > 0);

  const childrenList: React.ReactElement[] = [];
  children &&
    React.Children.forEach(
      children,
      (child: React.ReactElement, index: number) => {
        childrenList.push(
          React.cloneElement(child, {
            ...child.props,
            key: index,
            onVisibleChange,
          }),
        );
      },
    );
  styleSelectConfig.options.length > 0 &&
    childrenList.push(StyleSelectRender());
  moreMenuConfig.options.length > 0 && childrenList.push(moreMenuRender());

  return (
    <ToolbarContainer className="inline-toolbar-container" type={type}>
      {width < minWidth && menuCount > 1 ? moreMenuRender() : childrenList}
    </ToolbarContainer>
  );
};
