import React from 'react';
import { useTranslate } from '@ali/we-util';
import { IconButtonPropsOmit } from '../../../common/interface';
import { IconButtonDefaultProps } from '../../../utils/defaultProps';
import IconButton from '../../../common/iconButton';
import { ToolbarPictureNormal } from '@ali/we-design';

export type PictureButtonProps = IconButtonPropsOmit;

export const PictureButton: React.FC<PictureButtonProps> = React.memo((props) => {
  const t = useTranslate(props.locale);

  return (
    <IconButton
      testid="picture_button"
      icon={<ToolbarPictureNormal />}
      title={t('we_toolbar_picture')}
      tooltip={t('we_toolbar_picture_tooltip')}
      {...props}
    />
  );
});

PictureButton.defaultProps = { ...IconButtonDefaultProps };
