import React, { useState, useCallback } from 'react';
import { TOptionTitle, ToolbarFilterNormal } from '@ali/we-design';
import { SplitButtonOmit } from '../../../common/interface';
import SplitButton from '../../../common/splitButton';
import { useTranslate } from '@ali/we-util';

export interface IFilterButtonProps extends SplitButtonOmit {
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 点击左侧的回调函数
   */
  onClick?: () => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /*
   * 国际化语言
   */
  locale?: string;
  /**
   * 下拉内容
   */
  overlay: React.ReactElement;
}

export const FilterButton: React.FC<IFilterButtonProps> = (props) => {
  const {
    disabled,
    onClick,
    title,
    titles,
    locale,
    size,
    active: activeLeft,
    overlay,
    ...restProps
  } = props;
  const [active, setActive] = useState(false);
  const t = useTranslate(locale);
  const handleClickLeft = useCallback(() => {
    if (onClick) onClick();
  }, [onClick]);
  const hideOverlay = useCallback(() => setActive(false), []);
  return (
    <SplitButton
      onClickLeft={handleClickLeft}
      onActiveChange={setActive}
      disabled={disabled}
      testid="FilterButton"
      title={title || t('we_toolbar_filter_button')}
      size={size}
      icon={<ToolbarFilterNormal />}
      overlay={<div onClick={hideOverlay}>{overlay}</div>}
      {...restProps}
      activeLeft={activeLeft}
      active={active}
    />
  );
};

FilterButton.defaultProps = {
  titles: {},
};
