import { useTranslate } from '@ali/we-util';
import * as React from 'react';
import { ToolbarFormatNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';

export type ConditionalFormattingButtonProps = IconMenuPropsOmit;

export const ConditionalFormattingButton: React.FC<ConditionalFormattingButtonProps> = React.memo(
  (props) => {
    const [visible, setVisible] = React.useState(false);

    const handleVisibleChange = (value: boolean) => {
      setVisible(value);
    };
    const t = useTranslate(props.locale);

    return (
      <IconMenu
        visible={visible}
        icon={<ToolbarFormatNormal />}
        title={t('we_toolbar_format')}
        tooltip={t('we_toolbar_format')}
        onVisibleChange={handleVisibleChange}
        testid="ConditionalFormattingButton"
        {...props}
      />
    );
  },
);

ConditionalFormattingButton.defaultProps = {};
