import React, { useContext } from 'react';
import { RowWrapper as RowWrapperEl } from './styled';
import { RowWrapperProps } from '../interface';
import { ToolbarContext } from '../contexts';

export const RowWrapper: React.FC<RowWrapperProps> = React.memo((props) => {
  const { mode } = useContext(ToolbarContext);
  return (
    <RowWrapperEl className="row-wrapper" mode={mode}>
      {props.children}
    </RowWrapperEl>
  );
});
