import React, { useCallback, useMemo } from 'react';
import { SelectOption } from '@ali/we-design';
import { OptionWrap, OptionTitle, OptionExample } from './styled';
import TextSelect from '../../../common/textSelect';
import { TextSelectPropsOmit } from '../../../common/interface';
import { useTranslate } from '@ali/we-util';

export interface IParseResult {
  formatCode: string;
  value: string | number;
}
export interface INumberFormatter {
  parse: (value: string | number) => IParseResult;
  format: (value: string | number, formatCode: string) => string;
}
interface IProps extends TextSelectPropsOmit<string> {
  format?: string;
  targetValue?: string | number;
  onChange: (e: string) => void;
  numberFormatter: INumberFormatter;
  bordered?: boolean;
  className?: string;
  disabled?: boolean;
}

export const NumberFormatSelect: React.FC<IProps> = React.memo((props) => {
  const {
    targetValue,
    format,
    onChange,
    bordered,
    numberFormatter,
    locale,
    ...restProps
  } = props;

  const t = useTranslate(locale);

  const NUMBER_FORMATS = useMemo(
    () => [
      { values: ['General'], title: t('we_toolbar_routine') },
      { values: ['0'], title: t('we_toolbar_number'), example: '1234' },
      {
        values: ['0.00'],
        title: `${t('we_toolbar_number')}（${t('we_toolbar_decimal_point')}）`,
        example: '1234.56',
      },
      { values: ['0%'], title: t('we_toolbar_percentage'), example: '12%' },
      {
        values: ['0.00%'],
        title: `${t('we_toolbar_percentage')}（${t(
          'we_toolbar_decimal_point',
        )}）`,
        example: '12.34%',
      },
      {
        values: ['0.00E+00'],
        title: t('we_toolbar_scientific_counting'),
        example: '1.01E+03',
      },
      { values: ['¥0'], title: t('we_toolbar_rmb'), example: '¥1,234' },
      {
        values: ['¥0.00'],
        title: `${t('we_toolbar_rmb')}（${t('we_toolbar_decimal_point')}）`,
        example: '¥1,234.56',
      },
      { values: ['$0'], title: t('we_toolbar_dollar'), example: '$1,234' },
      {
        values: ['$0.00'],
        title: `${t('we_toolbar_dollar')}（${t('we_toolbar_decimal_point')}）`,
        example: '$1,234.56',
      },
      {
        values: ['yyyy/m/d', 'm\u6708d\u65e5', 'm/d/yyyy'],
        title: t('we_toolbar_date'),
        example: '2020/05/20',
      },
      {
        values: ['hh:mm:ss', 'h:mm', 'h:mm:ss', 'h:mm:ss.0'],
        title: t('we_toolbar_time'),
        example: '13:14:00',
      },
      {
        values: [
          'yyyy/m/d hh:mm:ss',
          'yyyy/m/d h:mm:ss.0',
          'yyyy/m/d h:mm',
          'yyyy/m/d h:mm:ss',
        ],
        title: t('we_toolbar_date_time'),
        example: '2020/05/20 13:14:00',
      },
      { values: ['@'], title: t('we_toolbar_text') },
    ],
    [t],
  );

  const finalValue = format || 'General';

  const renderTrigger = useCallback((v: string | undefined) => {
    const found = NUMBER_FORMATS.find((f) => f.values.includes(v || 'General'));
    if (found) {
      return <span>{found.title}</span>;
    }
    return <span>{NUMBER_FORMATS[0].title}</span>;
  }, []);

  const isNil = (v: any): v is null | undefined | void =>
    v === undefined || v === null;
  const isNumber = (v: any): v is number => typeof v === 'number';

  return (
    <TextSelect
      value={finalValue}
      onChange={onChange}
      renderTrigger={renderTrigger}
      bordered={bordered}
      testid="NumberFormatSelect"
      {...restProps}
    >
      {NUMBER_FORMATS.map(({ title, values: v, example }) => {
        let formatedValue = example ?? '';
        const key = v.includes(finalValue) ? finalValue : v[0];
        if (!isNil(targetValue) && targetValue !== '') {
          formatedValue = isNumber(targetValue)
            ? numberFormatter.format(targetValue, key)
            : targetValue;
        }
        return (
          <SelectOption
            key={key}
            value={key}
            testid={`NumberFormatSelectOption_${key}`}
          >
            <OptionWrap>
              <OptionTitle>{title}</OptionTitle>
              <OptionExample>{formatedValue}</OptionExample>
            </OptionWrap>
          </SelectOption>
        );
      })}
    </TextSelect>
  );
});
