import { useTranslate } from '@ali/we-util';
import * as React from 'react';
import { ToolbarRepeatNormal } from '@ali/we-design';
import IconMenu from '../../../common/iconMenu';
import { IconMenuPropsOmit } from '../../../common/interface';

export type HignlightRepeatMeunProps = IconMenuPropsOmit;

export const HignlightRepeatMeunButton: React.FC<HignlightRepeatMeunProps> = React.memo(
  (props) => {
    const [visible, setVisible] = React.useState(false);

    const handleVisibleChange = (value: boolean) => {
      setVisible(value);
    };
    const t = useTranslate(props.locale);

    return (
      <IconMenu
        visible={visible}
        icon={<ToolbarRepeatNormal />}
        title={t('we_toolbar_hignlight_repeat')}
        tooltip={t('we_toolbar_hignlight_repeat')}
        onVisibleChange={handleVisibleChange}
        testid="HignlightRepeatMeunButton"
        {...props}
      />
    );
  },
);

HignlightRepeatMeunButton.defaultProps = {};
